<template>
  <div>
    <el-tabs v-model="activeTab">
      <el-tab-pane name="list" label="列表">
        <sold_list @tocontrol_c="tocontrol"></sold_list>
      </el-tab-pane>
      <el-tab-pane name="control" label="管理">
        <el-tabs v-model="activeSubTab">
          <el-tab-pane name="info" label="车辆详情">
            <el-row>
              <el-col span="12" offset="6">
                <carinfo ref="carinfo_ref"></carinfo>
                <carsold_au ref="carsold_ref"></carsold_au>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="au" label="销售退回">
            <el-row>
              <el-col span="12" offset="6">
                <soldback ref="soldback_ref"></soldback>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>

        <el-divider>
          <el-link icon="el-icon-caret-left" @click="activeTab='list'">返回列表</el-link>
        </el-divider>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import sold_list from "@/components/carsold/detail_query";
import carinfo from "@/components/nowstore/carcontrol/carinfo";
import carsold_au from "@/components/carsold/carsold_detail_au";
import soldback from "@/components/carsold/soldback";

export default {
  components: { carsold_au, sold_list, carinfo, soldback },
  data() {
    return {
      activeTab: "list",
      activeSubTab: "info"
    };
  },
  methods: {
    tocontrol(data) {
      this.activeTab = "control";
      this.activeSubTab = "info";
      data.linkinfo = [];
      //车辆明细初始化
      this.$refs.carinfo_ref.info = data;
      //客户信息初始化
      this.$refs.carsold_ref.updateInfo(data.vin);
      this.$refs.soldback_ref.vin = data.vin;
    }
  }
};
</script>

