<template>
  <div>
    <el-row>
      <el-col span="3">
        <dtpicker @val_change="change_dt1"></dtpicker>
      </el-col>
      <el-col span="3">
        <dtpicker @val_change="change_dt2"></dtpicker>
      </el-col>
      <el-col span="3">
        <sel_mtype @val_change="change_stype"></sel_mtype>
      </el-col>
      <el-col span="3">
        <sel_price @val_change="change_price" :stype="info.stype" :data="info.price"></sel_price>
      </el-col>
      <el-col span="3">
        <sel_ltype
          @val_change="change_ltype"
          :stype="info.stype"
          :price="info.price"
          :data="info.ltype"
        ></sel_ltype>
      </el-col>
      <el-col span="3">
        <el-input v-model="info.vin" placeholder="车架号"></el-input>
      </el-col>
      <el-col span="2">
        <el-button @click="doquery" icon="el-icon-search" type="primary" :loading="queryloading">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details_in_page" border height="550px" style="width:100%">
      <el-table-column prop="index" width="50" label="序号"></el-table-column>
      <el-table-column width="200" label="VIN">
        <template slot-scope="scope">
          <el-link icon="el-icon-caret-right" @click="tocontrol(scope.row)">{{scope.row.vin}}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="typeinfo[0].stype" width="100" label="车型"></el-table-column>
      <el-table-column prop="typeinfo[0].price" width="100" label="价格"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置"></el-table-column>
      <el-table-column prop="clr" width="100" label="颜色"></el-table-column>
      <el-table-column prop="arrdt" width="150" label="入库日期"></el-table-column>
      <el-table-column prop="pdt" width="150" label="生产日期"></el-table-column>
      <el-table-column prop="source" width="150" label="车辆来源"></el-table-column>
    </el-table>
    <el-row>
      <div class="output">
        <el-button
          type="primary"
          plain
          @click="output"
          :loading="outputloading"
          icon="el-icon-download"
        >导出明细</el-button>
      </div>
    </el-row>

    <el-pagination
      background
      layout="prev, pager, next"
      :total="details.length"
      :hide-on-single-page="true"
      @current-change="cur_page"
    ></el-pagination>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datepicker";
import sel_mtype from "@/components/common/sel_carsold_mtype";
import sel_price from "@/components/common/sel_carsold_price";
import sel_ltype from "@/components/common/sel_carsold_ltype";
import axios from "axios";
import { no_au_then } from "@/components/utils";

export default {
  components: { dtpicker, sel_mtype, sel_price, sel_ltype },
  data() {
    return {
      report: "",
      token: sessionStorage.getItem("token"),
      queryloading: false,
      outputloading: false,
      info: {},
      details: [],
      nowpage: 1,
      infoperpage: 10 //每页最大记录数量
    };
  },
  mounted() {
    this.clearInfo();
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    createReport(details) {
      let map = new Map();
      for (const d of details) {
        map.get(d.typeinfo[0].stype) == undefined
          ? map.set(d.typeinfo[0].stype, 1)
          : map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1);
      }

      let re = [],
        sum = 0;
      map.forEach((v, k) => {
        re.push(`${k}：${v}`);
        sum = sum + v;
      });

      re.push("合计：" + sum);

      this.report = re.join("，");
    },
    tocontrol(d) {
      this.$emit("to_control", d);
    },
    output() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        axios({
          url: require("@/config").axios.output,
          method: "post",
          data: this.output_format()
        }).then(re => {
          window.open(require("@/config").domain + re.data.filename);
          this.outputloading = false;
        });
      });
    },
    output_format() {
      let fmt = {};

      fmt.fields = [
        "序号",
        "VIN",
        "车型",
        "价格",
        "配置",
        "颜色",
        "入库日期",
        "生产日期",
        "车辆来源"
      ];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.index;
        re[fmt.fields[1]] = d.vin;
        re[fmt.fields[2]] = d.typeinfo[0].stype;
        re[fmt.fields[3]] = d.typeinfo[0].price;
        re[fmt.fields[4]] = d.typeinfo[0].ltype;
        re[fmt.fields[5]] = d.clr;
        re[fmt.fields[6]] = d.arrdt;
        re[fmt.fields[7]] = d.pdt;
        re[fmt.fields[8]] = d.source;
        return re;
      });

      fmt.token = this.token;
      return fmt;
    },
    cur_page(val) {
      this.nowpage = val;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.getCarAllList,
        method: "post",
        data: { token: this.token, info: this.info }
      }).then(re => {
        no_au_then(re, re => {
          let i = 1;
          this.details = re.data.data.map(d => {
            d.index = i;
            i++;
            return d;
          });
          this.createReport(this.details);
          this.cur_page(1);
        });
        this.queryloading = false;
      });
    },
    clearInfo() {
      this.info = {
        dt1: new Date(),
        dt2: new Date(),
        stype: "",
        ltype: "",
        price: "",
        vin: ""
      };
    },
    change_stype(d) {
      this.info.stype = d;
      if (d == "") {
        this.info.price = "";
        this.info.ltype = "";
      }
    },
    change_price(d) {
      this.info.price = d;
    },
    change_ltype(d) {
      this.info.ltype = d;
    },
    change_dt1(d) {
      if (d == null) {
        this.info.dt1 = "";
      } else {
        this.info.dt1 = d;
      }
    },
    change_dt2(d) {
      if (d == null) {
        this.info.dt2 = "";
      } else {
        this.info.dt2 = d;
      }
    }
  }
};
</script>

