<template>
  <div>
    <el-button :loading="loading" type="primary" @click="get_will_call">请求下周将CALL</el-button>
  </div>
</template>

<script>
import axios from "axios";
import { no_au_then } from "@/components/utils";

export default {
  data() {
    return {
      loading: false,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    get_will_call() {
      this.loading = true;
      axios({
        url: require("@/config").axios.ppt_will_call,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        this.loading = false;
        no_au_then(re, re => {
          alert(JSON.stringify(re.data.data));
        });
      });
    }
  }
};
</script>