<template>
  <div class="frame">
    <div id="by_stype"></div>
  </div>
</template>

<script>
import G2 from "@antv/g2";
import { shortdate, days } from "@/components/utils";

export default {
  props: ["info"],
  watch: {
    info(nv, ov) {
      this.data_process(nv);
    }
  },
  data() {
    return {
      info: [],
      g2info: [],
      chart: {}
    };
  },
  mounted() {
    this.char_init();
  },
  methods: {
    char_init() {
      this.chart = new G2.Chart({
        container: "by_stype", // 指定图表容器 ID
        forceFit: true,
        height: 300 // 指定图表高度
      });

      this.chart
        .interval()
        .position("x*y")
        .color("x");
      // Step 4: 渲染图表
    },
    data_process(info) {
      let map = new Map();

      info.storelist.forEach(d => {
        const age = days(shortdate(d.carinfo[0].pdt), shortdate(new Date()));
        let key = d.typeinfo[0].stype;
        map.get(key) == undefined
          ? map.set(key, 1)
          : map.set(key, map.get(key) + 1);
      });

      let re = [];
      map.forEach((v, k) => {
        re.push({ x: k, y: v });
      });
      this.g2info = re;
      this.chart.source(this.g2info);
      this.chart.render();
    }
  }
};
</script>

<style scoped>
.frame {
  padding: 10px;
  border-bottom: 1px solid #dde;
}
</style>