<template>
  <div>
    <el-form ref="info" :model="info" label-width="120px">
      <el-form-item label="VIN">
        <el-link>{{info.vin}}</el-link>
      </el-form-item>
      <el-form-item label="车型">
        <sel_stype :data="info.typeinfo[0].stype" @val_change="change_stype"></sel_stype>
      </el-form-item>
      <el-form-item label="价格">
        <sel_price
          :data="info.typeinfo[0].price"
          :stype="info.typeinfo[0].stype"
          @val_change="change_price"
        ></sel_price>
      </el-form-item>
      <el-form-item label="配置">
        <sel_ltype
          :data="info.typeinfo[0].ltype"
          :stype="info.typeinfo[0].stype"
          :price="info.typeinfo[0].price"
          @val_change="change_ltype"
        ></sel_ltype>
      </el-form-item>
      <el-form-item label="颜色">
        <sel_clr :data="info.clr" :stype="info.typeinfo[0].stype" @val_change="change_clr"></sel_clr>
      </el-form-item>
      <el-form-item label="生产日期">
        <dtpicker :data="info.pdt" @val_change="change_pdt"></dtpicker>
      </el-form-item>
      <el-form-item label="入库日期">
        <dtpicker :data="info.arrdt" @val_change="change_arrdt"></dtpicker>
      </el-form-item>
      <el-form-item label="发动机号">
        <el-input v-model="info.fdjcode"></el-input>
      </el-form-item>
      <el-form-item label="车辆来源">
        <el-link>{{info.source}}</el-link>
      </el-form-item>
      <el-form-item label="合格证序号">
        <el-link>{{info.hgz}}</el-link>
      </el-form-item>
      <el-form-item>
        <el-col span="12" offset="12">
          <el-button @click="doupdate" type="primary" icon="el-icon-edit">更新数据</el-button>
        </el-col>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
import sel_stype from "@/components/common/sel_carsold_mtype";
import sel_price from "@/components/common/sel_carsold_price";
import sel_ltype from "@/components/common/sel_carsold_ltype";
import sel_clr from "@/components/common/sel_carsold_clr";
import dtpicker from "@/components/common/datepicker";
import { no_au_then } from "@/components/utils";

export default {
  props: ["data"],
  components: { sel_stype, sel_price, sel_ltype, sel_clr, dtpicker },
  watch: {
    data(nv, ov) {
      if (nv != "") {
        this.getInfo(nv);
      }
    }
  },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      info: {}
    };
  },
  mounted() {
    this.clearInfo();
  },
  methods: {
    input_ok() {
      let err = [];
      this.info.typeinfo[0].stype == "" ? err.push("需要车型") : "";
      this.info.typeinfo[0].price == "" ? err.push("需要价格") : "";
      this.info.typeinfo[0].ltype == "" ? err.push("需要配置") : "";
      this.info.clr == "" ? err.push("需要颜色") : "";
      return err;
    },
    doupdate() {
      if (this.input_ok().length == 0) {
        this.$confirm("确认更新？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.sincarinfo_update,
            method: "post",
            data: { token: this.token, info: this.info }
          }).then(re => {
            no_au_then(re, re => {
              this.$message("更新完成");
            });
          });
        });
      } else {
        this.$message(this.input_ok().join("，"));
      }
    },
    change_pdt(d) {
      this.info.pdt = d;
    },
    change_arrdt(d) {
      this.info.arrdt = d;
    },
    change_clr(d) {
      this.info.clr = d;
    },
    change_stype(d) {
      this.info.typeinfo[0].stype = d;
      this.info.typeinfo[0].price = "";
      this.info.typeinfo[0].ltype = "";
    },
    change_price(d) {
      this.info.typeinfo[0].price = d;
      this.info.typeinfo[0].ltype = "";
    },
    change_ltype(d) {
      this.info.typeinfo[0].ltype = d;
    },
    clearInfo() {
      this.info = {
        typeinfo: [
          {
            stype: "",
            price: "",
            ltype: ""
          }
        ],
        fdjcode: "",
        vin: "",
        pdt: new Date(),
        arrdt: new Date(),
        clr: "",
        source: "",
        hgz: ""
      };
    },
    getInfo(vin) {
      axios({
        url: require("@/config").axios.sincarinfo,
        method: "post",
        data: { token: this.token, info: { vin } }
      }).then(re => {
        this.info = re.data.data[0];
      });
    }
  }
};
</script>

<style scoped>
div {text-align: left;}
</style>