<template>
  <div>
    <el-row>
      <el-col span="4" offset=20>
        <el-button @click="getinfo" type="primary" plain>刷新</el-button>
      </el-col>
    </el-row>
    <el-divider>车辆库存统计图表</el-divider>
    <el-row>
      <el-col span="12">
        <h4>库龄</h4>
        <store_by_age :info="info"></store_by_age>
      </el-col>
      <el-col span="12">
        <h4>车型</h4>
        <store_by_stype :info="info"></store_by_stype>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

import { no_au_then } from "@/components/utils";
import store_by_age from "./store_by_age";
import store_by_stype from "./store_by_stype";

export default {
  components: { store_by_age, store_by_stype },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      info: []
    };
  },
  mounted() {
    this.getinfo();
  },
  methods: {
    getinfo() {
      axios({
        url: require("@/config").axios.g2_store,
        method: "post",
        data: {
          token: this.token
        }
      }).then(re => {
        no_au_then(re, re => {
          this.info = re.data.data;
        });
      });
    }
  }
};
</script>

<style scoped>
h4 {
  color: #666;
  font-size: 15px;
}
</style>