<template>
  <div>
    <el-select
      v-model="val"
      placeholder="位置"
      clearable
      @change="val_change"
      filterable
      allow-create
    >
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return { vals: [], val: "", token: sessionStorage.getItem("token") };
  },
  mounted() {
    axios({
      url: require("@/config").axios.outposilist,
      method: "post",
      data: { token: this.token }
    }).then(re => {
      this.vals = re.data.data.map(d => {
        return { val: d };
      });
    });
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.val);
    }
  }
};
</script>