<template>
  <div>
    <el-row>
      <el-col span="8" offset="8">
        <ul>
          <li>
            <div class="inputflex">
              <label>官方车型</label>
              <el-select v-model="type_class" placeholder="请选择官方车型" @change="type_class_change">
                <el-option
                  v-for="item in types_class"
                  :key="item.id"
                  :label="item.type"
                  :value="item"
                ></el-option>
              </el-select>
            </div>
          </li>
          <li>
            <div class="inputflex">
              <label>F_TYPE</label>
              <el-select v-model="type_in" placeholder="请选择未匹配车型" @change="type_in_change">
                <el-option
                  v-for="item in types_in"
                  :key="item.id"
                  :label="item.ltype"
                  :value="item"
                ></el-option>
              </el-select>
            </div>
          </li>
          <li>
            <el-button type="primary" @click="dofit">确认匹配</el-button>
          </li>
        </ul>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import mytitle from "@/components/common/title";

export default {
  components: { mytitle },
  data() {
    return {
      type_in: "",
      type_in_id: 0,
      type_price: 0,
      type_class: "",
      type_class_id: 0,
      types_in: [],
      types_class: [],
      err: []
    };
  },
  methods: {
    type_in_change(info) {
      this.type_in_id = info.type_id; //获取内部车型的id
      this.type_in = info.ltype; //获取内部车型文本
    },
    type_class_change(item) {
      this.type_class_id = item.id;
      this.type_price = item.price;
      this.type_class = item.type;

      axios({
        //刷新对应价格的内部车型列表
        url: require("@/config").axios.nofitintype,
        method: "post",
        data: { price: this.type_price, token: sessionStorage.getItem("token") }
      }).then(re => {
        this.types_in = re.data;
      });
    },
    input_err() {
      this.err = [];
      let re = false;
      if (this.type_in_id == 0) {
        this.err.push("未选择内部车型");
        re = true;
      }

      if (this.type_class_id == 0) {
        this.err.push("未选择官方车型");
        re = true;
      }

      return re;
    },
    dofit() {
      if (!this.input_err()) {
        this.$confirm("确认匹配车型吗？", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.dofittype,
            method: "post",
            data: {
              intypeid: this.type_in_id,
              classtypeid: this.type_class_id,
              token: sessionStorage.getItem("token")
            }
          }).then(re => {
            if (re.data.code == 200) {
              this.$message("匹配成功");
              this.type_in = "";
              this.type_in_id = 0;
              this.type_class = "";
              this.type_class_id = 0;
              this.getnofittype();
            } else {
              this.$message("匹配失败");
            }
          });
        });
      } else {
        this.$message(this.err.join("，"));
      }
    },
    getnofittype() {
      //从近两个月的销售车型中统计未匹配的官方车型
      axios({
        url: require("@/config").axios.nofittype,
        method: "post",
        data: { month: 2, token: sessionStorage.getItem("token") }
      }).then(re => {
        this.types_class = re.data;
      });
    }
  },
  mounted() {
    this.getnofittype();
  }
};
</script>

<style scoped>
ul li {
  padding: 8px 0;
}

div button {
  width: 100%;
}

.el-select {
  width: 100% !important;
}
</style>