<template>
  <div>
    <el-row>
      <el-col span="8">
        <div ref="sold_chart" id="sold_chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import G2 from "@antv/g2";

export default {
  data() {
    return {
      info: [],
      char: {}
    };
  },
  mounted() {
    this.info = new Array(10).fill(undefined).map((d, i) => {
      return { key: "aaaa" + i, val: Math.random() * 100 };
    });

//    this.createChart();
  },
  methods: {
    change_info() {
      this.info = [
        { key: "shake", val: 600 * Math.random() },
        { key: "oink451", val: 700 * Math.random() },
        { key: "family451", val: 800 * Math.random() }
      ];

      this.char.changeData(this.info);
    },
    createChart() {
      this.char = new G2.Chart({
        container: "sold_chart",
        forceFit: true,
        width: document.body.clientWidth / 3,
        height: 300
      });

      this.char.source(this.info);
      this.char.interval().position("key*val");
      this.char.render();
      // alert(this.$refs.sold_chart.offsetHeight);
      // alert(this.$refs.sold_chart.offsetWidth);
    }
  }
};
</script>

<style scoped>
#sold_chart {
  height: 300px;
}
</style>