<template>
  <div>
    <el-divider>内部调拨</el-divider>
    <el-row gutter="10">
      <el-col span="8">
        <dtpicker ref="movedt"></dtpicker>
      </el-col>
      <el-col span="4">
        <sel_posi1 @val_change="change_now_posi1" ref="posi1_ref"></sel_posi1>
      </el-col>
      <el-col span="4">
        <sel_posi2 :posi1="now_posi1" ref="posi2_ref"></sel_posi2>
      </el-col>
      <el-col span="2">
        <el-button type="primary" @click="domove">确认操作</el-button>
      </el-col>
    </el-row>
    <el-divider>外部调拨</el-divider>
    <el-row gutter="10">
      <el-col span="8">
        <dtpicker ref="moveoutdt"></dtpicker>
      </el-col>
      <el-col span="8">
        <sel_posi_out ref="posiout_ref"></sel_posi_out>
      </el-col>
      <el-col span="2">
        <el-button type="primary" @click="domoveout">确认操作</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datetimepicker";
import sel_posi1 from "@/components/common/sel_nowstore_posi1";
import sel_posi2 from "@/components/common/sel_nowstore_posi2";
import sel_posi_out from "@/components/common/sel_out_posi";
import axios from "axios";

export default {
  components: { dtpicker, sel_posi1, sel_posi2, sel_posi_out },
  data() {
    return {
      vin: "",
      now_posi1: "",
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    input_err() {
      let err = [];
      this.$refs.posi1_ref.val == "" ? err.push("请选择位置1") : "";
      this.$refs.posi2_ref.val == "" ? err.push("请选择位置2") : "";
      return err;
    },
    input1_err() {
      let err = [];
      this.$refs.posiout_ref.val == "" ? err.push("请选择调出位置") : "";
      return err;
    },
    domoveout() {
      if (this.input1_err().length == 0) {
        this.$confirm("确认操作？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.carmoveout,
            method: "post",
            data: {
              vin: this.vin,
              movedt: this.$refs.moveoutdt.value1,
              posi: this.$refs.posiout_ref.val,
              token: this.token
            }
          }).then(re => {
            this.$message("操作完成");
            this.$refs.posiout_ref.val = "";
          });
        });
      } else {
        this.$message(this.input1_err().join("，"));
      }
    },
    domove() {
      if (this.input_err().length == 0) {
        this.$confirm("确认操作？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.carmove,
            method: "post",
            data: {
              vin: this.vin,
              movedt: this.$refs.movedt.value1,
              posi1: this.$refs.posi1_ref.val,
              posi2: this.$refs.posi2_ref.val,
              token: this.token
            }
          }).then(re => {
            this.$message("操作完成");
            this.$refs.posi1_ref.val = "";
            this.$refs.posi2_ref.val = "";
          });
        });
      } else {
        this.$message(this.input_err().join("，"));
      }
    },
    change_now_posi1() {
      this.now_posi1 = this.$refs.posi1_ref.val;
    }
  }
};
</script>

