<template>
  <div>
    <el-row gutter="5">
      <el-col span="2">
        <stype ref="stype_ref" @val_change="change_stype"></stype>
      </el-col>
      <el-col span="2">
        <price ref="price_ref" :stype="now_stype"></price>
      </el-col>
      <el-col span="2">
        <ltype ref="ltype_ref" :stype="now_stype"></ltype>
      </el-col>
      <el-col span="2">
        <clr ref="clr_ref" :stype="now_stype"></clr>
      </el-col>
      <el-col span="2">
        <dtpicker ref="dt1"></dtpicker>
      </el-col>
      <el-col span="2">
        <dtpicker ref="dt2"></dtpicker>
      </el-col>
      <el-col span="2">
        <sel_celler ref="celler_ref" cate="展厅"></sel_celler>
      </el-col>
      <el-col span="2">
        <sel_state ref="state_ref"></sel_state>
      </el-col>
      <el-col span="2">
        <el-input placeholder="客户姓名" v-model="customerName"></el-input>
      </el-col>
      <el-col span="2">
        <el-button :loading="queryloading" icon="el-icon-search" type="primary" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="details_in_page" border height="620px" style="width: 100%">
      <el-table-column prop="index" label="序号" width="50"></el-table-column>
      <el-table-column prop="cellerinfo[0].name" label="销售顾问" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="车型" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="价格" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置" width="300"></el-table-column>
      <el-table-column prop="clr" label="颜色" width="100"></el-table-column>
      <el-table-column prop="odt" label="定单日期" width="150"></el-table-column>
      <el-table-column prop="name" label="客户姓名" width="150"></el-table-column>
      <el-table-column prop="opay" label="定金" width="80"></el-table-column>
      <el-table-column prop="carpay" label="车款" width="80"></el-table-column>
      <el-table-column prop="zspay" label="装饰款" width="80"></el-table-column>
      <el-table-column prop="ajpay" label="按揭款" width="80"></el-table-column>
      <el-table-column prop="payway1" label="支付方式" width="100"></el-table-column>
      <el-table-column prop="payway2" label="方式关联" width="100"></el-table-column>
      <el-table-column label="操作" width="300" class="decostype">
        <template slot-scope="scope">
          <ul class="btnul">
            <li>
              <el-button size="mini" @click="showmore(scope.row)">{{scope.row.decoinfo.length}} 装饰</el-button>
            </li>
            <li>
              <el-button size="mini" @click="updateorderorder(scope.row.car_order_id)">修改</el-button>
            </li>
          </ul>
        </template>
      </el-table-column>
    </el-table>
    <div class="output">
      <el-button
        type="primary"
        plain
        @click="output"
        :loading="outputloading"
        icon="el-icon-download"
      >导出明细</el-button>
    </div>
    <el-divider>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="details.length"
        :hide-on-single-page="true"
        @current-change="cur_page"
      ></el-pagination>
    </el-divider>
  </div>
</template>

<script>
import stype from "@/components/common/sel_nowstore_stype";
import ltype from "@/components/common/sel_nowstore_ltype";
import price from "@/components/common/sel_nowstore_price";
import clr from "@/components/common/sel_nowstore_clr";
import order_au from "@/components/carorder/order_au";
import deco_au from "@/components/common/deco_au";
import sel_celler from "@/components/common/sel_celler";
import dtpicker from "@/components/common/datepicker";
import sel_state from "@/components/common/sel_order_state";

import axios from "axios";

export default {
  components: {
    stype,
    ltype,
    price,
    clr,
    order_au,
    deco_au,
    sel_celler,
    dtpicker,
    sel_state
  },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      customerName: "", //查询客户姓名
      au_update: false,
      queryloading: false,
      outputloading: false, //导出表格等待反馈
      nowpage: 1, //当前页数序号
      infoperpage: 10, //每页最大记录数量
      details: [], //总明细
      now_stype: "",
      now_order_id: "", //当前选中定单ID
      now_deco_order_id: "" //当前选中订单装饰ID
    };
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    },
    output_fmt() {
      let fmt = {};

      fmt.fields = [
        "销售顾问",
        "车型",
        "价格",
        "配置",
        "颜色",
        "订单日期",
        "客户姓名",
        "定金",
        "车款",
        "装饰款",
        "按揭款",
        "支付方式",
        "方式关联",
        "装饰项目"
      ];

      fmt.json = this.details.map(d => {
        let decos = [];
        if (d.decoinfo.length > 0) {
          decos = d.decoinfo.map(d => {
            return `${d.name}:${d.js}:0:${d.count}`;
          });
        }

        let re = {};
        re[fmt.fields[0]] = d.cellerinfo[0].name;
        re[fmt.fields[1]] = d.typeinfo[0].stype;
        re[fmt.fields[2]] = d.typeinfo[0].price;
        re[fmt.fields[3]] = d.typeinfo[0].ltype;
        re[fmt.fields[4]] = d.clr;
        re[fmt.fields[5]] = d.odt;
        re[fmt.fields[6]] = d.name;
        re[fmt.fields[7]] = d.opay;
        re[fmt.fields[8]] = d.carpay;
        re[fmt.fields[9]] = d.zspay;
        re[fmt.fields[10]] = d.ajpay;
        re[fmt.fields[11]] = d.payway1;
        re[fmt.fields[12]] = d.payway2;
        re[fmt.fields[13]] = decos.join("，");

        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    }
  },
  methods: {
    updateorderorder(id) {
      this.$emit("toUpdatePage", id);
    },
    output() {
      if (this.details.length == 0) {
        this.$message("没有数据");
      } else {
        this.$confirm("确认导出明细？", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then(re => {
          this.outputloading = true;
          let data = this.output_fmt;
          data.token = this.token;

          axios({
            url: require("@/config").axios.output,
            method: "post",
            data: data
          }).then(re => {
            window.open(require("@/config").domain + re.data.filename);
            this.outputloading = false;
          });
        });
      }
    },
    showmore(d) {
      const decoArr = d.decoinfo.map(d => {
        return `<tr><td>${d.name}</td><td>${d.count}</td></tr>`;
      });

      if (decoArr.length == 0) {
        this.$message("没有装饰项目");
      } else {
        const content = `<table width='100%'>
        <tr><th>项目</td><th>数量</th></tr>
        ${decoArr.join("")}
        </table>`;
        this.$alert(content, "详情", {
          dangerouslyUseHTMLString: true
        });
      }
    },
    change_stype(d) {
      this.now_stype = d;
    },
    cur_page(val) {
      this.nowpage = val;
    },
    queryObj() {
      //创建查询条件对象
      let re = {};
      re.stype = this.$refs.stype_ref.val;
      re.price = this.$refs.price_ref.val;
      re.ltype = this.$refs.ltype_ref.val;
      re.clr = this.$refs.clr_ref.val;
      re.celler = this.$refs.celler_ref.celler;
      re.token = this.token;
      re.dt1 = this.$refs.dt1.value1;
      re.dt2 = this.$refs.dt2.value1;
      re.state = this.$refs.state_ref.val;
      re.name = this.customerName;
      return re;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.orderlist,
        method: "post",
        data: this.queryObj()
      }).then(re => {
        if (re.data.code == 401) {
          this.details = [];
        } else {
          let i = 1;
          this.details = re.data.data.map(d => {
            d.index = i;
            i++;
            return d;
          });
          this.nowpage = 1;
        }
        this.queryloading = false;
      });
    }
  }
};
</script>

<style scoped>
.output {
  text-align: right;
}

.btnul {
  display: flex;
}
</style>