<template>
  <div>
    <el-tabs v-model="active">
      <el-tab-pane name="list" label="有效上报">
        <list @to_au="to_au"></list>
      </el-tab-pane>
      <el-tab-pane name="no_doss" label="已售未报">
        <sold_no_doss @to_au="to_au"></sold_no_doss>
      </el-tab-pane>
      <el-tab-pane name="doss_back" label="退车未报">
        <doss_back @to_au="to_au"></doss_back>
      </el-tab-pane>
      <el-tab-pane name="au" label="DOSS操作">
        <el-row>
          <el-col span="12" offset="6">
            <au
              :vin="vin"
              @to_sold_doss_list="to_sold_doss_list"
              @to_back_doss_list="to_back_doss_list"
              @todoss_list="todoss_list"
            ></au>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import list from "@/components/doss/list";
import au from "@/components/doss/au";
import count from "@/components/doss/count";
import sold_no_doss from "@/components/doss/sold_no_doss";
import doss_back from "@/components/doss/doss_back";

export default {
  components: {
    doss_back,
    list,
    au,
    count,
    sold_no_doss
  },
  data() {
    return {
      vin: "",
      token: sessionStorage.getItem("token"),
      active: "list"
    };
  },
  methods: {
    todoss_list() {
      this.active = "list";
    },
    to_back_doss_list() {
      this.active = "doss_back";
    },
    to_sold_doss_list() {
      this.active = "no_doss";
    },
    to_au(d) {
      this.active = "au";
      this.vin = d.vin;
    }
  }
};
</script>

