<template>
  <div>
    <el-row v-show="islink">
      <el-col span="24">
        <el-button type="danger" icon="el-icon-delete" @click="clearlink">解除指定</el-button>
      </el-col>
    </el-row>
    <el-table :data="orders" border style="width: 100%">
      <el-table-column prop="cellerinfo[0].name" label="销售顾问"></el-table-column>
      <el-table-column prop="name" label="客户"></el-table-column>
      <el-table-column prop="odt" label="定车日期"></el-table-column>
      <el-table-column prop="linkinfo[0].vin" label="指定车辆"></el-table-column>
      <el-table-column label="指定订单">
        <template slot-scope="scope">
          <el-button @click="dolink(scope.row)" circle icon="el-icon-paperclip" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      islink: false,
      orders: [],
      vin: "",
      clr: "",
      type_id: "",
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    clearlink() {
      if (this.vin != "") {
        this.$confirm("确认操作？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.clearlink,
            method: "post",
            data: { vin: this.vin, token: this.token }
          }).then(re => {
            this.$message("操作完成");
            this.islink = false;
            this.getOrders({
              vin: this.vin,
              type_id: this.type_id,
              clr: this.clr
            });
          });
        });
      } else {
        this.$message("请先选择车辆");
      }
    },
    dolink(info) {
      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(re => {
        axios({
          url: require("@/config").axios.dolinkorder,
          method: "post",
          data: {
            order_id: info.car_order_id,
            vin: this.vin,
            token: this.token
          }
        }).then(re => {
          this.islink = true;
          this.getOrders({
            vin: this.vin,
            type_id: this.type_id,
            clr: this.clr
          });
        });
      });
    },
    getOrders(car) {
      this.vin = car.vin;
      this.clr = car.clr;
      this.type_id = car.type_id;
      axios({
        url: require("@/config").axios.findOrderByTypeidAndClr,
        method: "post",
        data: {
          type_id: this.type_id,
          clr: this.clr,
          token: this.token
        }
      }).then(re => {
        if (re.data.code == 200) {
          this.orders = re.data.data.map(d => {
            d.odt = require("@/components/utils").longdate(d.odt);
            return d;
          });
        }
      });
    }
  }
};
</script>

