<template>
  <div>
    <el-row>
      <el-col span="2" offset="8">
        <myuser ref="user"></myuser>
      </el-col>
      <el-col span="1">
        <el-button type="primary" :loading="queryloading" @click="doquery" icon="el-icon-search">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <el-col span="8" offset="8">
        <collapse :data="rightsList" ref="collapse" @au_is_update="au_is_update"></collapse>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import myuser from "@/components/common/sel_user";
import collapse from "@/components/common/collapse";

export default {
  components: { myuser, collapse },
  data() {
    return {
      queryloading: false,
      rightsList: []
    };
  },
  methods: {
    au_is_update() {
      this.doquery();
    },
    getUserAu() {
      return new Promise((res, rej) => {
        axios({
          url: require("@/config").axios.getUserAu,
          method: "post",
          data: {
            uid: this.$refs.user.val,
            token: sessionStorage.getItem("token")
          }
        })
          .then(re => {
            res(re.data);
          })
          .catch(err => {
            rej(err);
          });
      });
    },
    async doquery() {
      const rights = await this.getUserAu();
      this.rightsList = this.rightsList.map(d => {
        d.items = d.items.map(item => {
          if (rights.length > 0 && rights.join(",").indexOf(item.id) > -1) {
            item.state = 1;
          } else {
            item.state = 0;
          }
          return item;
        });
        return d;
      });
      this.$refs.collapse.uid = this.$refs.user.val;
    }
  },
  mounted() {
    axios({
      url: require("@/config").axios.rightslist,
      method: "post",
      data: { token: sessionStorage.getItem("token") }
    }).then(re => {
      this.rightsList = re.data;
    });
  }
};
</script>

<style scoped>
button {
  cursor: pointer;
}
</style>