<template>
  <div>
    <el-row>
      <el-col span="5">
        <mthpicker ref="mtpk1_ref"></mthpicker>
      </el-col>
      <el-col span="2">
        <el-button type="primary" @click="doquery" :loading="queryloading" icon="el-icon-search">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>

    <el-table :data="details" style="width: 100%">
      <el-table-column prop="name" label="销售顾问" width="150"></el-table-column>
      <el-table-column label="交车">
        <el-table-column prop="sold.done" label="完成" width="80"></el-table-column>
        <el-table-column prop="sold.tag" label="目标" width="80"></el-table-column>
        <el-table-column prop="sold.rate" label="完成率" width="80"></el-table-column>
        <el-table-column prop="sold.pay" label="提成" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="特殊车">
        <el-table-column prop="special.done" label="完成" width="80"></el-table-column>
        <el-table-column prop="special.tag" label="目标" width="80"></el-table-column>
        <el-table-column prop="special.pay" label="提成" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="按揭">
        <el-table-column prop="anjie.done" label="完成" width="80"></el-table-column>
        <el-table-column prop="anjie.tag" label="目标" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="按揭收款">
        <el-table-column prop="anjiepay.done" label="完成" width="80"></el-table-column>
        <el-table-column prop="anjiepay.tag" label="目标" width="80"></el-table-column>
        <el-table-column prop="anjiepay.pay" label="提成" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="总产值">
        <el-table-column prop="zcz.done" label="完成" width="80"></el-table-column>
        <el-table-column prop="zcz.tag" label="目标" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="净产值">
        <el-table-column prop="jcz.done" label="完成" width="80"></el-table-column>
        <el-table-column prop="jcz.pay" label="提成" width="80"></el-table-column>
      </el-table-column>
      <el-table-column prop="paysum" label="提成合计"></el-table-column>
    </el-table>
    <div class="output">
      <el-button
        type="primary"
        plain
        @click="output"
        :loading="outputloading"
        icon="el-icon-download"
      >导出明细</el-button>
    </div>
  </div>
</template>

<script>
import mthpicker from "@/components/common/monthpicker";
import axios from "axios";
import cellermain from "@/components/celler/main";

export default {
  components: { mthpicker },
  data() {
    return {
      queryloading: false,
      outputloading: false,
      drawer: false,
      year: 0,
      month: 0,
      details: [],
      car_decos: []
    };
  },
  computed: {
    output_format() {
      let fmt = {};

      fmt.fields = [
        "销售顾问",
        "交车_完成",
        "交车_目标",
        "交车_完成率",
        "交车_提成",
        "特殊车_完成",
        "特殊车_目标",
        "特殊车_提成",
        "按揭_完成",
        "按揭_目标",
        "按揭收款_完成",
        "按揭收款_目标",
        "按揭收款_提成",
        "总产值_完成",
        "总产值_目标",
        "净产值_完成",
        "净产值_提成",
        "提成合计"
      ];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.name;
        re[fmt.fields[1]] = d.sold.done;
        re[fmt.fields[2]] = d.sold.tag;
        re[fmt.fields[3]] = d.sold.rate;
        re[fmt.fields[4]] = d.sold.pay;
        re[fmt.fields[5]] = d.special.done;
        re[fmt.fields[6]] = d.special.tag;
        re[fmt.fields[7]] = d.special.pay;
        re[fmt.fields[8]] = d.anjie.done;
        re[fmt.fields[9]] = d.anjie.tag;
        re[fmt.fields[10]] = d.anjiepay.done;
        re[fmt.fields[11]] = d.anjiepay.tag;
        re[fmt.fields[12]] = d.anjiepay.pay;
        re[fmt.fields[13]] = d.zcz.done; ////
        re[fmt.fields[14]] = d.zcz.tag;
        re[fmt.fields[15]] = d.jcz.done;
        re[fmt.fields[16]] = d.jcz.pay;
        re[fmt.fields[17]] = d.paysum;
        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    }
  },
  methods: {
    output() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        axios({
          url: require("@/config").axios.outputsalary,
          method: "post",
          data: this.output_format
        }).then(re => {
          window.open(require("@/config").domain + re.data.filename);
          this.outputloading = false;
        });
      });
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.salarycountlist,
        method: "post",
        data: {
          year: this.$refs.mtpk1_ref.year,
          month: this.$refs.mtpk1_ref.month,
          token: sessionStorage.getItem("token")
        }
      }).then(re => {
        //找不到对应月份政策数据
        if (re.data.code == 401) {
          this.details = [];
        } else {
          let total_count = {
            name: "合计",
            celler_id: "0",
            sold: {
              tag: 0,
              done: 0,
              rate: 0,
              pay: 0
            },
            special: {
              tag: 0,
              done: 0,
              pay: 0
            },
            anjie: {
              tag: 0,
              done: 0
            },
            anjiepay: {
              tag: 0,
              done: 0,
              pay: 0
            },
            zcz: {
              done: 0,
              tag: 0
            },
            jcz: {
              done: 0,
              pay: 0
            },
            paysum: 0,
            soldinfo: []
          };

          re.data.forEach(d => {
            total_count.sold.tag = total_count.sold.tag + d.sold.tag;
            total_count.sold.done = total_count.sold.done + d.sold.done;
            total_count.sold.pay = total_count.sold.pay + d.sold.pay;

            total_count.special.tag = total_count.special.tag + d.special.tag;
            total_count.special.done =
              total_count.special.done + d.special.done;
            total_count.special.pay = total_count.special.pay + d.special.pay;

            total_count.anjie.tag = total_count.anjie.tag + d.anjie.tag;
            total_count.anjie.done = total_count.anjie.done + d.anjie.done;

            total_count.anjiepay.tag =
              total_count.anjiepay.tag + d.anjiepay.tag;
            total_count.anjiepay.done =
              total_count.anjiepay.done + d.anjiepay.done;
            total_count.anjiepay.pay =
              total_count.anjiepay.pay + d.anjiepay.pay;

            total_count.zcz.tag = total_count.zcz.tag + d.zcz.tag;
            total_count.zcz.done = total_count.zcz.done + d.zcz.done;

            total_count.jcz.done = total_count.jcz.done + d.jcz.done;
            total_count.jcz.pay = total_count.jcz.pay + d.jcz.pay;

            total_count.paysum = total_count.paysum + d.paysum;
          });

          total_count.sold.rate =
            Math.round(total_count.sold.done / total_count.sold.tag) * 100 +
            "%";

          re.data.push(total_count);
          this.details = re.data;
        }
        this.queryloading = false;
      });
    }
  }
};
</script>

<style  scoped>
div .output {
  text-align: right;
}
</style>