<template>
  <div>
    <el-divider>新车型数据补全</el-divider>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>选择车型</label>
          <el-select
            v-model="fittypeinfo.ltype"
            placeholder="配置"
            filterable
            allow-create
            @change="findtypeinfo"
          >
            <el-option v-for="item in ltypes" :key="item.val" :label="item.val" :value="item.val"></el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row gutter="10">
      <el-col span="12">
        <div class="inputflex">
          <label>车型</label>
          <el-input placeholder="车型" v-model.number="fittypeinfo.stype"></el-input>
        </div>
      </el-col>

      <el-col span="12">
        <div class="inputflex">
          <label>价格</label>
          <el-input placeholder="价格" v-model.number="fittypeinfo.price"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row gutter="10">
      <el-col span="12">
        <div class="inputflex">
          <label>内部代码</label>
          <el-input placeholder="内部代码" v-model="fittypeinfo.code_pri"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>厂家代码</label>
          <el-input placeholder="厂家代码" v-model="fittypeinfo.code_pub"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="4" offset="20">
        <el-button type="primary" icon="el-icon-edit" @click="doupdatecarinfo">保存参数</el-button>
      </el-col>
    </el-row>
    <el-divider>已有车型数据更新</el-divider>
    <el-row gutter="10">
      <el-col span="12">
        <div class="inputflex">
          <label>主要车型</label>
          <sel_stype @val_change="change_stype"></sel_stype>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>车型配置</label>
          <sel_ltype :stype="now_stype" @val_change="oldtype_change"></sel_ltype>
        </div>
      </el-col>
    </el-row>
    <el-row gutter="10">
      <el-col span="12">
        <div class="inputflex">
          <label>车型</label>
          <el-input placeholder="车型" v-model.number="oldtypeinfo.stype"></el-input>
        </div>
      </el-col>

      <el-col span="12">
        <div class="inputflex">
          <label>价格</label>
          <el-input placeholder="价格" v-model.number="oldtypeinfo.price"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row gutter="10">
      <el-col span="12">
        <div class="inputflex">
          <label>内部代码</label>
          <el-input placeholder="内部代码" v-model="oldtypeinfo.code_pri"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>厂家代码</label>
          <el-input placeholder="厂家代码" v-model="oldtypeinfo.code_pub"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="4" offset="20">
        <el-button type="primary" icon="el-icon-edit" @click="doupdateoldinfo">保存参数</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import sel_stype from "@/components/common/sel_cartype_stype";
import sel_ltype from "@/components/common/sel_cartype_ltype";

export default {
  components: { sel_stype, sel_ltype },
  data() {
    return {
      ltypes: [],
      now_stype: "",
      token: sessionStorage.getItem("token"),
      fittypeinfo: {
        stype: "",
        ltype: "",
        code_pri: "",
        code_pub: "",
        price: 0
      },
      oldtypeinfo: {
        stype: "",
        ltype: "",
        code_pri: "",
        code_pub: "",
        price: 0
      }
    };
  },
  mounted() {
    this.getNewLtypes();
  },
  methods: {
    change_stype(d) {
      this.now_stype = d;
    },
    //保存已有车型的参数
    doupdateoldinfo() {
      if (this.oldtypeinfo.ltype == "") {
        this.$message("请选择车型配置");
      }

      this.$confirm("确认保存车型参数？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.updatecarinfo,
          method: "post",
          data: { token: this.token, data: this.oldtypeinfo }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    },
    //保存新车型的参数
    doupdatecarinfo() {
      if (this.fittypeinfo.ltype == "") {
        this.$message("请选择车型配置");
      }

      this.$confirm("确认保存车型参数？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.updatecarinfo,
          method: "post",
          data: { token: this.token, data: this.fittypeinfo }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    },
    oldtype_change(cartype) {
      axios({
        url: require("@/config").axios.findtypeinfo,
        method: "post",
        data: { token: this.token, ltype: cartype }
      }).then(re => {
        const info = re.data.data;
        this.oldtypeinfo.ltype = info.ltype;
        this.oldtypeinfo.code_pri = info.typecode_pri;
        this.oldtypeinfo.code_pub = info.typecode_pub;
        this.oldtypeinfo.price = info.price;
        this.oldtypeinfo.stype = info.stype;
      });
    },
    findtypeinfo() {
      axios({
        url: require("@/config").axios.findtypeinfo,
        method: "post",
        data: { token: this.token, ltype: this.fittypeinfo.ltype }
      }).then(re => {
        const info = re.data.data;
        this.fittypeinfo.code_pri = info.typecode_pri;
        this.fittypeinfo.code_pub = info.typecode_pub;
        this.fittypeinfo.price = info.price;
        this.fittypeinfo.stype = info.stype;
      });
    },
    getNewLtypes() {
      axios({
        url: require("@/config").axios.getNewLtypes,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        this.ltypes = re.data.data;
      });
    }
  }
};
</script>

<style scoped>
.checkboxstyle {
  line-height: 40px;
}

button {
  width: 100%;
}
</style>