<template>
  <div>
    <el-select v-model="val" placeholder="主要车型" clearable @change="val_change">
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["stype"],
  data() {
    return { vals: [], val: "", token: sessionStorage.getItem("token") };
  },
  watch: {
    stype(nv,ov) {
      nv==""?this.vals=[]:this.getlist();
    }
  },
  methods: {
    getlist() {
      axios({
        url: require("@/config").axios.carinway_ltypelist,
        method: "post",
        data: { token: this.token, stype: this.stype }
      }).then(re => {
        this.vals = re.data.data.map(d => {
          return { val: d };
        });
      });
    },
    val_change() {
      this.$emit("val_change", this.val);
    }
  }
};
</script>