<template>
  <div>
    <el-table :data="cellerspage" border style="width: 100%" height="620px">
      <el-table-column fixed prop="createdt" label="创建日期" width="150"></el-table-column>
      <el-table-column prop="cate" label="分区" width="120"></el-table-column>
      <el-table-column prop="name" label="姓名" width="120"></el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button type="text" @click="mute(scope.row._id)" circle  icon="el-icon-close"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-divider>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="cellers.length"
        :hide-on-single-page="true"
        @current-change="cur_page"
      ></el-pagination>
    </el-divider>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["cellers"],
  data() {
    return {
      nowpage: 1,
      infoperpage: 10,
      activeName: "list",
      token:sessionStorage.getItem("token")
    };
  },
  computed: {
    cellerspage() {
      if (this.cellers.length > 0) {
        return this.cellers.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    cur_page(val) {
      this.nowpage = val;
    },
    mute(id) {
      this.$confirm("禁用该销售人员吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.muteceller,
          method: "post",
          data: { id: id,token:this.token }
        }).then(re => {
          this.$emit("updatecellerlist_c");
        });
      });
    }
  }
};
</script>

