<template>
  <div>
    <el-row>
      <el-col span="8">
        <div class="inputflex">
          <label>展厅激励</label>
          <el-input v-model.number="info.payceller"></el-input>
        </div>
      </el-col>
      <el-col span="8">
        <div class="inputflex">
          <label>二网激励</label>
          <el-input v-model.number="info.paynet"></el-input>
        </div>
      </el-col>
      <el-col span="4">
        <el-button type="primary" icon="el-icon-edit" @click="addsoldme">设定激励</el-button>
      </el-col>
      <el-col span="4">
        <el-button type="warning" icon="el-icon-delete" @click="clearsoldme" v-show="has_pay">取消激励</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      has_pay: false,
      token: sessionStorage.getItem("token"),
      info: {
        payceller: 0,
        paynet: 0,
        vin: ""
      }
    };
  },
  methods: {
    addsoldme() {
      if (this.info.vin == "") {
        this.$message("请先选择车辆");
        return;
      }
      this.$confirm("增加促销奖励？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.addsoldme,
          method: "post",
          data: { data: this.info, token: this.token }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    },

    clearsoldme() {
      if (this.info.vin == "") {
        this.$message("请先选择车辆");
        return;
      }
      this.$confirm("取消促销奖励？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.clearsoldme,
          method: "post",
          data: { data: this.info, token: this.token }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    }
  }
};
</script>

