<template>
  <div>
    <el-button type="primary" :loading="loading" @click="getinfo">请求线上可CALL</el-button>
  </div>
</template>

<script>
import { no_au_then } from "@/components/utils";
import axios from "axios";
export default {
  props: ["login_ok"],
  watch: {
    login_ok(nv, ov) {
      nv == true ? this.getinfo() : "";
    }
  },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      loading: false,
      info: []
    };
  },
  methods: {
    getinfo() {
      this.loading = true;
      axios({
        url: require("@/config").axios.ppt_car_can_call,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        this.loading = false;
        no_au_then(re, re => {
          axios({
            url: require("@/config").axios.import_cancall,
            method: "post",
            data: { token: this.token, info: re.data.data }
          }).then(re => {
            no_au_then(re, re => {
              if (re.data.code == 200) {
                this.$message("操作完成");
              }
            });
          });
        });
      });
    }
  }
};
</script>