<template>
  <div>
    <el-autocomplete
      v-model="vin"
      :fetch-suggestions="querySearchAsync"
      placeholder="请输入四位车架号"
      @select="handleSelect"
    ></el-autocomplete>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: sessionStorage.getItem("token"),
      vin: "",
      timeout: null
    };
  },
  methods: {
    querySearchAsync(queryString, cb) {
      //车架号小于4位不执行
      if (queryString.trim().length < 3) {
        return;
      }

      axios({
        url: require("@/config").axios.carall_vin_search,
        method: "post",
        data: { token: this.token, info: { vin_parts: queryString } }
      }).then(re => {
        cb(re.data.data);
      });
    },
    handleSelect(item) {
      this.$emit("val_change", item);
    }
  }
};
</script>

