<template>
  <div>
    <el-tabs v-model="activeName">
      <el-tab-pane name="list" label="列表">
        <storelist @tocontrolpage_c="tocontrolpage"></storelist>
      </el-tab-pane>
      <el-tab-pane name="storecontrol" label="操作">
        <el-tabs v-model="activecontrol">
          <el-tab-pane name="carinfo" label="详情">
            <el-row>
              <el-col span="16" offset="4">
                <carinfo ref="carinfo_ref"></carinfo>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="carsold" label="销售交车">
            <el-row>
              <el-col span="12" offset="6">
                <addsold ref="addsold_ref"></addsold>
              </el-col>
            </el-row>
          </el-tab-pane>

          <el-tab-pane name="carmove" label="调拨移动">
            <el-row>
              <el-col span="12" offset="6">
                <carmove ref="carmove_ref"></carmove>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="keymove" label="钥匙变动">
            <el-row>
              <el-col span="8" offset="8">
                <keymove ref="keymove_ref"></keymove>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="orderlink" label="指定订单">
            <el-row>
              <el-col span="12" offset="6">
                <orderlink ref="orderlink_ref"></orderlink>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="fullpay" label="车辆点款">
            <el-row>
              <el-col span="12" offset="6">
                <payfull ref="payfull_ref"></payfull>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="decocontrol" label="加装">
            <el-row>
              <el-col span="12" offset="6">
                <deco_au_car :vin="vin" :update_tri="update_deco_tri"></deco_au_car>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="order" label="生成展厅订单">
            <el-row>
              <el-col span="12" offset="6">
                <order_au @tolist="tolist" :vin="vin"></order_au>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="orderfast" label="生成二网订单">
            <el-row>
              <el-col span="12" offset="6">
                <order_fast_au @tolist="tolist" :vin="vin"></order_fast_au>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="soldme" label="促销">
            <el-row>
              <el-col span="12" offset="6">
                <soldme ref="soldme_ref"></soldme>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane name="tags" label="标签管理">
            <el-row>
              <el-col span="12" offset="6">
                <tags ref="tags_ref" :vin="vin"></tags>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>
        <el-divider>
          <el-link type="primary" @click="tolist" icon="el-icon-caret-left">返回列表</el-link>
        </el-divider>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import storelist from "@/components/nowstore/carstorelist";
import carmove from "@/components/nowstore/carcontrol/carmove";
import orderlink from "@/components/nowstore/carcontrol/orderlink";
import keymove from "@/components/nowstore/carcontrol/keymove";
import payfull from "@/components/nowstore/carcontrol/payfull";
import carinfo from "@/components/nowstore/carcontrol/carinfo";
import deco_au_car from "@/components/common/deco_au_car";
import order_au from "@/components/carorder/order_au";
import soldme from "@/components/nowstore/carcontrol/soldme";
import addsold from "@/components/nowstore/carcontrol/addsold";
import tags from "@/components/nowstore/carcontrol/cartag";
import order_fast_au from "@/components/carorder/order_fast_au";

export default {
  components: {
    order_fast_au,
    storelist,
    carmove,
    orderlink,
    keymove,
    payfull,
    carinfo,
    deco_au_car,
    order_au,
    soldme,
    addsold,
    tags
  },
  data() {
    return {
      list_on: true,
      activeName: "list",
      activecontrol: "carinfo",
      now_car_info: {},
      vin: ""
    };
  },
  methods: {
    tolist() {
      this.vin = "";
      this.activeName = "list";
    },
    tocontrolpage(data) {
      //切换页面
      this.activecontrol = "carinfo";
      this.activeName = "storecontrol";
      this.now_car_info = data;
      this.vin = this.now_car_info.vin;
      this.update_deco_tri = Date.now();

      //添加销售的可用订单列表
      this.$refs.addsold_ref.getorderfit(data);

      //车辆明细初始化
      this.$refs.carinfo_ref.info = data;

      //车辆调拨初始化
      this.$refs.carmove_ref.vin = data.vin;

      //钥匙调拨初始化
      this.$refs.keymove_ref.vin = data.vin;
      if (data.key_posi == "") {
        this.$refs.keymove_ref.key_is_out = false;
      } else {
        this.$refs.keymove_ref.key_is_out = true;
      }

      //车辆点款初始化
      this.$refs.payfull_ref.vin = data.vin;
      if (data.carinfo[0].payall == "") {
        this.$refs.payfull_ref.is_payfull = false;
      } else {
        this.$refs.payfull_ref.is_payfull = true;
      }

      //车辆订单 绑定初始化
      if (data.linkinfo.length > 0) {
        this.$refs.orderlink_ref.islink = true;
      } else {
        this.$refs.orderlink_ref.islink = false;
      }

      this.$refs.orderlink_ref.getOrders({
        vin: data.vin,
        clr: data.carinfo[0].clr,
        type_id: data.carinfo[0].type_id
      });

      //促销奖励
      this.$refs.soldme_ref.info.vin = data.vin;
      if (data.soldmeinfo.length > 0) {
        this.$refs.soldme_ref.has_pay = true;
        this.$refs.soldme_ref.info.payceller = data.soldmeinfo[0].payceller;
        this.$refs.soldme_ref.info.paynet = data.soldmeinfo[0].paynet;
      } else {
        this.$refs.soldme_ref.has_pay = false;
        this.$refs.soldme_ref.info.payceller = 0;
        this.$refs.soldme_ref.info.paynet = 0;
      }
    }
  }
};
</script>

