<template>
  <div>
    <el-row>
      <el-col span="2">
        <dtpicker @val_change="change_dt1"></dtpicker>
      </el-col>
      <el-col span="2">
        <dtpicker @val_change="change_dt2"></dtpicker>
      </el-col>
      <el-col span="2">
        <sel_state @val_change="change_state" :data="info.state"></sel_state>
      </el-col>
      <el-col span="3">
        <el-input v-model="info.vin" placeholder="VIN" @keyup.enter.native="doquery"></el-input>
      </el-col>
      <el-col span="3">
        <el-button icon="el-icon-search" type="primary" @click="doquery" :loading="queryloading">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details_in_page" border height="550px" style="width: 100%">
      <el-table-column prop="index" label="序号" width="50"></el-table-column>
      <el-table-column prop="vin" label="VIN" width="200"></el-table-column>
      <el-table-column prop="bonus" label="奖金" width="100"></el-table-column>
      <el-table-column prop="reserve" label="储备金" width="100"></el-table-column>
      <el-table-column label="合计" width="100">
        <template slot-scope="scope">{{scope.row.bonus+scope.row.reserve}}</template>
      </el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="车型" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="价格" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置"></el-table-column>
      <el-table-column prop="calldt" label="CALL车日期" width="150"></el-table-column>
      <el-table-column prop="kpdt" label="开票日期" width="200"></el-table-column>
      <el-table-column prop="fpcode" label="发票号码" width="200"></el-table-column>
    </el-table>

    <div class="outputstyle">
      <el-link icon="el-icon-download" @click="output">导出明细</el-link>
    </div>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="details.length"
      :hide-on-single-page="true"
      @current-change="cur_page"
    ></el-pagination>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datepicker";
import sel_stype from "@/components/common/sel_nowstore_stype";
import sel_price from "@/components/common/sel_nowstore_price";
import sel_ltype from "@/components/common/sel_nowstore_ltype";
import { shortdate, outputData, no_au_then } from "@/components/utils";
import sel_state from "@/components/common/sel_callcar_fp_state";
import axios from "axios";

export default {
  components: { dtpicker, sel_stype, sel_price, sel_ltype, sel_state },
  data() {
    return {
      report: "",
      token: sessionStorage.getItem("token"),
      details: [],
      info: {},
      queryloading: false,
      nowpage: 1, //当前页数序号
      infoperpage: 10 //每页最大记录数量
    };
  },
  mounted() {
    this.clearInfo();
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    output_format() {
      let fmt = {};

      fmt.fields = [
        "VIN",
        "奖金",
        "储备金",
        "合计",
        "车型",
        "价格",
        "配置",
        "CALL车日期",
        "开票日期",
        "发票号"
      ];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.vin;
        re[fmt.fields[1]] = d.bonus;
        re[fmt.fields[2]] = d.reserve;
        re[fmt.fields[3]] = d.bonus + d.reserve;
        re[fmt.fields[4]] = d.typeinfo[0].stype;
        re[fmt.fields[5]] = d.typeinfo[0].price;
        re[fmt.fields[6]] = d.typeinfo[0].ltype;
        re[fmt.fields[7]] = d.calldt;
        re[fmt.fields[8]] = d.kpdt;
        re[fmt.fields[9]] = d.fpcode;
        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    },
    output() {
      if (this.details.length == 0) {
        this.$message("没有数据");
        return;
      }

      outputData(this.output_format());
    },
    createReport() {
      let map = new Map(),
        sum = 0,
        re = [];

      this.details.forEach(d => {
        map.get(d.typeinfo[0].stype) == undefined
          ? map.set(d.typeinfo[0].stype, 1)
          : map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1);
      });

      map.forEach((v, k) => {
        re.push(`${k}：${v}`);
        sum = sum + v;
      });
      re.push(`合计：${sum}`);

      this.report = re.join("，");
    },
    cur_page(val) {
      this.nowpage = val;
    },
    change_state(d) {
      this.info.state = d;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.callcarlist,
        method: "post",
        data: { token: this.token, info: this.info }
      }).then(re => {
        this.queryloading = false;
        no_au_then(re, re => {
          let i = 1;
          let data = re.data.data;
          data.forEach(d => {
            d.calldt = shortdate(d.calldt);
            d.index = i;
            i++;
          });
          this.details = data;
          this.nowpage = 1;
          this.createReport();
        });
      });
    },
    clearInfo() {
      this.info = {
        dt1: new Date(),
        dt2: new Date(),
        stype: "",
        price: "",
        ltype: "",
        vin: "",
        state: ""
      };
    },
    change_dt1(d) {
      this.info.dt1 = d;
    },
    change_dt2(d) {
      this.info.dt2 = d;
    },
    change_stype(d) {
      this.info.stype = d;
      this.info.price = "";
      this.info.ltype = "";
      this.info.vin = "";
      this.info.state = "";
    },

    change_price(d) {
      this.info.price = d;
      this.info.ltype = "";
      this.info.vin = "";
      this.info.state = "";
    },
    change_ltype(d) {
      this.info.ltype = d;
      this.info.vin = "";
      this.info.state = "";
    }
  }
};
</script>

