<template>
  <div>
    <el-select v-model="celler" placeholder="销售顾问" @change="sel_change" clearable>
      <el-option v-for="item in cellers" :key="item.id" :label="item.name" :value="item.id"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["cate", "data"],
  data() {
    return { cellers: [], celler: "" };
  },
  watch: {
    data(nv, ov) {
      this.celler = nv;
    },
    cate(nv, ov) {
      nv == "" ? (this.cellers = []) : this.getData();
    }
  },
  mounted() {
    if (this.cate != "") {
      this.getData();
    }
  },
  methods: {
    getData() {
      axios({
        url: require("@/config").axios.cellermanlist + "/" + this.cate,
        method: "post",
        data: { token: sessionStorage.getItem("token") }
      }).then(re => {
        this.cellers = re.data.data.map(d => {
          return { id: d.celler_id, name: d.name };
        });
      });
    },
    sel_change(data) {
      this.$emit("val_change", data);
    }
  }
};
</script>