<template>
  <div>
    <el-row gutter="5">
      <el-col span="2">
        <stype ref="stype_ref" @val_change="change_stype"></stype>
      </el-col>
      <el-col span="2">
        <price ref="price_ref" @val_change="change_price" :stype="order.stype"></price>
      </el-col>
      <el-col span="2">
        <ltype ref="ltype_ref" @val_change="change_ltype" :stype="order.stype"></ltype>
      </el-col>
      <el-col span="2">
        <clr ref="clr_ref" @val_change="change_clr" :stype="order.stype"></clr>
      </el-col>
      <el-col span="2">
        <dtpicker ref="dt1" @val_change="change_dt1"></dtpicker>
      </el-col>
      <el-col span="2">
        <dtpicker ref="dt2" @val_change="change_dt2"></dtpicker>
      </el-col>
      <el-col span="2">
        <sel_cate @val_change="change_cate"></sel_cate>
      </el-col>
      <el-col span="2">
        <sel_celler @val_change="change_celler" :cate="order.cate"></sel_celler>
      </el-col>
      <el-col span="2">
        <el-button :loading="queryloading" icon="el-icon-search" type="primary" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="details_in_page" border height="620px" style="width: 100%">
      <el-table-column prop="index" label="序号" width="50"></el-table-column>
      <el-table-column prop="cellerinfo[0].name" label="销售顾问" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="车型" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="价格" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置"></el-table-column>
      <el-table-column prop="tips" label="备注"></el-table-column>
      <el-table-column prop="clr" label="颜色" width="100"></el-table-column>
      <el-table-column prop="odt" label="定单日期" width="150"></el-table-column>
      <el-table-column label="操作" width="300" class="decostype">
        <template slot-scope="scope">
          <ul class="btnul">
            <li>
              <el-button
                size="mini"
                @click="tocontrol(scope.row.car_order_id)"
                plain
                type="primary"
                icon="el-icon-setting"
                circle
              ></el-button>
            </li>
          </ul>
        </template>
      </el-table-column>
    </el-table>
    <el-divider>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="details.length"
        :hide-on-single-page="true"
        @current-change="cur_page"
      ></el-pagination>
    </el-divider>
  </div>
</template>

<script>
import stype from "@/components/common/sel_nowstore_stype";
import ltype from "@/components/common/sel_nowstore_ltype";
import price from "@/components/common/sel_nowstore_price";
import clr from "@/components/common/sel_nowstore_clr";
import sel_cate from "@/components/common/sel_carsold_cate";
import sel_celler from "@/components/common/sel_celler";
import dtpicker from "@/components/common/datepicker";

import axios from "axios";

export default {
  components: {
    stype,
    ltype,
    price,
    clr,
    sel_cate,
    sel_celler,
    dtpicker
  },
  data() {
    return {
      order: {},
      token: sessionStorage.getItem("token"),
      customerName: "", //查询客户姓名
      au_update: false,
      queryloading: false,
      outputloading: false, //导出表格等待反馈
      nowpage: 1, //当前页数序号
      infoperpage: 10, //每页最大记录数量
      details: [], //总明细
      now_stype: "",
      now_order_id: "", //当前选中定单ID
      now_deco_order_id: "" //当前选中订单装饰ID
    };
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  mounted() {
    this.clear_data();
  },
  methods: {
    clear_data() {
      this.order = {
        stype: "",
        price: "",
        ltype: "",
        clr: "",
        dt1: new Date(),
        dt2: new Date(),
        cate: "",
        celler_id: ""
      };
    },
    tocontrol(id) {
      this.$emit("tocontrol_c", id);
    },
    change_stype(d) {
      this.order.stype = d;
    },
    change_price(d) {
      this.order.price = d;
    },
    change_ltype(d) {
      this.order.ltype = d;
    },
    change_clr(d) {
      this.order.clr = d;
    },
    change_dt1(d) {
      this.order.dt1 = d;
    },
    change_dt2(d) {
      this.order.dt2 = d;
    },
    change_celler(d) {
      this.order.celler_id = d;
    },
    change_cate(d) {
      this.order.cate = d;
    },
    cur_page(val) {
      this.nowpage = val;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.fastorderlist,
        method: "post",
        data: { token: this.token, info: this.order }
      }).then(re => {
        require("@/components/utils").no_au_then(re, re => {
          if (re.data.code == 401) {
            this.details = [];
          } else {
            let i = 1;
            this.details = re.data.data.map(d => {
              d.index = i;
              i++;
              return d;
            });
            this.nowpage = 1;
          }
          this.queryloading = false;
        });
      });
    }
  }
};
</script>

