<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="tbClick">
      <el-tab-pane label="人员列表" name="list">
        <el-row>
          <el-col span="3">
            <el-input
              placeholder="请输入关键字查询"
              @keyup.enter.native="doactivequery"
              v-model="actviecellername"
            ></el-input>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <mycellerlist :cellers="cellerlist" @updatecellerlist_c="updatecellerlist_p"></mycellerlist>
      </el-tab-pane>
      <el-tab-pane label="添加人员" name="addceller">
        <myaddceller @updatecellerlist_c="updatecellerlist_p"></myaddceller>
      </el-tab-pane>
      <el-tab-pane label="激活人员" name="activeceller">
        <el-row>
          <el-col span="3">
            <el-input placeholder="请输入关键字查询" @keyup.enter.native="doquery" v-model="cellername"></el-input>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <activeceller @updatemutecellerlist_c="updatecellerlist_p" :cellers="mutecellerlist"></activeceller>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import mytitle from "@/components/common/title";
import myaddceller from "@/components/celler/addceller";
import mycellerlist from "@/components/celler/cellerlist";
import activeceller from "@/components/celler/activeceller";
import axios from "axios";

export default {
  components: { mytitle, myaddceller, mycellerlist, activeceller },
  data() {
    return {
      activeName: "list",
      cellerlist: [],
      mutecellerlist: [],
      cellername: "",
      actviecellername: "",
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    doactivequery() {
      this.updateactivecellerlist_p(this.actviecellername);
    },
    doquery() {
      this.updatemutecellerlist_p(this.cellername);
    },
    updatecellerlist_p() {
      this.updateactivecellerlist_p();
      this.updatemutecellerlist_p(this.cellername);
    },
    updatemutecellerlist_p(cellername) {
      axios({
        url: require("@/config").axios.mutecellerlist,
        method: "post",
        data: { cellername: cellername, token: this.token }
      }).then(re => {
        re.data.data.map((d, i) => {
          d.createdt = require("@/components/utils.js").longdate(d.createdt);
        });
        this.mutecellerlist = re.data.data;
      });
    },
    updateactivecellerlist_p(actviecellername) {
      axios({
        url: require("@/config").axios.cellerlist,
        method: "post",
        data: {
          cellername: actviecellername,
          token:this.token
        }
      }).then(re => {
        re.data.data.map((d, i) => {
          d.createdt = require("@/components/utils.js").longdate(d.createdt);
        });
        this.cellerlist = re.data.data;
      });
    },
    tbClick(tb, event) {
      console.log(tab, event);
    }
  }
};
</script>

<style lang="scss" scoped>
</style>