<template>
  <div>
    <el-divider>当前标签</el-divider>
    <el-table :data="tags_on" style="width:100%" border>
      <el-table-column prop="name" label="标签名称" width="150px"></el-table-column>
      <el-table-column prop="content" label="具体描述"></el-table-column>
      <el-table-column label="操作" width="80px">
        <template slot-scope="scope">
          <el-button size="mini" @click="deltag(scope.row)" type="danger">移除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-divider>可用标签</el-divider>
    <el-table :data="tags_off" style="width:100%" border>
      <el-table-column prop="name" label="标签名称" width="150px"></el-table-column>
      <el-table-column prop="content" label="具体描述"></el-table-column>
      <el-table-column label="操作" width="80px">
        <template slot-scope="scope">
          <el-button size="mini" @click="addtag(scope.row)" type="primary">添加</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["vin"],
  data() {
    return {
      token: sessionStorage.getItem("token"),
      all_tags: [],
      val: "",
      info: { vin: "" },
      tags_on: [],
      tags_off: []
    };
  },
  watch: {
    vin(nv, ov) {
      this.info.vin = nv;
      this.getlist();
    }
  },
  methods: {
    deltag(d) {
      this.$confirm("确认删除标签？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.car_tag_del,
          method: "post",
          data: {
            token: this.token,
            info: { vin: this.info.vin, tag_id: d.tag_id }
          }
        }).then(re => {
          this.$message("操作完成");
          this.getlist();
        });
      });
    },
    addtag(d) {
      this.$confirm("确认添加标签？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.car_tag_add,
          method: "post",
          data: {
            token: this.token,
            info: { vin: this.info.vin, tag_id: d.tag_id }
          }
        }).then(re => {
          this.$message("操作完成");
          this.getlist();
        });
      });},
    getlist() {
      if (this.info.vin == "") {
        this.$message("请先选择车辆");
        return;
      }
      axios({
        url: require("@/config").axios.tag_sincar,
        method: "post",
        data: { token: this.token, info: { vin: this.info.vin } }
      }).then(re => {
        this.tags_on = re.data.data.tags_on;
        this.tags_off = re.data.data.tags_off;
      });
    }
  }
};
</script>

