<template>
  <div>
    <el-row>
      <el-col span="8" offset="8">
        <ul>
          <li>
            <div class="inputflex">
              <label>分类</label>
              <el-select v-model.lazy="cate" placeholder="请选择">
                <el-option v-for="item in cates" :key="item" :label="item" :value="item"></el-option>
              </el-select>
            </div>
          </li>
          <li>
            <div class="inputflex">
              <label>人员姓名</label>
              <el-input placeholder="请输入人员姓名" v-model="name"></el-input>
            </div>
          </li>
          <li>
            <el-button type="primary" @click="doaddceller" icon="el-icon-edit-outline">确认添加</el-button>
          </li>
        </ul>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      cate: "",
      cates: ["展厅", "网点"],
      name: "",
      token: sessionStorage.getItem("token")
    };
  },
  mounted() {
    this.cate = this.cates[0];
  },
  methods: {
    doaddceller() {
      if (this.name.length > 0 && this.cate.length > 0) {
        this.$confirm("确定添加人员？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          axios({
            url: require("@/config").axios.addceller,
            method: "post",
            data: {
              name: this.name,
              cate: this.cate,
              token: this.token
            }
          }).then(() => {
            this.name = "";
            this.$alert("人员添加成功");
            this.$emit("updatecellerlist_c", { code: 999 });
          });
        });
      } else {
        this.$alert("请输入完整信息！");
      }
    }
  }
};
</script>

<style  scoped>
div button {
  width: 100%;
}
div ul li {
  padding: 8px 0;
}
</style>