<template>
  <div>
    <el-row>
      <el-col span="24">
        <data_count :data="datas"></data_count>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="4" offset="20">
        <el-button icon="el-icon-refresh" type="success" @click="getDatas">刷新</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import data_count from "@/components/common/data_count";

export default {
  components: { data_count },
  data() {
    return {
      datas: {},
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    getDatas() {
      axios({
        url: require("@/config").axios.getOrderCount,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        this.datas = re.data;
      });
    }
  },
  mounted() {
    this.datas = this.getDatas();
  }
};
</script>

<style scoped>
table {
  width: 100%;
  color: #444;
  font-size: 0.9rem;
  border-collapse: collapse;
}

table th,
table td {
  padding: 5px 15px;
  border-bottom: 1px solid #ddd;
}
</style>