<template>
  <div>
    <el-tabs v-model="activeTable" @tab-click="tbClick">
      <el-tab-pane name="orderlist" label="列表">
        <order_list @toUpdatePage="toUpdatePage"></order_list>
      </el-tab-pane>
      <el-tab-pane name="addorder" label="订单管理">
        <el-row>
          <el-col span="12" offset="6">
            <order_au @tolist="tolist" :car_order_id="car_order_id"></order_au>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane name="count" label="统计">
        <el-row>
          <el-col span="12" offset="6">
            <order_count></order_count>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import order_au from "./order_au";
import order_count from "./order_count";
import order_list from "./orderlist";

export default {
  components: { order_au, order_count, order_list },
  data() {
    return {
      activeTable: "orderlist",
      car_order_id: ""
    };
  },
  methods: {
    tolist() {
      this.activeTable = "orderlist";
      this.car_order_id = "";
    },
    toUpdatePage(id) {
      this.activeTable = "addorder";
      this.car_order_id = id;
    },
    tbClick() {
      console.log("tbClick");
    }
  }
};
</script>

