<template>
  <div>
    <el-row>
      <el-col span="4">
        <monthpicker ref="monthpicker_ref"></monthpicker>
      </el-col>
      <el-col span="2">
        <el-button type="primary" @click="doquery" :loading="queryloading" icon="el-icon-search">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="taglistpage" border height="620px" style="width: 100%" row-key="_id">
      <el-table-column prop="cellername" label="销售人员" width="100"></el-table-column>
      <el-table-column prop="chanzhi" label="单车产值" width="100"></el-table-column>
      <el-table-column prop="anjie" label="单车按揭款" width="100"></el-table-column>
      <el-table-column prop="soldtag" label="交车目标" width="100"></el-table-column>
      <el-table-column prop="anjietag" label="按揭数量" width="100"></el-table-column>
      <el-table-column prop="specialtag" label="特殊车" width="100"></el-table-column>
      <el-table-column prop="tagrate" label="任务完成率" width="100"></el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="del(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import monthpicker from "@/components/common/monthpicker";
import updatetag from "@/components/salary/addtag";
import axios from "axios";

export default {
  components: { monthpicker, updatetag },
  data() {
    return {
      queryloading: false,
      nowpage: 1,
      infoperpage: 10,
      drawer: false,
      direction: "ltr",
      isactive: true,
      taglist: []
    };
  },
  computed: {
    taglistpage() {
      if (this.taglist.length > 0) {
        return this.taglist.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    del(id) {
      this.$confirm("删除该记录数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.deltag,
          method: "post",
          data: { _id: id, token: sessionStorage.getItem("token") }
        }).then(re => {
          if (re.data.ok) {
            this.$message("删除成功");
            this.doquery();
          }
        });
      });
    },
    cur_page(val) {
      this.nowpage = val;
    },
    updatedeco_close_p() {
      this.drawer = false;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.taglist,
        method: "post",
        data: {
          year: this.$refs.monthpicker_ref.year,
          month: this.$refs.monthpicker_ref.month,
          token: sessionStorage.getItem("token")
        }
      }).then(re => {
        this.taglist = re.data.data.map(d => {
          d.cellername = d.cellerinfo[0].name;
          return d;
        });
        this.queryloading = false;
      });
    }
  }
};
</script>

