<template>
  <div>
    <el-select v-model="code" placeholder="DOSS状态" @change="sel_change" clearable>
      <el-option v-for="item in codes" :key="item.id" :label="item.name" :value="item.id"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["data"],
  data() {
    return {
      codes: [
        { id: 0, name: "交车上报" },
        { id: 1, name: "虚报" },
        { id: 2, name: "申请退车" },
        { id: 3, name: "退车完成" }
      ],
      code: ""
    };
  },
  watch: {
    data(nv, ov) {
      this.code = nv;
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    sel_change(data) {
      this.$emit("val_change", data);
    }
  }
};
</script>