<template>
  <div class="frame">
    <div id="by_age"></div>
  </div>
</template>

<script>
import G2 from "@antv/g2";
import { shortdate, days } from "@/components/utils";

export default {
  props: ["info"],
  watch: {
    info(nv, ov) {
      this.data_process(nv);
    }
  },
  data() {
    return {
      info: [],
      g2info: [],
      chart: {}
    };
  },
  mounted() {
    this.char_init();
  },
  methods: {
    char_init() {
      this.chart = new G2.Chart({
        container: "by_age", // 指定图表容器 ID
        forceFit: true,
        height: 300 // 指定图表高度
      });

      this.chart
        .interval()
        .position("x*y")
        .color("x");
      // Step 4: 渲染图表
    },
    data_process(info) {
      let map = new Map();
      let ages = [
        "-30天",
        "30-60天",
        "60-90天",
        "90-120天",
        "120-150天",
        "150-180天",
        "180-210天",
        "210天+"
      ];

      ages.forEach(d => {
        map.set(d, 0);
      });

      info.storelist.forEach(d => {
        const age = days(shortdate(d.carinfo[0].pdt), shortdate(new Date()));
        let key = "";

        age < 30 ? (key = "-30天") : "";
        age >= 30 && age < 60 ? (key = "30-60天") : "";
        age >= 60 && age < 90 ? (key = "60-90天") : "";
        age >= 90 && age < 120 ? (key = "90-120天") : "";
        age >= 120 && age < 150 ? (key = "120-150天") : "";
        age >= 150 && age < 180 ? (key = "150-180天") : "";
        age >= 180 && age < 210 ? (key = "180-210天") : "";
        age >= 210 ? (key = "210天+") : "";
        map.get(key) == undefined
          ? map.set(key, 1)
          : map.set(key, map.get(key) + 1);
      });

      let re = [];
      map.forEach((v, k) => {
        re.push({ x: k, y: v });
      });
      this.g2info = re;
      this.chart.source(this.g2info);
      this.chart.render();
    }
  }
};
</script>

<style scoped>
.frame {
  padding: 10px;
  border-bottom: 1px solid #dde;
}
</style>