  </div>
</template>

<script>
import myheader from "./layout/header";
import mymainer from "./layout/mainer";
import myaside from "./layout/asider";
import myfooter from "./layout/footer";

export default {
  components: { myheader, mymainer, myaside, myfooter },
  data() {
    return {
      token: "",
      loadcomponent: "reg"
    };
  },
  methods: {
    shift_com(d) {
      this.$refs.mainer_ref.cur_load = d.key;
      this.$refs.mainer_ref.current = d.curr_user;
    }
  }
};
</script>

