<template>
  <div>
    <el-select
      v-model="val"
      placeholder="方式2"
      clearable
      allow-create
      filterable
      @change="val_change"
    >
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["payway1", "data"],
  data() {
    return { vals: [], val: "" };
  },
  watch: {
    data(nv, ov) {
      this.val = nv;
    },
    payway1(nv, ov) {
      nv == "" ? (this.vals = []) : this.getdata();
    }
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.val);
    },
    getdata() {
      axios({
        url: require("@/config").axios.carsoldpayway2list + "/" + this.payway1,
        method: "post",
        data: { token: sessionStorage.getItem("token") }
      }).then(re => {
        this.vals = re.data.data.map(d => {
          return { val: d };
        });
      });
    }
  }
};
</script>