<template>
  <div>
    <el-button @click="getHistory" type="primary" size="mini" plain="">历史</el-button>
  </div>
</template>

<script>
import utils from "@/components/utils";
import axios from "axios";

export default {
  props: ["vin"],
  data() {
    return { token: sessionStorage.getItem("token") };
  },
  methods: {
    getHistory() {
      if (this.vin == "") {
        this.$message("未获取到车架号");
        return;
      }
      axios({
        url: require("@/config").axios.carhistory,
        method: "post",
        data: {
          token: this.token,
          vin: this.vin
        }
      }).then(re => {
        if (re.data.length == 0) {
          this.$message("没有记录");
        } else {
          const historys = re.data.map(d => {
            return `<tr><td>${utils.longdate(d.dt)}</td><td>${d.msg}</td></tr>`;
          });

          const content = `<table width='100%'>
        <tr><th>日期</td><th>描述</th></tr>
        ${historys.join("")}
        </table>`;

          this.$alert(content, "历史", {
            dangerouslyUseHTMLString: true
          });
        }
      });
    }
  }
};
</script>

<style  scoped>
div {
  display: inline-block;
  margin-left: 10px;
}
</style>