<template>
  <div>
    <el-row>
      <el-col span="3">
        <dtpicker @val_change="change_dt1"></dtpicker>
      </el-col>
      <el-col span="3">
        <dtpicker @val_change="change_dt2"></dtpicker>
      </el-col>
      <el-col span="3">
        <sel_doss_code @val_change="change_code"></sel_doss_code>
      </el-col>
      <el-col span="2">
        <el-button type="primary" icon="el-icon-search" :loading="queryloading" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-divider>{{report1}}</el-divider>
    <el-table :data="details_in_page" border height="580px" style="width:100%">
      <el-table-column prop="index" width="50" label="序号"></el-table-column>
      <el-table-column width="200" label="VIN">
        <template slot-scope="scope">
          <el-link @click="tocontrol(scope.row)" 
            icon="el-icon-caret-right">{{scope.row.vin}}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="typeinfo[0].stype" width="80" label="车型"></el-table-column>
      <el-table-column prop="typeinfo[0].price" width="80" label="价格"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置"></el-table-column>
      <el-table-column prop="carinfo[0].clr" width="80" label="颜色"></el-table-column>
      <el-table-column prop="dt" label="操作日期"></el-table-column>
      <el-table-column label="销售顾问">
        <template slot-scope="scope">
          <span
            v-show="scope.row.soldinfo.length>0"
          >{{scope.row.cellerinfo[0].cate}}-{{scope.row.cellerinfo[0].name}}</span>
          <span v-show="scope.row.soldinfo.length==0">-</span>
        </template>
      </el-table-column>
      <el-table-column label="客户姓名">
        <template slot-scope="scope">
          <span v-show="scope.row.soldinfo.length>0">{{scope.row.soldinfo[0].customer}}</span>
          <span v-show="scope.row.soldinfo.length==0">-</span>
        </template>
      </el-table-column>
      <el-table-column label="操作方式">
        <template slot-scope="scope">
          <span>{{ scope.row.code | process_doss_code }}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-divider></el-divider>
    <el-row>
      <div class="output">
        <el-button @click="dooutput" :loading="outputloading" icon="el-icon-download">导出明细</el-button>
      </div>
    </el-row>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="details.length"
      :hide-on-single-page="true"
      @current-change="cur_page"
    ></el-pagination>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datepicker";
import axios from "axios";
import { no_au_then, longdate, outputData } from "@/components/utils";
import sel_doss_code from "@/components/common/sel_doss_code";

export default {
  components: { dtpicker, sel_doss_code },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      outputloading: false,
      queryloading: false,
      info: {},
      details: [],
      nowpage: 1,
      infoperpage: 10, //每页最大记录数量
      report: "",
      report1: "",
      arr: ["交车上报", "虚报", "申请退车", "退车完成"]
    };
  },
  filters: {
    process_doss_code(d) {
      const arr = ["交车上报", "虚报", "申请退车", "退车完成"];
      return arr[d];
    }
  },
  mounted() {
    this.clearInfo();
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    tocontrol(d) {
      this.$emit("to_au", d);
    },
    process_doss_code(d) {
      return this.arr[d];
    },
    dooutput() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        outputData(this.output_format(), () => {
          this.outputloading = false;
        });
      });
    },
    output_format() {
      let fmt = {};

      fmt.fields = [
        "VIN",
        "车型",
        "价格",
        "配置",
        "颜色",
        "操作日期",
        "销售顾问",
        "客户姓名",
        "操作方式"
      ];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.vin;
        re[fmt.fields[1]] = d.typeinfo[0].stype;
        re[fmt.fields[2]] = d.typeinfo[0].price;
        re[fmt.fields[3]] = d.typeinfo[0].ltype;
        re[fmt.fields[4]] = d.carinfo[0].clr;
        re[fmt.fields[5]] = d.dt;
        re[fmt.fields[6]] =
          d.soldinfo.length > 0
            ? `${d.soldinfo[0].cate} ${d.cellerinfo[0].name}`
            : "-";
        re[fmt.fields[7]] =
          d.soldinfo.length > 0 ? `${d.soldinfo[0].customer} ` : "-";
        re[fmt.fields[8]] = this.arr[d.code];
        return re;
      });
      fmt.token = sessionStorage.getItem("token");
      return fmt;
    },
    cur_page(val) {
      this.nowpage = val;
    },
    change_dt1(d) {
      if (d) {
        this.info.dt1 = d;
      } else {
        this.info.dt1 = "";
      }
    },
    change_dt2(d) {
      if (d) {
        this.info.dt2 = d;
      } else {
        this.info.dt2 = "";
      }
    },
    change_code(d) {
      this.info.code = d;
    },
    clearInfo() {
      this.info = {
        dt1: new Date(),
        dt2: new Date(),
        code: ""
      };
    },
    createReport1(d1) {
      let map = new Map();
      let re = [],
        sum = 0;

      let name_set = new Set(); //车型 set
      d1.forEach(d => {
        name_set.add(d.typeinfo[0].stype);
      });

      let cars = [];
      name_set.forEach(d => {
        cars.push({ stype: d, c0: 0, c1: 0, c3: 0 }); //c0 上报 c1 虚报 c3 退车完成
      });

      d1.forEach(d => {
        cars.forEach(car => {
          if (d.typeinfo[0].stype == car.stype && d.code == 0) {
            car.c0 = car.c0 + 1;
          }
          if (d.typeinfo[0].stype == car.stype && d.code == 1) {
            car.c1 = car.c1 + 1;
          }
          if (d.typeinfo[0].stype == car.stype && d.code == 3) {
            car.c3 = car.c3 + 1;
          }
        });
      });

      cars.forEach(d => {
        d.str = `${d.stype}：${d.c0 > 0 ? d.c0 : ""}${
          d.c1 > 0 ? "+" + d.c1 : ""
        }${d.c3 > 0 ? "-" + d.c3 : ""}=${d.c0 + d.c1 - d.c3}`;
      });

      this.report1 = cars
        .map(d => {
          return d.str;
        })
        .join("，");
    },
    createReport(d1) {
      let report_map = new Map();
      d1.forEach(d => {
        report_map.get(d.code) == undefined
          ? report_map.set(d.code, 1)
          : report_map.set(d.code, report_map.get(d.code) + 1);
      });

      let report_str = [];
      let sum = 0;
      report_map.forEach((v, k) => {
        switch (k) {
          case 0:
            report_str.push(`交车上报：${v}`);
            sum = sum + v;
            break;
          case 1:
            report_str.push(`虚报：${v}`);
            sum = sum + v;
            break;
          case 2:
            report_str.push(`申请退车：${v}`);
            break;
          case 3:
            report_str.push(`退车完成：${v}`);
            sum = sum - v;
            break;
        }
      });
      this.report = report_str.join("，") + "，有效上报合计：" + sum;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.getDossReport,
        method: "post",
        data: { token: this.token, info: this.info }
      }).then(re => {
        this.queryloading = false;
        no_au_then(re, re => {
          let i = 1;
          this.details = re.data.data.map(d => {
            d.dt = longdate(d.dt);
            d.index = i;
            i++;
            return d;
          });

          this.cur_page(1);
          this.createReport(this.details);
          this.createReport1(this.details);
        });
      });
    }
  }
};
</script>

