<template>
  <div>
    <ul>
      <li>
        <div class="inputflex">
          <label>名称</label>
          <el-input v-model.lazy.trim="decoinfo.name"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>分类</label>
          <el-select v-model.lazy="decoinfo.cate" placeholder="请选择">
            <el-option
              v-for="item in cate_opt"
              :key="item.label"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>财务成本价</label>
          <el-input v-model.number.lazy.trim="decoinfo.cb"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>销售结算价</label>
          <el-input v-model.number.lazy.trim="decoinfo.js"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>短码</label>
          <el-input v-model.number.lazy.trim="decoinfo.scode"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>厂家代码</label>
          <el-input v-model.lazy.trim="decoinfo.classcode"></el-input>
        </div>
      </li>
      <li>
        <el-button type="primary" @click="doupdatadeco">确认修改</el-button>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      cate: "",
      cate_opt: [
        { id: "001", label: "-" },
        { id: "002", label: "外观" },
        { id: "003", label: "内饰" },
        { id: "004", label: "影音" }
      ],
      decoinfo: {
        js: 0,
        cb: 0,
        name: "",
        err_info: [],
        scode: "",
        classcode: "",
        _id: ""
      }
    };
  },
  methods: {
    ref_update(d) {
      axios({
        url: require("@/config").axios.decoinfo,
        method: "post",
        data: {
          deid: d,
          token:sessionStorage.getItem("token")
        }
      }).then(re => {
        //        alert(JSON.stringify(re.data.data));
        this.decoinfo = re.data.data;
      });
    },
    input_err() {
      this.err_info = []; //置空错误提示

      if (this.decoinfo.name.length == 0) this.err_info.push("名称为空");
      if (this.decoinfo.scode.length == 0) this.err_info.push("短码为空");
      if (!/\d+/.test(this.decoinfo.js)) this.err_info.push("结算价不是数值");
      if (!/\d+/.test(this.decoinfo.cb)) this.err_info.push("成本价不是数值");

      if (this.err_info.length == 0) {
        //错误提示为空
        return false;
      } else {
        return true;
      }
    },
    doupdatadeco() {
      if (!this.input_err()) {
        this.$confirm("更新装饰项目？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.updatedeco,
            method: "post",
            data: {
              id: this.decoinfo._id,
              info: {
                name: this.decoinfo.name,
                scode: this.decoinfo.scode,
                cb: this.decoinfo.cb,
                js: this.decoinfo.js,
                cate: this.decoinfo.cate,
                classcode: this.decoinfo.classcode
              },
              token:sessionStorage.getItem("token")
            }
          }).then(re => {
            this.$alert("更新成功", "提示", {
              confirmButtonText: "确定"
            }).then(() => {
              this.decoinfo.name = "";
              this.decoinfo.scode = "";
              this.decoinfo.cb = 0;
              this.decoinfo.js = 0;
              this.decoinfo.cate = "";
              this.decoinfo.classcode = "";
              this.decoinfo.decoid = "";
              this.$emit("updatedeco_close_c");
            });
          });
        });
        //检查输入无误
      } else {
        this.$message(this.err_info.join("，"));
      }
    }
  }
};
</script>

<style scoped>
div button {
  width: 100%;
}
div ul li {
  padding: 8px 0;
}
</style>