<template>
  <div>
    <el-row>
      <el-col span="4" offset="6">
        <sel_cate @val_change="change_cate" :data="cate"></sel_cate>
      </el-col>
      <el-col span="4">
        <dtpicker @val_change="change_dt1" :data="dt1"></dtpicker>
      </el-col>
      <el-col span="4">
        <dtpicker @val_change="change_dt2" :data="dt2"></dtpicker>
      </el-col>
      <el-col span="4">
        <el-button @click="getinfo" type="primary" plain>刷新</el-button>
      </el-col>
    </el-row>
    <el-divider>车辆销售统计图表</el-divider>
    <el-row>
      <el-col span="12">
        <h4>销售顾问</h4>
        <sold_by_celler :info="info"></sold_by_celler>
      </el-col>
      <el-col span="12">
        <h4>车型</h4>
        <sold_by_cartype :info="info"></sold_by_cartype>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <h4>交车日期</h4>
        <sold_by_solddt :info="info"></sold_by_solddt>
      </el-col>
      <el-col span="12">
        <h4>库龄</h4>
        <sold_by_age :info="info"></sold_by_age>
      </el-col>
    </el-row>
    <el-row>
      <el-col span=12>

      <h4>购买方式</h4>
      <sold_by_payway :info="info"></sold_by_payway>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import sel_cate from "@/components/common/sel_carsold_cate";
import sold_by_celler from "./sold_by_celler";
import sold_by_payway from "./sold_by_payway";
import sold_by_solddt from "./sold_by_solddt";
import sold_by_age from "./sold_by_age";
import sold_by_cartype from "./sold_by_cartype";
import axios from "axios";

import dtpicker from "@/components/common/datepicker";
import { no_au_then } from "@/components/utils";

export default {
  components: {
    sold_by_payway,
    sold_by_celler,
    sel_cate,
    dtpicker,
    sold_by_cartype,
    sold_by_solddt,
    sold_by_age
  },
  data() {
    return {
      dt1: new Date(),
      dt2: new Date(),
      cate: "展厅",
      token: sessionStorage.getItem("token"),
      info: []
    };
  },
  mounted() {
    this.getinfo();
  },
  methods: {
    change_dt1(d) {
      this.dt1 = d;
    },
    change_dt2(d) {
      this.dt2 = d;
    },
    change_cate(d) {
      this.cate = d;
    },
    getinfo() {
      if (this.cate == "") {
        this.$message("请选择分区");
        return;
      }
      axios({
        url: require("@/config").axios.g2_day_sold,
        method: "post",
        data: {
          token: this.token,
          info: { dt1: this.dt1, dt2: this.dt2, cate: this.cate }
        }
      }).then(re => {
        no_au_then(re, re => {
          this.info = re.data.data;
        });
      });
    }
  }
};
</script>

<style scoped>
h4 {
  color: #666;
  font-size: 15px;
}
</style>