<template>
  <div>
    <el-row>
      <el-col span="2">
        <sel_stype @val_change="change_stype"></sel_stype>
      </el-col>
      <el-col span="2">
        <sel_price :stype="info.stype" :data="info.price" @val_change="change_price"></sel_price>
      </el-col>
      <el-col span="4">
        <sel_ltype
          :stype="info.stype"
          :price="info.price"
          :data="info.ltype"
          @val_change="change_ltype"
        ></sel_ltype>
      </el-col>
      <el-col span="3">
        <el-button type="primary" icon="el-icon-search" :loading="queryloading" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details_in_page" border height="580px" style="width:100%">
      <el-table-column prop="index" width="50" label="序号"></el-table-column>
      <el-table-column width="200" label="VIN">
        <template slot-scope="scope">
          <el-link @click="tocontrol(scope.row)" 
            icon="el-icon-caret-right">{{scope.row.vin}}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="typeinfo[0].stype" width="80" label="车型"></el-table-column>
      <el-table-column prop="typeinfo[0].price" width="80" label="价格"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置"></el-table-column>
      <el-table-column prop="carinfo[0].clr" width="80" label="颜色"></el-table-column>
      <el-table-column prop="reportdt" label="退车申请"></el-table-column>
      <el-table-column prop="backdt" label="退车完成"></el-table-column>
      <el-table-column label="是否销售">
        <template slot-scope="scope">
          <span>{{scope.row | process_sold}}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-divider></el-divider>
    <el-row>
      <div class="output">
        <el-button @click="dooutput" :loading="outputloading" icon="el-icon-download">导出明细</el-button>
      </div>
    </el-row>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="details.length"
      :hide-on-single-page="true"
      @current-change="cur_page"
    ></el-pagination>
  </div>
</template>

<script>
import axios from "axios";
import sel_stype from "@/components/common/sel_carsold_mtype";
import sel_price from "@/components/common/sel_carsold_price";
import sel_ltype from "@/components/common/sel_carsold_ltype";
import {
  shortdate,
  longdate,
  no_au_then,
  outputData
} from "@/components/utils";

export default {
  components: { sel_stype, sel_price, sel_ltype },
  data() {
    return {
      queryloading: false,
      token: sessionStorage.getItem("token"),
      info: {},
      details: [],
      nowpage: 1,
      infoperpage: 10, //每页最大记录数量
      report: ""
    };
  },
  filters: {
    process_sold(d) {
      if (d.soldinfo.length > 0) {
        return `${longdate(d.soldinfo[0].solddt)} ${d.soldinfo[0].cate} ${
          d.cellerinfo[0].name
        }，客户：${d.soldinfo[0].customer}，${d.soldinfo[0].payway_2}`;
      } else {
        return "未销售";
      }
    }
  },
  mounted() {
    this.clearInfo();
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    process_sold(d) {
      if (d.soldinfo.length > 0) {
        return `${longdate(d.soldinfo[0].solddt)} ${d.soldinfo[0].cate} ${
          d.cellerinfo[0].name
        }，客户：${d.soldinfo[0].customer}，${d.soldinfo[0].payway_2}`;
      } else {
        return "未销售";
      }
    },
    dooutput() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        outputData(this.output_format(), () => {
          this.outputloading = false;
        });
      });
    },
    output_format() {
      let fmt = {};

      fmt.fields = [
        "VIN",
        "车型",
        "价格",
        "配置",
        "颜色",
        "退车申请",
        "退车完成",
        "销售情况"
      ];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.vin;
        re[fmt.fields[1]] = d.typeinfo[0].stype;
        re[fmt.fields[2]] = d.typeinfo[0].price;
        re[fmt.fields[3]] = d.typeinfo[0].ltype;
        re[fmt.fields[4]] = d.carinfo[0].clr;
        re[fmt.fields[5]] = d.reportdt;
        re[fmt.fields[6]] = d.backdt;
        re[fmt.fields[7]] = this.process_sold(d);
        return re;
      });
      fmt.token = sessionStorage.getItem("token");
      return fmt;
    },
    create_report(d) {
      let map = new Map();
      d.forEach(d => {
        map.get(d.typeinfo[0].stype) == undefined
          ? map.set(d.typeinfo[0].stype, 1)
          : map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1);
      });

      let re = [],
        sum = 0;

      map.forEach((v, k) => {
        sum = sum + v;
        re.push(`${k}：${v}`);
      });

      re.push("合计：" + sum);
      this.report = re.join("，");
    },
    cur_page(val) {
      this.nowpage = val;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.doss_back_list,
        method: "post",
        data: { token: this.token, info: this.info }
      }).then(re => {
        this.queryloading = false;
        no_au_then(re, re => {
          let i = 1;
          this.details = re.data.data.map(d => {
            d.reportdt = longdate(d.reportdt);
            if (d.backdt != "") {
              d.backdt = longdate(d.backdt);
            }
            d.index = i;
            i++;
            return d;
          });

          this.create_report(this.details);
        });

        this.cur_page(1);
      });
    },
    change_ltype(d) {
      this.info.ltype = d;
    },
    change_price(d) {
      this.info.price = d;
      this.info.ltype = "";
    },
    change_stype(d) {
      this.info.stype = d;
      this.info.price = "";
      this.info.ltype = "";
    },
    clearInfo() {
      this.info = {
        stype: "",
        price: "",
        ltype: ""
      };
    },
    tocontrol(d) {
      this.$emit("to_au", d);
    }
  }
};
</script>

<style scoped>
</style>