<template>
  <div>
    <el-upload
      class="upload-demo"
      :action="upload_action"
      :file-list="fileList"
      :on-success="success"
      :before-upload="beforeUpload"
      accept=".jpg"
    >
      <div class="el-upload__text">
        <em>点击上传.jpg文件</em>
      </div>
    </el-upload>
    {{uploaded_filename}}
  </div>
</template>


<script>
import axios from "axios";

export default {
  data() {
    return {
      upload_action: require("@/config").axios.img_up_path,
      fileList: [],
      uploaded_filename: ""
    };
  },
  methods: {
    beforeUpload(file) {
      let size_check = false;

      if (file.size > 1 * 1024 * 1024) {
        //文件大小限制1M
        this.$message("文件过大（超过1M）");
      } else {
        size_check = true;
      }

      return size_check;
    },
    success(resp, file, fileList) {
      this.uploaded_filename = resp.msg;
    }
  }
};
</script>