<template>
  <div>
    <el-row>
      <el-col span="5">
        <sel_vin @val_change="change_vin"></sel_vin>
      </el-col>
    </el-row>
    <el-row>
      <sincarinfo :data="carinfo"></sincarinfo>
    </el-row>
    <el-row>
      <el-col span="8">{{soldinfo}}</el-col>
      <el-col span="8">
        <span id="now_code">当前状态：{{now_code}}</span>
      </el-col>
      <el-col span="8">
        <el-link @click="gethistory" icon="el-icon-search">查看历史</el-link>
      </el-col>
    </el-row>
    <el-divider>变更状态</el-divider>
    <el-row>
      <el-col span="6" offset="5">
        <dtpicker @val_change="change_dt"></dtpicker>
      </el-col>
      <el-col span="4">
        <sel_doss_code @val_change="change_code" :data="info.code"></sel_doss_code>
      </el-col>
      <el-col span="4">
        <el-button @click="do_add_history" type="primary" icon="el-icon-edit">变更状态</el-button>
      </el-col>
    </el-row>
    <el-divider>
      <el-link icon="el-icon-caret-left" @click="todoss_list">【有效上报】</el-link>
      <el-link icon="el-icon-caret-left" @click="to_sold_doss_list">【已售未报】</el-link>
      <el-link icon="el-icon-caret-left" @click="to_back_doss_list">【退车未报】</el-link>
    </el-divider>
  </div>
</template>

<script>
import sel_vin from "@/components/common/vin_search/vin_all";
import axios from "axios";
import { no_au_then, longdate } from "@/components/utils";
import sincarinfo from "@/components/common/sincarinfo";
import sel_doss_code from "@/components/common/sel_doss_code";
import dtpicker from "@/components/common/datetimepicker";

export default {
  props: ["vin"],
  watch: {
    vin(nv, ov) {
      this.change_vin({ value: nv });
    }
  },
  components: { sel_vin, sincarinfo, sel_doss_code, dtpicker },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      info: {
        vin: "",
        code: "",
        dt: new Date()
      },
      now_code: "",
      now_code_id: "",
      soldinfo: "交车状态：",
      carinfo: { soldinfo: [], cellerinfo: [] },
      arr: ["交车上报", "虚报", "申请退车", "退车完成"]
    };
  },
  methods: {
    todoss_list() {
      this.$emit("todoss_list");
    },
    to_sold_doss_list() {
      this.$emit("to_sold_doss_list");
    },
    to_back_doss_list() {
      this.$emit("to_back_doss_list");
    },
    check_code() {
      let err = [];
      this.info.code == this.now_code_id
        ? err.push("与当前状态重复，无法变更")
        : "";

      this.info.code != 2 && this.now_code_id == 0
        ? err.push("逻辑错误，请选择申请退车")
        : "";

      this.info.code != 2 && this.now_code_id == 1
        ? err.push("逻辑错误，请选择申请退车")
        : "";

      this.carinfo.soldinfo.length > 0 &&
      this.now_code_id == -1 &&
      this.info.code != 0 //有销售记录，无上报记录，要求选择 “交车上报
        ? err.push("逻辑错误，请选择交车上报")
        : "";

      this.carinfo.soldinfo.length == 0 &&
      this.now_code_id == -1 &&
      this.info.code != 1 //无销售记录，无上报记录，要求选择 “虚报
        ? err.push("逻辑错误，请选择虚报")
        : "";

      this.info.code != 3 && this.now_code_id == 2
        ? err.push("逻辑错误，请选择退车完成")
        : "";

      this.carinfo.soldinfo.length > 0 &&
      this.info.code != 0 &&
      this.now_code_id == 3 //有销售记录，退车完成 ，要求选择交车上报
        ? err.push("逻辑错误，请选择交车上报")
        : "";

      this.carinfo.soldinfo.length == 0 &&
      this.info.code != 1 &&
      this.now_code_id == 3 //无销售记录，退车完成 ，要求选择虚报
        ? err.push("逻辑错误，请选择虚报")
        : "";

      return err;
      //有些code是带有因果的，比如1后面只能是2，如果用户选了3 或者4 就提示操作有误
    },
    do_add_history() {
      if (this.check_code().length > 0) {
        this.$message(this.check_code().join("；"));
        return;
      }

      if (this.info.vin == "" || this.info.code.length == 0) {
        this.$message("请选择车辆、变更后状态");
        return;
      }

      this.$confirm("确认操作?", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.add_doss_history,
          method: "post",
          data: { token: this.token, info: this.info }
        }).then(er => {
          this.$message("变更完成");
          this.info.code = "";
          this.change_vin({ value: this.info.vin });
        });
      });
    },
    change_dt(d) {
      this.info.dt = d;
    },
    change_code(d) {
      this.info.code = d;
    },
    gethistory() {
      if (this.info.vin == "") {
        this.$message("请先选择车辆");
        return;
      }

      axios({
        url: require("@/config").axios.sin_car_doss_history,
        method: "post",
        data: { token: this.token, info: { vin: this.info.vin } }
      }).then(re => {
        no_au_then(re, re => {
          const history = re.data.data.map(d => {
            let code = "";

            return `<tr><td>${longdate(d.dt)}</td><td>${
              this.arr[d.code]
            }</td></tr>`;
          });

          const content = `<table width='100%'>
        <tr><th>日期</td><th>操作</th></tr>
        ${history.join("")}
        </table>`;
          this.$alert(content, "详情", {
            dangerouslyUseHTMLString: true
          });
        });
      });
    },
    change_vin(d) {
      this.info.vin = d.value;
      axios({
        url: require("@/config").axios.sincarinfo,
        method: "post",
        data: { token: this.token, info: { vin: this.info.vin } }
      }).then(re => {
        no_au_then(re, re => {
          this.carinfo = re.data.data[0];

          if (this.carinfo.soldinfo.length > 0) {
            const sold = this.carinfo.soldinfo[0];
            const celler = this.carinfo.cellerinfo[0];
            this.soldinfo = `销售顾问：${sold.cate}-${celler.name}，客户：${sold.customer}，${sold.payway_1}`;
          } else {
            this.soldinfo = "未销售";
          }
        });
      });

      axios({
        url: require("@/config").axios.sin_car_doss_code,
        method: "post",
        data: { token: this.token, info: { vin: this.info.vin } }
      }).then(re => {
        no_au_then(re, re => {
          this.now_code_id = re.data.data.code;
          if (this.now_code_id > -1) {
            this.now_code = this.arr[this.now_code_id];
          } else {
            this.now_code = "未上报";
          }
        });
      });
    }
  }
};
</script>

<style scoped>
.returnlist {
  display: inline-block;
  color: #555;
  padding: 5px 10px;
}
</style>