import jwt from "jsonwebtoken";
import axios from "axios";

//授权过期之后的统一操作
const no_au_then = (re, cb) => {
  if (re.data.code == 403) {
    this.$message("授权过期，请重新登录");
    return;
  } else {
    cb(re)
  }
}

//等待
const wait = sec => {
  return new Promise((res, rej) => {
    setTimeout(() => {
      res();
    }, sec);
  });
};

//转为短日期
const shortdate = dtstr => {
  if (dtstr == "-") {
    return "-";
  } else {
    const dt = new Date(dtstr);
    return `${dt.getFullYear()}-${
      dt.getMonth() + 1 < 10 ? "0" + (dt.getMonth() + 1) : dt.getMonth() + 1
      }-${dt.getDate() < 10 ? "0" + dt.getDate() : dt.getDate()}`;
  }
};

//转为长日期
const longdate = dtstr => {
  if (dtstr == "-") {
    return "-";
  } else {
    const dt = new Date(dtstr);
    return `${shortdate(dtstr)} ${
      dt.getHours() < 10 ? "0" + dt.getHours() : dt.getHours()
      }:${dt.getMinutes() < 10 ? "0" + dt.getMinutes() : dt.getMinutes()}`;
  }
};

//计算两个日期相差天数
const days = (dt1str, dt2str) => {
  let dateStart = new Date(dt1str);
  let dateEnd = new Date(dt2str);
  return (dateEnd - dateStart) / (1000 * 60 * 60 * 24);
};

//颜色样式映射
const clr_map = clr => {
  switch (clr) {
    case "闪电白":
      return "shandianbai";
    case "珍珠黑":
      return "zhenzhuhei";
    case "墨玉黑":
      return "moyuhei";
    case "风尚红":
      return "fengshanghong";
    case "晶灰":
      return "jinghui";
    case "陨铁灰":
      return "yuntiehui";
    case "玄铁灰":
      return "xuantiehui";
    case "亚麻金":
      return "yamajin";
    case "星夜蓝":
      return "xingyelan";
    case "黯影灰":
      return "anyinghui";
    case "月光银":
      return "yueguangyin";
    default:
      return "";
  }
};

//判断权限ID是否在当前用户权限下
const au_pass = async au_id => {
  const payload = await jwt.verify(
    sessionStorage.getItem("token"),
    require("@/config").secret
  );

  if (payload.au.includes(au_id)) {
    return true;
  } else {
    return false;
  }
};

//导出
const outputData = (details, rec) => {
  let data = details;
  data.token = sessionStorage.getItem("token");

  axios({
    url: require("@/config").axios.output,
    method: "post",
    data: data
  }).then(re => {
    window.open(require("@/config").domain + re.data.filename);
    rec()
  });
}

module.exports = { wait, shortdate, longdate, days, clr_map, au_pass, outputData, no_au_then };
