<template>
  <div>
    <el-row gutter="10">
      <el-col span="2">
        <sel_carinway_stype @val_change="change_stype" ref="stype_ref"></sel_carinway_stype>
      </el-col>
      <el-col span="4">
        <sel_carinway_ltype :stype="now_stype" ref="ltype_ref"></sel_carinway_ltype>
      </el-col>
      <el-col span="2">
        <sel_carinway_clr ref="clr_ref"></sel_carinway_clr>
      </el-col>
      <el-col span="2">
        <sel_carinway_state ref="state_ref"></sel_carinway_state>
      </el-col>
      <el-col span="2">
        <el-button type="primary" icon="el-icon-search" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details_in_page" border height="580px" style="width: 100%">
      <el-table-column prop="index" label="序号" width="50"></el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="车型" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置" width="450"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="价格" width="80"></el-table-column>
      <el-table-column prop="vin" label="车架号" width="200"></el-table-column>
      <el-table-column prop="clr" label="颜色" width="70"></el-table-column>
      <el-table-column prop="way_state" label="状态" width="200"></el-table-column>
    </el-table>

    <el-divider>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="details.length"
        :hide-on-single-page="true"
        @current-change="cur_page"
      ></el-pagination>
    </el-divider>

    <div class="output">
      <el-button type="primary" plain @click="output" icon="el-icon-download">导出明细</el-button>
    </div>
  </div>
</template>

<script>
import sel_carinway_stype from "@/components/common/sel_carinway_stype";
import sel_carinway_ltype from "@/components/common/sel_carinway_ltype";
import sel_carinway_clr from "@/components/common/sel_carinway_clr";
import sel_carinway_state from "@/components/common/sel_carinway_state";
import axios from "axios";

export default {
  components: {
    sel_carinway_stype,
    sel_carinway_ltype,
    sel_carinway_clr,
    sel_carinway_state
  },
  data() {
    return {
      report: "",
      now_stype: "",
      nowpage: 1, //当前页数序号
      infoperpage: 10, //每页最大记录数量
      token: sessionStorage.getItem("token"),
      details: []
    };
  },
  computed: {
    output_fmt() {
      let fmt = {};

      fmt.fields = ["车型", "配置", "价格", "车架号", "颜色", "状态"];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.typeinfo[0].stype;
        re[fmt.fields[1]] = d.typeinfo[0].ltype;
        re[fmt.fields[2]] = d.typeinfo[0].price;
        re[fmt.fields[3]] = d.vin;
        re[fmt.fields[4]] = d.clr;
        re[fmt.fields[5]] = d.way_state;

        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    },
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    createReport(data) {
      let map = new Map(),
        re = [],
        sum = 0;

      data.forEach(d => {
        map.get(d.typeinfo[0].stype) == undefined
          ? map.set(d.typeinfo[0].stype, 1)
          : map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1);
      });

      map.forEach((v, k) => {
        sum = sum + v;
        re.push(`${k}：${v}`);
      });

      re.push(`合计：${sum}`);

      this.report = re.join("，");
    },
    output() {
      if (this.output_fmt.json.length == 0) {
        this.$message("没有数据");
      } else {
        this.$confirm("确认导出明细？", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then(re => {
          require("@/components/utils").outputData(this.output_fmt);
        });
      }
    },
    change_stype(d) {
      this.now_stype = d;
    },
    cur_page(val) {
      this.nowpage = val;
    },
    doquery() {
      axios({
        url: require("@/config").axios.carinwaylist,
        method: "post",
        data: {
          token: this.token,
          data: {
            stype: this.$refs.stype_ref.val,
            ltype: this.$refs.ltype_ref.val,
            clr: this.$refs.clr_ref.val,
            state: this.$refs.state_ref.val
          }
        }
      }).then(re => {
        let i = 1;
        this.details = re.data.map(d => {
          d.index = i;
          i++;
          return d;
        });

        this.createReport(this.details);
      });
    }
  }
};
</script>

