<template>
  <div>
    <el-select v-model="user" placeholder="请选择" @change="sel_change" clearable>
      <el-option v-for="item in users" :key="item.id" :label="item.name" :value="item.id"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return { users: [], user: "", val: {} };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      axios({
        url: require("@/config").axios.usermanlist,
        method: "post",
        data: { token: sessionStorage.getItem("token") }
      }).then(re => {
        this.users = re.data.map(d => {
          return { id: d.uid, name: d.name, rights: d.rights };
        });
      });
    },
    sel_change(val) {
      this.val = val;
    }
  }
};
</script>