<template>
  <div>
    <el-row>
      <el-col span="3">
        <el-button icon="el-icon-plus" type="success" @click="toadd">重置</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="4">
        <stype @val_change="change_stype" ref="stype_ref" :data="order.stype"></stype>
      </el-col>
      <el-col span="4">
        <price :stype="order.stype" @val_change="change_price" ref="price_ref" :data="order.price"></price>
      </el-col>
      <el-col span="12">
        <ltype
          ref="ltype_ref"
          :stype="order.stype"
          :price="order.price"
          @val_change="change_ltype"
          :data="order.ltype"
        ></ltype>
      </el-col>
      <el-col span="4">
        <clr ref="clr_ref" :stype="order.stype" @val_change="change_clr" :data="order.clr"></clr>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>定车日期</label>
          <datetimepicker ref="odt_ref" @val_change="odt_change" :data="order.odt"></datetimepicker>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>分区</label>
          <cate @val_change="change_cate" ref="cate_ref" :data="order.cate"></cate>
          <celler
            ref="celler_ref"
            :cate="order.cate"
            @val_change="change_celler"
            :data="order.celler_id"
          ></celler>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-input v-model="order.tips" placeholder="备注信息"></el-input>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row v-if="isAdd">
      <el-col span="5" offset="19">
        <el-button-group>
          <el-button v-if="isAdd" type="primary" @click="doaddorder" icon="el-icon-document-add">新增</el-button>
          <el-button type="danger" @click="backlist" icon="el-icon-caret-left">返回</el-button>
        </el-button-group>
      </el-col>
    </el-row>
    <el-row v-if="!isAdd">
      <el-col span="10" offset="14">
        <el-button-group>
          <el-button type="success" @click="doupdateorder" icon="el-icon-edit">更新</el-button>
          <el-button type="warning" @click="dodelorder" icon="el-icon-delete">删除</el-button>
          <el-button type="danger" @click="backlist" icon="el-icon-caret-left">返回</el-button>
        </el-button-group>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import stype from "@/components/common/sel_nowstore_stype";
import ltype from "@/components/common/sel_nowstore_ltype";
import price from "@/components/common/sel_nowstore_price";
import clr from "@/components/common/sel_nowstore_clr";
import datetimepicker from "@/components/common/datetimepicker";
import cate from "@/components/common/sel_carsold_cate";
import celler from "@/components/common/sel_celler";
import axios from "axios";

export default {
  props: ["car_order_id", "vin"], //可通过传入订单ID来查找订单的详细内容； 可传入车架号来补全对应的车型颜色选框
  components: {
    stype,
    ltype,
    price,
    clr,
    datetimepicker,
    cate,
    celler
  },
  watch: {
    vin(nv, ov) {
      if (nv != "") {
        this.clearinfo();
        axios({
          url: require("@/config").axios.typeinfo_by_vin,
          method: "post",
          data: { token: this.token, info: { vin: nv } }
        }).then(re => {
          const info = re.data.data;
          this.order.stype = info.typeinfo[0].stype;
          this.order.price = info.typeinfo[0].price;
          this.order.ltype = info.typeinfo[0].ltype;
          this.order.clr = info.clr;
        });
      }
    },
    car_order_id(nv, ov) {
      if (nv == "" || nv == undefined) {
        this.isAdd = true;
      } else {
        this.isAdd = false;
        this.getinfo(nv);
      }
    }
  },
  data() {
    return {
      order: {},
      isAdd: true,
      token: sessionStorage.getItem("token")
    };
  },
  mounted() {
    this.clearinfo();
  },
  methods: {
    getinfo(order_id) {
      axios({
        url: require("@/config").axios.getfastorderinfo,
        method: "post",
        data: { token: this.token, info: { car_order_id: order_id } }
      }).then(re => {
        require("@/components/utils").no_au_then(re, re => {
          this.order = re.data.data;
        });
      });
    },
    backlist() {
      this.$emit("tolist");
    },
    odt_change(d) {
      this.order.odt = d;
    },
    toadd() {
      this.isAdd = true;
      this.car_order_id = "";
      this.clearinfo();
    },
    tolist() {
      this.$emit("tolist");
    },
    clearinfo() {
      this.order = {
        stype: "",
        price: "",
        ltype: "",
        clr: "",
        odt: new Date(),
        cate: "",
        celler: "",
        celler_id: "",
        tips: ""
      };
    },
    input_ok() {
      let err = [];
      this.$refs.stype_ref.val.length == 0 ? err.push("请选择车型") : "";
      this.$refs.price_ref.val.length == 0 ? err.push("请选择价格") : "";
      this.$refs.ltype_ref.val.length == 0 ? err.push("请选择配置") : "";
      this.$refs.clr_ref.val.length == 0 ? err.push("请选择颜色") : "";
      this.$refs.cate_ref.val.length == 0 ? err.push("请选择分区") : "";
      this.$refs.celler_ref.celler == 0 ? err.push("请选择销售顾问") : "";
      return err;
    },
    doaddorder() {
      if (this.input_ok().length > 0) {
        this.$message(this.input_ok().join("，"));
      } else {
        this.$confirm("确认操作？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.addcarorderfast,
            method: "post",
            data: { token: this.token, info: this.order }
          }).then(re => {
            require("../utils").no_au_then(re, re => {
              if (re.data.code == 200) {
                //                this.car_order_id = re.data.car_order_id;
                this.$message("新增成功");
                this.clearinfo();
              } else {
                this.$message("订单新增失败");
              }
            });
          });
        });
      }
    },
    dodelorder() {
      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(re => {
        axios({
          url: require("@/config").axios.delcarorder,
          method: "post",
          data: { token: this.token, info: { car_order_id: this.car_order_id } }
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("删除完成");
            this.clearinfo();
            this.fitinfo();
          } else {
            this.$message("订单删除失败");
          }
        });
      });
    },
    doupdateorder() {
      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.updatecarorder,
          method: "post",
          data: {
            token: this.token,
            info: { order: this.order, car_order_id: this.car_order_id }
          }
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("更新成功");
          } else {
            this.$message("因某些问题，订单新增失败");
          }
        });
      });
    },
    change_cate(d) {
      this.order.cate = d;
    },
    pw1() {
      return this.$refs.payway1_ref.val;
    },
    change_celler(d) {
      this.order.celler_id = d;
    },
    change_clr(d) {
      this.order.clr = d;
    },
    change_payway1(d) {
      this.order.payway1 = d;
    },
    change_ltype(d) {
      this.order.ltype = d;
    },
    change_stype(d) {
      this.order.stype = d;
      if (d == "") {
        this.order.price = "";
        this.order.ltype = "";
        this.order.clr = "";
      }
    },
    change_price(d) {
      this.order.price = d;
    }
  }
};
</script>

