<template>
  <div>
    <el-button type="primary" :loading="loading" @click="dologin">准备登录</el-button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      loading: false,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    dologin() {
      this.$emit("clear_chapcha");
      this.loading = true;
      axios({
        url: require("@/config").axios.ppt_launch,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        this.loading = false;
        this.$emit("get_chapta", re.data.data.img);
      });
    }
  }
};
</script>

