<template>
  <div>
    <el-row gutter="10">
      <el-col span="3">
        <datepicker @val_change="change_dt1"></datepicker>
      </el-col>
      <el-col span="3">
        <datepicker @val_change="change_dt2"></datepicker>
      </el-col>
      <el-col span="2">
        <car_cate @val_change="cate_change"></car_cate>
      </el-col>
      <el-col span="2">
        <myceller @val_change="celler_change" :cate="info.cate" :data="info.celler_id"></myceller>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="2">
        <car_mtype @val_change="change_stype"></car_mtype>
      </el-col>
      <el-col span="2">
        <car_price @val_change="change_price" :stype="info.stype"></car_price>
      </el-col>
      <el-col span="2">
        <el-input v-model="info.vin" placeholder="车架号"></el-input>
      </el-col>
      <el-col span="2">
        <el-input v-model="info.customer" placeholder="客户姓名"></el-input>
      </el-col>
      <el-col span="2">
        <el-input v-model="info.tel" placeholder="联系方式"></el-input>
      </el-col>
      <el-col span="2">
        <el-button type="primary" @click="doquery" :loading="queryloading" icon="el-icon-search">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details_in_page" border height="550px" style="width: 100%">
      <el-table-column prop="index" width="50" label="序号"></el-table-column>
      <el-table-column prop="cellerinfo[0].name" label="销售顾问" width="100"></el-table-column>
      <el-table-column prop="solddt" label="购车日期" width="150"></el-table-column>
      <el-table-column prop="customer" label="客户姓名" width="100"></el-table-column>
      <el-table-column prop="idcode" label="身份证号" width="180"></el-table-column>
      <el-table-column prop="tel" label="电话" width="120"></el-table-column>
      <el-table-column prop="address" label="地址" width="200"></el-table-column>
      <el-table-column prop="payway_1" label="支付方式1" width="100"></el-table-column>
      <el-table-column prop="payway_2" label="支付方式2" width="100"></el-table-column>
      <el-table-column label="车架号" width="200">
        <template slot-scope="scope">
          <el-link @click="tocontrol(scope.row)" icon="el-icon-caret-right">{{scope.row.vin}}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="主要车型" width="90"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="详细车型" width="350"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="市场报价" width="80"></el-table-column>
      <el-table-column prop="price_car" label="成交价" width="80"></el-table-column>
      <el-table-column prop="price_deco" label="装饰款" width="70"></el-table-column>
      <el-table-column prop="price_anjie" label="按揭款" width="70"></el-table-column>
      <el-table-column label="装饰" width="80" class="decostype">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="showdecos(scope.row)"
            v-show="scope.row.decos.length>0"
          >{{scope.row.decos.length}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="outputstyle">
      <el-link icon="el-icon-download" @click="output">导出明细</el-link>
    </div>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="details.length"
      :hide-on-single-page="true"
      @current-change="cur_page"
    ></el-pagination>
  </div>
</template>

<script>
import datepicker from "@/components/common/datepicker";
import myceller from "@/components/common/sel_celler";
import car_cate from "@/components/common/sel_carsold_cate";
import car_mtype from "@/components/common/sel_carsold_mtype";
import car_ltype from "@/components/common/sel_carsold_ltype";
import car_price from "@/components/common/sel_carsold_price";
import car_clr from "@/components/common/sel_carsold_clr";
import car_payway1 from "@/components/common/sel_carsold_payway1";
import car_payway2 from "@/components/common/sel_carsold_payway2";
import { outputData } from "@/components/utils";

import axios from "axios";

export default {
  components: {
    datepicker,
    myceller,
    car_cate,
    car_mtype,
    car_ltype,
    car_price,
    car_clr,
    car_payway1,
    car_payway2
  },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      info: {},
      report: "",
      tb_i: 1,
      queryloading: false, //查询等待反馈
      nowpage: 1, //当前页数序号
      infoperpage: 10, //每页最大记录数量
      details: [], //总明细
      car_decos: [], //装饰明细项目
      now_cate: "", //当前选择分区
      now_stype: "", //当前选择主车型
      now_payway1: "", //支付方式
      vin: "", //车架号
      customer: "", //用户姓名
      tel: "", //电话
      address: "", //地址
      payway1: "", //支付方式
      celler_id: "", //
      payway2: "" //详细支付方式
    };
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    },
    output_format() {
      let fmt = {};

      fmt.fields = [
        "销售顾问",
        "购车日期",
        "客户姓名",
        "身份证号",
        "电话",
        "地址",
        "支付方式1",
        "支付方式2",
        "车架号",
        "主要车型",
        "详细车型",
        "市场报价",
        "成交价",
        "装饰款",
        "按揭款",
        "装饰明细"
      ];

      fmt.json = this.details.map(d => {
        let decos = [];
        if (d.decos.length > 0) {
          decos = d.decos.map(d => {
            return `${d.name}:${d.js}:0:${d.count}`;
          });
        }

        let re = {};
        re[fmt.fields[0]] = d.cellerinfo[0].name;
        re[fmt.fields[1]] = d.solddt;
        re[fmt.fields[2]] = d.customer;
        re[fmt.fields[3]] = "'" + d.idcode.replace("'", "");
        re[fmt.fields[4]] = d.tel;
        re[fmt.fields[5]] = d.address;
        re[fmt.fields[6]] = d.payway_1;
        re[fmt.fields[7]] = d.payway_2;
        re[fmt.fields[8]] = d.vin;
        re[fmt.fields[9]] = d.typeinfo[0].stype;
        re[fmt.fields[10]] = d.typeinfo[0].ltype;
        re[fmt.fields[11]] = d.typeinfo[0].price;
        re[fmt.fields[12]] = d.price_car;
        re[fmt.fields[13]] = d.price_deco;
        re[fmt.fields[14]] = d.price_anjie;
        re[fmt.fields[15]] = decos.join("，");
        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    }
  },
  mounted() {
    this.clearInfo();
  },
  methods: {
    clearInfo() {
      this.info = {
        dt1: new Date(),
        dt2: new Date(),
        cate: "",
        vin: "",
        celler_id: "",
        stype: "",
        price: "",
        vin: "",
        customer: "",
        tel: "",
        payway1: "",
        payway2: "",
        clr: "", //未使用
        ltype: "" //未使用
      };
    },
    createReport(d) {
      let map = new Map();
      let re = [],
        sum = 0;

      d.forEach(d => {
        map.get(d.typeinfo[0].stype) == undefined
          ? map.set(d.typeinfo[0].stype, 1)
          : map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1);
      });

      map.forEach((v, k) => {
        sum = sum + v;
        re.push(`${k}：${v}`);
      });

      re.push(`合计：${sum}`);

      this.report = re.join("，");
    },
    tocontrol(d) {
      this.$emit("tocontrol_c", d);
    },
    celler_change(d) {
      this.info.celler_id = d;
    },
    showdecos(d) {
      const decoArr = d.decos.map(d => {
        return `<tr><td>${d.name}</td><td>${d.count}</td></tr>`;
      });

      if (decoArr.length == 0) {
        this.$message("没有装饰项目");
      } else {
        const content = `<table width='100%'>
        <tr><th>项目</td><th>数量</th></tr>
        ${decoArr.join("")}
        </table>`;
        this.$alert(content, "详情", {
          dangerouslyUseHTMLString: true
        });
      }
    },
    payway1_change(d) {
      this.info.payway_1 = d;
    },
    change_stype(d) {
      //切换主车型
      this.info.stype = d;
      this.info.price = "";
    },
    change_price(d) {
      this.info.price = d;
    },
    change_ltype(d) {
      this.info.ltype = d;
    },
    change_dt1(d) {
      this.info.dt1 = d;
    },
    change_dt2(d) {
      this.info.dt2 = d;
    },
    cate_change(d) {
      //切换分区
      this.info.cate = d;
      this.info.celler_id = "";
    },
    output() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        outputData(this.output_format);
      });
    },
    cur_page(val) {
      this.nowpage = val;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.soldlist,
        method: "post",
        data: { token: this.token, info: this.info }
      }).then(re => {
        this.queryloading = false;
        if (re.data.code == 401) {
          this.details = [];
        } else {
          let i = 1;
          this.details = re.data.data.map(d => {
            d.index = i;
            i++;
            return d;
          });
          this.createReport(this.details);
          this.cur_page(1);
        }
      });
    }
  }
};
</script>

<style  scoped>
.decostype {
  font-size: 0.8rem !important;
}

.link {
  line-height: 35px;
}

.optionsdiv ul {
  padding: 20px;
  width: 100%;
}

.optionsdiv ul li {
  padding: 8px 0;
}
</style>