<template>
  <div>
    <el-row>
      <el-col span="4">
        <el-button v-if="isAdd" type="success" icon="el-icon-plus" @click="doAdd">新建项目</el-button>
      </el-col>
    </el-row>
    <div v-if="!isAdd">
      <el-row gutter="10">
        <el-col span="16">
          <el-select
            v-model="select_deco"
            filterable
            remote
            reserve-keyword
            placeholder="请输入关键词"
            :remote-method="remoteMethod"
            :loading="queryloading"
            @change="select_item"
          >
            <el-option v-for="item in decolist" :key="item._id" :label="item.name" :value="item"></el-option>
          </el-select>
        </el-col>
        <el-col span="8">
          <el-input-number
            v-model="deco_num"
            @change="handleChange"
            :min="1"
            :max="10"
            label="描述文字"
          ></el-input-number>
        </el-col>
      </el-row>
      <el-table :data="decos" @selection-change="handleSelectionChange" ref="multipleTable">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="name" label="名称" width="180"></el-table-column>
        <el-table-column prop="count" label="数量" width="50"></el-table-column>
        <el-table-column fixed="right" label="操作" width="50">
          <template slot-scope="scope">
            <el-button @click="remove_deco(scope.row.name)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-divider></el-divider>
      <el-row>
        <el-col span="12" offset="12">
          <el-button-group>
            <el-button type="primary" @click="doupdate" icon="el-icon-edit">更新</el-button>
            <el-button type="info" icon="el-icon-refresh" @click="toggle">选中</el-button>
          </el-button-group>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      needAdd: false,
      deco_num: 1,
      select_deco: {},
      queryloading: false,
      isAdd: true,
      deco_id: 0,
      decos: [],
      sel_decos: [],
      deco_keys: "",
      decolist: [],
      deco_order_id: 0,
      order_id: 0,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    toggle() {
      this.decos.forEach(d => {
        this.$refs.multipleTable.toggleRowSelection(d);
      });
    },
    handleSelectionChange(d) {
      this.sel_decos = d;
    },
    updateData(order_id) {
      this.order_id = order_id;
      axios({
        url: require("@/config").axios.orderinfo,
        method: "post",
        data: { car_order_id: this.order_id, token: this.token }
      }).then(re => {
        if (re.data.data.deco_order_id == 0) {
          //切换显示样式
          this.isAdd = true;
          this.decos = [];
        } else {
          this.deco_order_id = re.data.data.deco_order_id;
          this.isAdd = false;
          this.needAdd = false;
          this.decos = re.data.data.decoinfo;
        }
      });
    },
    doupdate() {
      this.$confirm("确认更新装饰项目？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        let data = { data: this.decos };
        data.needAdd = this.needAdd;
        if (this.needAdd == true) {
          data.deco_order_id = this.deco_id;
        } else {
          data.deco_order_id = this.deco_order_id;
        }
        data.order_id = this.order_id;
        data.token = this.token;
        axios({
          url: require("@/config").axios.orderupdatedeco,
          method: "post",
          data: data
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("更新完成");
            this.needAdd = false; //重置 需要新增 开关，否则多点几次就会增加几次项目
          } else {
            this.$message("因为某些原因，更新失败");
          }
        });
      });
    },
    remove_deco(d) {
      this.decos.forEach((item, i) => {
        if (item.name == d) {
          this.decos.splice(i, 1);
        }
      });
    },
    select_item() {
      let dupData = false;
      this.decos.forEach((item, i) => {
        if (item.name == this.select_deco.name) {
          //找到重复的数据
          dupData = true;
        }
      });

      if (dupData) {
        this.$message("重复项目");
      } else {
        this.select_deco.count = this.deco_num;
        this.decos.push(this.select_deco);
      }
    },
    remoteMethod(keys) {
      if (keys.length != "") {
        // 带有查询条件
        this.queryloading = true;
        axios({
          url: require("@/config").axios.decolist,
          method: "post",
          data: {
            querystr: keys,
            onlyactive: true,
            token: this.token
          }
        }).then(re => {
          this.decolist = re.data.data;
          this.queryloading = false;
        });
      } else {
        this.decolist = [];
      }
    },
    doAdd() {
      this.deco_id = Date.now(); //生成项目ID
      this.isAdd = false; //切换显示状态
      this.needAdd = true; //操作状态，true，添加数据，false 更新数据
    }
  }
};
</script>

