<template>
  <div>
    <el-row>
      <el-col span="3">
        <dtpicker @val_change="change_dt1"></dtpicker>
      </el-col>
      <el-col span="3">
        <dtpicker @val_change="change_dt2"></dtpicker>
      </el-col>
      <el-col span="3">
        <el-button type="primary" :loading="queryloading" icon="el-icon-search" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details" border height="550px" style="width: 100%">
      <el-table-column prop="index" label="序号" width="50"></el-table-column>
      <el-table-column prop="vin" label="VIN" width="200"></el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="车型" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="价格" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置"></el-table-column>
      <el-table-column prop="clr" label="颜色" width="100"></el-table-column>
      <el-table-column prop="arrdt" label="调入日期" width="100"></el-table-column>
      <el-table-column prop="source" label="调入公司" width="200"></el-table-column>
      <el-table-column label="销售">
        <template slot-scope="scope">{{scope.row | process_sold}}</template>
      </el-table-column>
    </el-table>
    <el-divider></el-divider>
    <el-row>
      <el-col span="3" offset="21">
        <el-button icon="el-icon-download" :loading="outputloading" @click="dooutput">导出明细</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datepicker";
import axios from "axios";
import { shortdate, no_au_then, outputData } from "@/components/utils";

export default {
  components: { dtpicker },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      report: "",
      queryloading: false, //查询等待反馈
      outputloading: false, //导出表格等待反馈
      details: [], //总明细
      info: {}
    };
  },
  mounted() {
    this.clearInfo();
  },
  filters: {
    process_sold(d) {
      let re = "未销售";
      if (d.soldinfo.length > 0) {
        re = `${shortdate(d.soldinfo[0].solddt)} ${d.soldinfo[0].cate} 已售`;
      }
      return re;
    }
  },
  methods: {
    process_sold(d) {
      let re = "未销售";
      if (d.soldinfo.length > 0) {
        re = `${shortdate(d.soldinfo[0].solddt)} ${d.soldinfo[0].cate} 已售`;
      }
      return re;
    },
    output_format() {
      let fmt = {};

      fmt.fields = [
        "VIN",
        "车型",
        "价格",
        "配置",
        "颜色",
        "调入日期",
        "调入公司",
        "销售情况"
      ];

      fmt.json = this.details.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.vin;
        re[fmt.fields[1]] = d.typeinfo[0].stype;
        re[fmt.fields[2]] = d.typeinfo[0].price;
        re[fmt.fields[3]] = d.typeinfo[0].ltype;
        re[fmt.fields[4]] = d.clr;
        re[fmt.fields[5]] = d.arrdt;
        re[fmt.fields[6]] = d.source;
        re[fmt.fields[7]] = this.process_sold(d);
        return re;
      });
      fmt.token = sessionStorage.getItem("token");
      return fmt;
    },
    dooutput() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        outputData(this.output_format(), () => {
          this.outputloading = false;
        });
      });
    },
    createReport(d) {
      let map = new Map();
      d.forEach(d => {
        map.get(d.typeinfo[0].stype) == undefined
          ? map.set(d.typeinfo[0].stype, 1)
          : map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1);
      });

      let re = [],
        sum = 0;
      map.forEach((v, k) => {
        sum = sum + v;
        re.push(`${k}：${v}`);
      });

      re.push(`合计：${sum}`);

      this.report = re.join("，");
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.moveInList,
        method: "post",
        data: { token: this.token, info: this.info }
      }).then(re => {
        this.queryloading = false;
        no_au_then(re, re => {
          let i = 1;
          let data = re.data.data.map(d => {
            d.arrdt = shortdate(d.arrdt);
            d.index = i;
            i++;
            return d;
          });
          this.details = data;
          this.createReport(this.details);
        });
      });
    },
    change_dt1(d) {
      this.info.dt1 = d;
    },
    change_dt2(d) {
      this.info.dt2 = d;
    },
    clearInfo() {
      this.info = {
        dt1: new Date(),
        dt2: new Date()
      };
    }
  }
};
</script>

<style scoped>
</style>