<template>
  <div>
    <el-select v-model="val" placeholder="配置" clearable @change="val_change">
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["stype", "price", "data"],
  data() {
    return { vals: [], val: "" };
  },
  watch: {
    data(nv, ov) {
      this.val = nv;
    },
    stype(nv, ov) {
      nv != "" && this.price != "" ? this.getData() : (this.this.vals = []);
    },
    price(nv, ov) {
      nv != "" && this.stype != "" ? this.getData() : (this.this.vals = []);
    }
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.val);
    },
    getData() {
      axios({
        url: require("@/config").axios.ltypelist_all,
        method: "post",
        data: {
          stype: this.stype,
          price: this.price,
          token: sessionStorage.getItem("token")
        }
      }).then(re => {
        this.vals = re.data.data.map(d => {
          return { val: d };
        });
      });
    }
  }
};
</script>