<template>
  <div class="frame">
    <div id="by_payway"></div>
    {{g2info}}
  </div>
</template>

<script>
import G2 from "@antv/g2";

export default {
  props: ["info"],
  watch: {
    info(nv, ov) {
      this.data_process(nv);
    }
  },
  data() {
    return {
      info: [],
      g2info: [],
      chart: {}
    };
  },
  mounted() {
    this.char_init();
  },
  methods: {
    char_init() {
      this.chart = new G2.Chart({
        container: "by_payway", // 指定图表容器 ID
        forceFit: true,
        height: 300 // 指定图表高度
      });

      this.chart
        .intervalStack()
        .position("x*y")
        .color("x");
      // Step 4: 渲染图表
    },
    data_process(info) {
      let map = new Map();
      info.soldlist.forEach(d => {
        const key = d.payway_1;
        map.get(key) == undefined
          ? map.set(key, 1)
          : map.set(key, map.get(key) + 1);
      });

      let re = [],
        sum = 0;
      map.forEach((v, k) => {
        sum = sum + v;
        re.push({ x: k, y: v });
      });

      re = re.map(d => {
        return { x: d.x, y: Math.round((d.y * 100) / sum) / 100 };
      });
      this.g2info = re;
      this.chart.source(this.g2info);
      this.chart.render();
    }
  }
};
</script>

<style scoped>
.frame {
  padding: 10px;
  border-bottom: 1px solid #dde;
}
</style>