<template>
  <div>
    <el-row>
      <el-col span="8" offset="8">
        <el-input v-model="password"></el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8" offset="8">
        <el-divider></el-divider>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8" offset="8">
        <el-button type="primary" @click="doupdatapwd">确认修改</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: ["curr_user"],
  data() {
    return {
      password: "",
      input_is_err: false
    };
  },
  methods: {
    input_ok() {
      this.input_is_err = false;
      if (this.password.length < 6) {
        this.$message("密码至少6位");
        this.input_is_err = true;
      } else {
        if (/[^\w\.\/]/gi.test(this.password)) {
          //正则为 查找非 字母 . /
          this.$message("密码不可包含汉字");
          this.input_is_err = true;
        }
      }
    },
    doupdatapwd() {
      this.input_ok();
      if (!this.input_is_err) {
        this.$confirm("即将修改密码，请牢记新密码后点击确定。", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          axios({
            url: require("@/config").axios.updatepwd,
            method: "post",
            data: {
              uid: this.curr_user.uid,
              pwd: this.password,
              token: sessionStorage.getItem("token")
            }
          }).then(re => {
            this.$alert("密码已修改，将跳转到首页。", "修改成功", {
              confirmButtonText: "确定",
              callback: () => {
                this.$router.push("/");
              }
            });
          });
        });
      }
    }
  }
};
</script>

<style scoped>
div button {
  width: 100%;
}

div ul li {
  padding: 8px 0;
}
</style>