<template>
  <div>
    <el-row gutter=20>
      <el-col span="6">
        <el-input v-model.trim="querystr" @keyup.enter.native="doquery" placeholder="请输入查询条件"></el-input>
      </el-col>
      <el-col span="1">
        <el-checkbox v-model="isactive">只看启用中项目</el-checkbox>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="decolistpage" border height="620px" style="width: 100%">
      <el-table-column fixed prop="createdt" label="创建日期" width="150"></el-table-column>
      <el-table-column prop="name" label="名称" width="300"></el-table-column>
      <el-table-column prop="cb" label="成本价" width="80"></el-table-column>
      <el-table-column prop="js" label="结算价" width="80"></el-table-column>
      <el-table-column prop="cate" label="分类" width="80"></el-table-column>
      <el-table-column prop="classcode" label="厂家代码" width="200"></el-table-column>
      <el-table-column prop="scode" label="短码" width="120"></el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button @click="updatedecoshow(scope.row)" type="text" circle icon="el-icon-edit-outline"></el-button>
          <el-button type="text" @click="mute(scope.row._id)" circle icon="el-icon-close"></el-button>
          <el-button type="text" @click="active(scope.row._id)" circle icon="el-icon-check"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-divider>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="decolist.length"
        :hide-on-single-page="true"
        @current-change="cur_page"
      ></el-pagination>
    </el-divider>
    <el-drawer title="更新装饰参数" :visible.sync="drawer" :direction="direction">
      <el-row>
        <el-col span="20" offset="2">
          <updatedeco ref="updatedeco_ref" @updatedeco_close_c="updatedeco_close_p"></updatedeco>
        </el-col>
      </el-row>
    </el-drawer>
  </div>
</template>

<script>
import axios from "axios";
import updatedeco from "@/components/deco/updatedeco";

export default {
  components: { updatedeco },
  data() {
    return {
      nowpage: 1,
      infoperpage: 10,
      drawer: false,
      direction: "ltr",
      isactive: true,
      decolist: [],
      querystr: ""
    };
  },
  computed: {
    decolistpage() {
      if (this.decolist.length > 0) {
        return this.decolist.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    }
  },
  methods: {
    cur_page(val) {
      this.nowpage = val;
    },
    updatedeco_close_p() {
      this.drawer = false;
    },
    active(id) {
      this.$confirm("启用这个项目？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.decoactive,
          method: "post",
          data: { id: id ,token:sessionStorage.getItem("token")}
        }).then(() => {
          this.$message("操作完成");
        });
      });
    },
    mute(id) {
      this.$confirm("禁用这个项目？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.decomute,
          method: "post",
          data: { id: id,token:sessionStorage.getItem("token") }
        }).then(() => {
          this.$message("操作完成");
        });
      });
    },
    async updatedecoshow(data) {
      this.drawer = true;
      this.$refs.updatedeco_ref.ref_update(data._id);
    },
    doquery() {
      axios({
        url: require("@/config").axios.decolist,
        method: "post",
        data: { querystr: this.querystr, onlyactive: this.isactive,token:sessionStorage.getItem("token") }
      }).then(re => {
        re.data.data.map((d, i) => {
          d.createdt = require("@/components/utils").longdate(d.createdt);
        });
        this.decolist = re.data.data;
      });
    }
  }
};
</script>

<style scoped>
.el-checkbox {
  line-height: 40px !important;
}
</style>