<template>
  <div>
    <el-divider>线上可CALL车</el-divider>
    <el-row>
      <el-col span="12">
        <down_can_call></down_can_call>
      </el-col>
      <el-col span="12">
        <csv_upload @on_uploaded="uploaded_can_call"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>下周可CALL车</el-divider>
    <el-row>
      <el-col span="12">
        <down_will_call></down_will_call>
      </el-col>
      <el-col span="12">
        <csv_upload @on_uploaded="uploaded_will_call"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>更新奖金数据：Bonus</el-divider>
    <el-row>
      <el-col span="12" offset="12">
        <csv_upload @on_uploaded="uploaded_bonus"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>更新备用金数据：Reserve</el-divider>
    <el-row>
      <el-col span="12" offset="12">
        <csv_upload @on_uploaded="uploaded_reserve"></csv_upload>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="6">
        <ready_login @get_chapta="get_chapta" @clear_chapcha="clear_chapcha"></ready_login>
      </el-col>
      <el-col span="6">
        <img :src="img_url" />
      </el-col>
      <el-col span="6">
        <el-input v-model="chaptcha" type="primary" maxlength="4"></el-input>
      </el-col>
      <el-col span="6">
        <do_login :data="chaptcha" @login_ok="change_login_ok"></do_login>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8">
        <ppt_inway></ppt_inway>
      </el-col>
      <el-col span="8">
        <ppt_can_call></ppt_can_call>
      </el-col>
      <el-col span="8">
        <ppt_will_call></ppt_will_call>
      </el-col>
    </el-row>
    <el-row>
      <ppt_close></ppt_close>
    </el-row>
    {{login_info}}
    {{inway_info}}
  </div>
</template>

<script>
import ppt_will_call from "@/components/pptr/will_call";
import ppt_can_call from "@/components/pptr/can_call";
import ppt_close from "@/components/pptr/close";
import ppt_inway from "@/components/pptr/in_way";
import ready_login from "@/components/pptr/login";
import do_login from "@/components/pptr/login2";
import down_can_call from "@/components/common/template/down_can_call";
import down_will_call from "@/components/common/template/down_will_call";
import csv_upload from "@/components/common/uploader_csv";
import axios from "axios";
import { no_au_then } from "@/components/utils";

export default {
  data() {
    return {
      login_ok: false,
      img_url: "",
      chaptcha: "",
      login_info: "",
      inway_info: [],
      token: sessionStorage.getItem("token")
    };
  },
  components: {
    ppt_will_call,
    ppt_can_call,
    ppt_close,
    down_can_call,
    down_will_call,
    csv_upload,
    ready_login,
    do_login,
    ppt_inway
  },
  methods: {
    change_login_ok() {
      this.login_ok = true;
    },
    in_way_info(d) {
      alert(JSON.stringify(d));

      axios;
    },
    clear_chapcha() {
      this.img_url = "";
    },
    get_chapta(d) {
      this.img_url = require("@/config").domain + d;
    },
    // ppt_car_inway() {
    //   axios({
    //     url: require("@/config").axios.ppt_car_inway,
    //     method: "post",
    //     data: { token: this.token }
    //   }).then(re => {
    //     no_au_then(re, re => {
    //       alert(JSON.stringify(re.data.data));
    //     });
    //   });
    // },
    // ppt_close() {
    //   axios({
    //     url: require("@/config").axios.ppt_close,
    //     method: "post",
    //     data: { token: this.token }
    //   }).then(re => {
    //     no_au_then(re, re => {
    //       this.$message("已关闭");
    //     });
    //   });
    // },
    ppt_can_call() {
      axios({
        url: require("@/config").axios.ppt_car_can_call,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        alert(re.data.data);
        //        re.data.data = `<html><body><form><button value="testbutton" /></form></body></html>`;
        const doc = new DOMParser().parseFromString(
          `<html>${re.data.data}</html>`,
          "text/html"
        );
        alert(doc.querySelector("form:nth-child(1)").innerHTML);
        // alert(
        //   $(re.data.data)
        //     .$("form[name=FRM2]")
        //     .html()
        // );
      });
    },
    // ppt_launch() {
    //   axios({
    //     url: require("@/config").axios.ppt_launch,
    //     method: "post",
    //     data: { token: this.token }
    //   }).then(re => {
    //     this.img_url = require("@/config").domain + re.data.data.img;
    //   });
    // },
    // ppt_login() {
    //   axios({
    //     url: require("@/config").axios.ppt_login,
    //     method: "post",
    //     data: { token: this.token, info: { chaptcha: this.chaptcha } }
    //   }).then(re => {
    //     no_au_then(re, re => {
    //       re.data.code == 200
    //         ? (this.login_info = "登陆成功")
    //         : (this.login_info = re.data.msg.name);
    //     });
    //   });
    // },
    uploaded_reserve(d) {
      this.$confirm("上传完成。开始更新 备用金 数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.import_reserve,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("更新完成");
        });
      });
    },
    uploaded_bonus(d) {
      this.$confirm("上传完成。开始更新 奖金 数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.import_bonus,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("更新完成");
        });
      });
    },
    uploaded_can_call(d) {
      this.$confirm("上传完成。开始更新 可CALL车辆 数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.import_cancall,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("更新完成");
        });
      });
    },
    uploaded_will_call(d) {
      this.$confirm("上传完成。开始更新 将CALL车辆 数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.import_willcall,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("更新完成");
        });
      });
    }
  }
};
</script>

