<template>
  <div>
    <el-select v-model="val" placeholder="状态" clearable @change="val_change">
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["data"],
  watch: {
    data(nv, ov) {
      this.val = nv;
    }
  },
  data() {
    return {
      vals: [{ val: "有效订单" }, { val: "无效订单" }],
      val: "有效订单"
    };
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.val);
    }
  }
};
</script>