<template>
  <div>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>车架号</label>
          <vin_inway @on_val_change="vin_change" ref="vin_ref"></vin_inway>
        </div>
      </el-col>
      <el-col span="8">
        <div class="inputflex">
          <label>配置</label>
          <el-input v-model="car.typeinfo[0].ltype"></el-input>
        </div>
      </el-col>
      <el-col span="4">
        <el-button @click="fitdata" plain type="primary">数据匹配</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>车型</label>
          <el-input v-model="car.typeinfo[0].stype"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>颜色</label>
          <clr ref="clr_ref" :stype="car.typeinfo[0].stype" @val_change="clr_change"></clr>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>价格</label>
          <el-input v-model="car.typeinfo[0].price"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>内部代码</label>
          <el-input v-model="car.typeinfo[0].typecode_pri"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>厂家代码</label>
          <el-input v-model="car.typeinfo[0].typecode_pub"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>发动机号</label>
          <el-input v-model="car.fdjcode"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>生产日期</label>
          <dtpicker @val_change="birdt_change" ref="birdt_ref"></dtpicker>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>入库日期</label>
          <dtpicker @val_change="indt_change" ref="indt_ref"></dtpicker>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>入库位置</label>
          <posi1 @val_change="posi1_change"></posi1>
          <posi2 :posi1="car.now_posi1" @val_change="posi2_change"></posi2>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>车辆来源</label>
          <car_source @val_change="source_change"></car_source>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>合格证编号</label>
          <el-link>{{car.hgz}}</el-link>
        </div>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <el-col span="4" offset="20">
        <el-button type="primary" @click="docarin">确认入库</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import vin_inway from "@/components/common/vin_search/vin_inway";
import dtpicker from "@/components/common/datepicker";
import posi1 from "@/components/common/sel_nowstore_posi1";
import posi2 from "@/components/common/sel_nowstore_posi2";
import car_source from "@/components/common/sel_carsource";
import clr from "@/components/common/sel_carsold_clr";
import axios from "axios";

export default {
  components: { vin_inway, dtpicker, posi1, posi2, car_source, clr },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      car: {
        source: "",
        now_posi1: "",
        now_posi2: "",
        hgz: 0,
        vin: "",
        fdjcode: "",
        clr: "",
        birdt: new Date(),
        indt: new Date(),
        typeinfo: [
          {
            type_id: "",
            ltype: "",
            price: 0,
            stype: "",
            typecode_pri: "",
            typecode_pub: ""
          }
        ]
      }
    };
  },
  methods: {
    clear_car() {
      //入库完成，清空对象 没有处理 来源 跟入库位置，方便快速下台入库
      this.car = Object.assign(this.car, {
        hgz: 0,
        vin: "",
        fdjcode: "",
        clr: "",
        birdt: new Date(),
        indt: new Date(),
        typeinfo: [
          {
            type_id: "",
            ltype: "",
            price: 0,
            stype: "",
            typecode_pri: "",
            typecode_pub: ""
          }
        ]
      });

      this.$refs.vin_ref.vin = "";
      this.$refs.birdt_ref.value1 = new Date();
      this.$refs.indt_ref.value1 = new Date();
    },
    birdt_change(d) {
      this.car.birdt = d;
    },
    indt_change(d) {
      this.car.indt = d;
    },
    source_change(d) {
      this.car.source = d;
    },
    clr_change(d) {
      this.car.clr = d;
    },
    queryhgzindex() {
      //查询合格证编号
      axios({
        url: require("@/config").axios.hgz_getnowindex,
        method: "post",
        data: {
          token: this.token,
          info: { stype: this.car.typeinfo[0].stype.trim() }
        }
      }).then(re => {
        if (re.data.code == 201) {
          this.$message("无车型记录，可能为新车型，编号为1");
          this.car.hgz = 1;
        } else {
          this.car.hgz = re.data.now_index;
        }
      });
    },
    input_ok() {
      let err = [];
      this.$refs.vin_ref.vin.trim().length == 0 ? err.push("需要车架号") : "";
      this.car.typeinfo[0].ltype.trim().length == 0
        ? err.push("需要车辆配置")
        : "";
      this.car.typeinfo[0].stype.trim().length == 0 ? err.push("需要车型") : "";
      this.car.clr.length == 0 ? err.push("需要颜色") : "";
      this.car.typeinfo[0].price == 0 ? err.push("需要价格") : "";
      this.car.typeinfo[0].typecode_pri.trim().length == 0
        ? err.push("需要内部代码")
        : "";
      this.car.typeinfo[0].typecode_pub.trim().length == 0
        ? err.push("需要厂家代码")
        : "";
      this.car.fdjcode.length == 0 ? err.push("需要发动机号") : "";
      this.car.birdt == null ? err.push("需要生产日期") : "";
      this.car.indt == null ? err.push("需要入库日期") : "";

      this.car.now_posi1 == "" ? err.push("需要入库位置1") : "";
      this.car.now_posi2 == "" ? err.push("需要入库位置2") : "";
      this.car.source == "" ? err.push("需要车辆来源") : "";

      return err;
    },
    docarin() {
      if (this.input_ok().length > 0) {
        this.$message(this.input_ok().join("，"));
        return;
      } else {
        if (this.car.typeinfo[0].type_id == "") {
          //没有车型ID则禁止入库，新车型应该是在下周将CALL里创建，入库时候的车型处于下游
          this.$alert("请先点击'数据匹配' 一致化车型参数");
          return;
        }

        this.$confirm("确认入库？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          this.car.vin = this.$refs.vin_ref.vin;
          this.car.birdt = this.$refs.birdt_ref.value1;
          this.car.indt = this.$refs.indt_ref.value1;

          axios({
            url: require("@/config").axios.carinway_carin,
            method: "post",
            data: {
              token: this.token,
              info: this.car
            }
          }).then(re => {
            this.clear_car();
            this.$message("操作完成");
          });
        });
      }
    },
    fitdata() {
      //通过车辆配置匹配其他的参数，主要用于手工入库的时候避免输入错误提供帮助
      if (this.car.typeinfo[0].ltype == "") {
        this.$message("车型配置为空，无法匹配");
        return;
      }

      axios({
        url: require("@/config").axios.fitdatabyltype,
        method: "post",
        data: { token: this.token, info: { ltype: this.car.typeinfo[0].ltype } }
      }).then(re => {
        const data = re.data;
        if (data.code == 200) {
          const typeinfo = data.data[0];
          this.car.typeinfo[0].price = typeinfo.price;
          this.car.typeinfo[0].stype = typeinfo.stype;
          this.car.typeinfo[0].typecode_pri = typeinfo.typecode_pri;
          this.car.typeinfo[0].typecode_pub = typeinfo.typecode_pub;
          this.car.typeinfo[0].type_id = typeinfo.type_id;
          this.$message("Done");
          this.queryhgzindex();
        } else {
          this.$message("没有匹配到数据");
          this.hgz = 0;
        }
      });
    },
    posi1_change(d) {
      this.car.now_posi1 = d;
    },
    posi2_change(d) {
      this.car.now_posi2 = d;
    },
    vin_change(d) {
      axios({
        url: require("@/config").axios.carinway_sin_car_info,
        method: "post",
        data: { token: this.token, info: { vin: d.value } }
      }).then(re => {
        this.car = Object.assign(this.car, re.data.data[0]); //合并对象，避免覆盖掉一些必要的属性
        this.$refs.clr_ref.val = re.data.data[0].clr;
        this.queryhgzindex();
      });
    }
  }
};
</script>

