<template>
  <div>
    <el-row>
      <el-col span="3">
        <el-button icon="el-icon-plus" type="success" @click="toadd">重置</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="4">
        <stype @val_change="change_stype" :data="order.stype"></stype>
      </el-col>
      <el-col span="4">
        <price :stype="order.stype" @val_change="change_price" :data="order.price"></price>
      </el-col>
      <el-col span="12">
        <ltype
          :stype="order.stype"
          :price="order.price"
          :data="order.ltype"
          @val_change="change_ltype"
        ></ltype>
      </el-col>
      <el-col span="4">
        <clr :stype="order.stype" :data="order.clr" @val_change="change_clr"></clr>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8">
        <div class="inputflex">
          <label>客户姓名</label>
          <el-input v-model.lazy.trim="order.name"></el-input>
        </div>
      </el-col>
      <el-col span="16">
        <div class="inputflex">
          <label>身份证号</label>
          <el-input v-model.lazy.trim="order.idcode" maxlength="18"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8">
        <div class="inputflex">
          <label>电话号码</label>
          <el-input v-model.lazy.trim="order.tel" maxlength="18"></el-input>
        </div>
      </el-col>
      <el-col span="16">
        <div class="inputflex">
          <label>地址</label>
          <el-input v-model.lazy.trim="order.address"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8">
        <div class="inputflex">
          <label>定车日期</label>
          <datetimepicker ref="odt_ref" :data="order.odt" @val_change="change_odt"></datetimepicker>
        </div>
      </el-col>
      <el-col span="8">
        <div class="inputflex">
          <label>定金</label>
          <el-input placeholder="定金" v-model.number="order.opay"></el-input>
        </div>
      </el-col>
      <el-col span="8">
        <div class="inputflex">
          <label>车款</label>
          <el-input placeholder="车款" v-model.number="order.carpay"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>支付方式</label>
          <payway1 @val_change="change_payway1" :data="order.payway1"></payway1>
          <payway2 @val_change="change_payway2" :data="order.payway2" :payway1="order.payway1"></payway2>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>按揭款</label>
          <el-input placeholder="按揭款" v-model.number="order.ajpay"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>装饰款</label>
          <el-input placeholder="装饰款" v-model.number="order.zspay"></el-input>
        </div>
      </el-col>
    </el-row>

    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>分区</label>
          <cate @val_change="change_nowcate" :data="order.cate"></cate>
          <celler :cate="order.cate" :data="order.celler_id" @val_change="change_celler"></celler>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>状态</label>
          <state ref="state_ref" :data="order.state" @val_change="change_state"></state>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-input v-model="order.tips" placeholder="备注信息"></el-input>
    </el-row>
    <el-divider></el-divider>
    <el-row v-show="isAdd">
      <el-col span="5" offset="19">
        <el-button-group>
          <el-button v-if="isAdd" type="primary" @click="doadd" icon="el-icon-document-add">新增</el-button>
          <el-button type="danger" @click="tolist" icon="el-icon-caret-left">返回</el-button>
        </el-button-group>
      </el-col>
    </el-row>
    <el-row v-show="!isAdd">
      <el-col span="10" offset="14">
        <el-button-group>
          <el-button type="success" @click="doupdate" icon="el-icon-edit">更新</el-button>
          <!--
          <el-button type="warning" @click="dodel" icon="el-icon-delete">删除</el-button>
          -->
          <el-button type="info" @click="add_deco" icon="el-icon-edit">装饰</el-button>
          <el-button type="danger" @click="tolist" icon="el-icon-caret-left">返回</el-button>
        </el-button-group>
      </el-col>
    </el-row>
    <el-drawer
      title="更新装饰数据"
      :visible.sync="deco_drawer"
      direction="rtl"
      @opened="deco_drawer_opened"
    >
      <el-row>
        <el-col span="20" offset="2">
          <deco_au ref="deco_au_ref"></deco_au>
        </el-col>
      </el-row>
    </el-drawer>
  </div>
</template>

<script>
import stype from "@/components/common/sel_nowstore_stype";
import ltype from "@/components/common/sel_nowstore_ltype";
import price from "@/components/common/sel_nowstore_price";
import clr from "@/components/common/sel_nowstore_clr";
import datetimepicker from "@/components/common/datetimepicker";
import payway1 from "@/components/common/sel_carsold_payway1";
import payway2 from "@/components/common/sel_carsold_payway2";
import cate from "@/components/common/sel_carsold_cate";
import celler from "@/components/common/sel_celler";
import state from "@/components/common/sel_order_state";
import deco_au from "@/components/common/deco_au";
import axios from "axios";
import { no_au_then } from "@/components/utils";

export default {
  props: ["vin", "car_order_id"],
  components: {
    stype,
    ltype,
    price,
    clr,
    datetimepicker,
    payway1,
    payway2,
    cate,
    celler,
    state,
    deco_au
  },
  data() {
    return {
      deco_drawer: false,
      needDeco: false,
      isAdd: true,
      order: {},
      token: sessionStorage.getItem("token")
    };
  },
  mounted() {
    this.clearinfo();
  },
  watch: {
    car_order_id(nv, ov) {
      //监听是否传入订单号
      if (nv != "" && nv != undefined) {
        this.isAdd = false;
        axios({
          url: require("@/config").axios.orderinfo_by_order_id,
          method: "post",
          data: { token: this.token, info: { car_order_id: nv } }
        }).then(re => {
          this.order = re.data.data;
        });
      } else {
        this.isAdd = true;
        this.clearinfo();
      }
    },
    vin(nv, ov) {
      //监听是否传入车架号
      if (nv != "" && nv != undefined) {
        this.isAdd = true;
        this.clearinfo();
        axios({
          url: require("@/config").axios.typeinfo_by_vin,
          method: "post",
          data: { token: this.token, info: { vin: nv } }
        }).then(re => {
          const info = re.data.data;
          this.order.stype = info.typeinfo[0].stype;
          this.order.price = info.typeinfo[0].price;
          this.order.ltype = info.typeinfo[0].ltype;
          this.order.clr = info.clr;
        });
      }
    }
  },
  methods: {
    toadd() {
      this.car_order_id = "";
      this.isAdd = true;
      this.needDeco = false;
      this.clearinfo();
    },
    tolist() {
      this.$emit("tolist");
    },
    deco_drawer_opened() {
      this.$refs.deco_au_ref.updateData(this.car_order_id);
    },
    clearinfo() {
      this.order = {
        stype: "",
        price: "",
        ltype: "",
        clr: "",
        zspay: 0,
        ajpay: 0,
        carpay: 0,
        odt: new Date(),
        name: "",
        idcode: "",
        address: "",
        opay: 0,
        carpay: 0,
        payway1: "",
        payway2: "",
        cate: "",
        tips: "",
        celler_id: "",
        state: "有效订单"
      };
    },
    input_ok() {
      let err = [];
      this.order.stype == "" ? err.push("请选择车型") : "";
      this.order.price == "" ? err.push("请选择价格") : "";
      this.order.ltype == "" ? err.push("请选择配置") : "";
      this.order.clr == "" ? err.push("请选择颜色") : "";
      this.order.name == "" ? err.push("请填写订单名称") : "";
      this.order.payway1 == "" ? err.push("请选择支付方式") : "";
      this.order.payway2 == "" ? err.push("请选择方式关联") : "";
      this.order.cate == "" ? err.push("请选择分区") : "";
      this.order.celler_id == "" ? err.push("请选择销售顾问") : "";
      this.order.state == "" ? err.push("请选择状态") : "";
      return err;
    },
    doadd() {
      if (this.input_ok().length > 0) {
        this.$message(this.input_ok().join("，"));
      } else {
        this.$confirm("确认操作？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.addcarorder,
            method: "post",
            data: { token: this.token, info: this.order }
          }).then(re => {
            no_au_then(re, re => {
              if (re.data.code == 200) {
                this.isAdd = false;
                this.car_order_id = re.data.car_order_id;
                this.$message("新增成功");
              } else {
                this.$message("订单新增失败");
              }
            });
          });
        });
      }
    },
    dodel() {
      if (this.car_order_id == "") {
        this.$message("请先选择订单");
        return;
      }

      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(re => {
        axios({
          url: require("@/config").axios.delcarorder,
          method: "post",
          data: { token: this.token, info: { car_order_id: this.car_order_id } }
        }).then(re => {
          no_au_then(re, re => {
            if (re.data.code == 200) {
              this.$message("删除完成");
              this.car_order_id = "";
            } else {
              this.$message("订单删除失败");
            }
          });
        });
      });
    },
    doupdate() {
      if (this.car_order_id == "") {
        this.$message("请先选择订单");
        return;
      }

      if (this.input_ok().length > 0) {
        this.$message(this.input_ok().join("，"));
        return;
      }

      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.updatecarorder,
          method: "post",
          data: {
            token: this.token,
            info: { order: this.order, car_order_id: this.car_order_id }
          }
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("更新成功");
          } else {
            this.$message("因某些问题，订单新增失败");
          }
        });
      });
    },
    add_deco() {
      if (this.car_order_id == "") {
        this.$message("请先选择订单");
        return;
      }
      this.deco_drawer = true;
    },
    change_nowcate(d) {
      this.order.cate = d;
    },
    change_payway1(d) {
      this.order.payway1 = d;
    },
    change_payway2(d) {
      this.order.payway2 = d;
    },
    change_stype(d) {
      this.order.stype = d;
      if (d == "") {
        this.order.price = "";
        this.order.ltype = "";
        this.order.clr = "";
      }
    },
    change_price(d) {
      this.order.price = d;
    },
    change_ltype(d) {
      this.order.ltype = d;
    },
    change_clr(d) {
      this.order.clr = d;
    },
    change_celler(d) {
      this.order.celler_id = d;
    },
    change_state(d) {
      this.order.state = d;
    },
    change_odt(d) {
      this.order.odt = d;
    }
  }
};
</script>

