<template>
  <div>
    <el-menu
      :default-active="activeIndex"
      mode="horizontal"
      @select="handleSelect"
      id="header"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-submenu index="user">
        <template slot="title">{{current.name}}</template>
        <el-menu-item index="updatepwd">修改密码</el-menu-item>
        <el-menu-item index @click="quit">退出系统</el-menu-item>
      </el-submenu>
      <el-submenu v-for="(cate,cate_i) in headerView" :key="cate_i" :index="cate_i">
        <template slot="title">{{cate.name}}</template>
        <el-menu-item v-for="item in cate.items" :index="item.val" :key="item.id">{{item.text}}</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
import jwt from "jsonwebtoken";
import axios from "axios";

export default {
  data() {
    return {
      current: {}, //当前用户
      activeIndex: "1",
      headerView: [] //头部权限对象
    };
  },
  async created() {
    try {
      if (!sessionStorage.getItem("token")) {
        this.$router.push("/"); //token过期，返回e到首页
      } else {
        this.current = jwt.verify(
          sessionStorage.getItem("token"),
          require("../../config").secret
        );

        axios({
          url: require("@/config").axios.headerView,
          method: "post",
          data: { uid: this.current.uid,token:sessionStorage.getItem("token") }
        }).then(re => {
          this.headerView = re.data;
        });
      }
    } catch (err) {
      this.$router.push("/"); //token过期，返回e到首页
    }
  },
  methods: {
    quit() {
      sessionStorage.removeItem("token");
      this.$router.push("/");
    },
    handleSelect(key, user) {
      const d = { key: key, curr_user: this.current };
      this.$emit("shift_mainer_com", d);
    }
  }
};
</script>

<style scoped>
div .el-link--inner {
  font-size: 2rem;
  color: #fff !important;
}
</style>