<template>
  <div>
    <ul>
      <li>
        <span class="lb">主车型</span>
        <span class="content">{{info.typeinfo[0].stype}}</span>
      </li>
      <li>
        <span class="lb">价格</span>
        <span class="content">￥{{info.typeinfo[0].price}}</span>
      </li>
      <li>
        <span class="lb">配置</span>
        <span class="content">{{info.typeinfo[0].ltype}}</span>
      </li>
      <li>
        <span class="lb">颜色</span>
        <span class="content">{{info.carinfo[0].clr}}</span>
      </li>
      <li>
        <span class="lb">车架号</span>
        <span class="content">{{info.vin}}</span>
      </li>
      <li>
        <span class="lb">生产</span>
        <span class="content">{{info.carinfo[0].pdt}}</span>
      </li>
      <li>
        <span class="lb">入库</span>
        <span class="content">{{info.carinfo[0].arrdt}}</span>
      </li>
      <li>
        <span class="lb">调拨</span>
        <span class="content">
          {{info.movedt}}
          <carhistory :vin="info.vin"></carhistory>
        </span>
      </li>
      <li>
        <span class="lb">点款</span>
        <span class="content">{{info.carinfo[0].payall |dol_fmt}}</span>
      </li>
      <li>
        <span class="lb">上报</span>
        <span class="content">{{info.carinfo[0].dolreport | dol_fmt}}</span>
      </li>
      <li>
        <span class="lb">位置</span>
        <span class="content">{{info.posi1}}-{{info.posi2}}</span>
      </li>
      <li>
        <span class="lb">发动机号</span>
        <span class="content">{{info.carinfo[0].fdjcode}}</span>
      </li>
      <li>
        <span class="lb">来源</span>
        <span class="content">{{info.carinfo[0].source}} 合格证序号：{{info.carinfo[0].hgz}}</span>
      </li>
      <li>
        <span class="lb">厂家型号</span>
        <span class="content">{{info.typeinfo[0].typecode_pub}}</span>
      </li>
      <li>
        <span class="lb">内部型号</span>
        <span class="content">{{info.typeinfo[0].typecode_pri}}</span>
      </li>
      <li>
        <span class="lb">钥匙</span>
        <span class="content">{{info.key_posi | key_posi_fmt}}</span>
      </li>
      <li>
        <span class="lb">指定订单</span>
        <span class="content">{{info | linkorder_fmt}}</span>
      </li>
      <li>
        <span class="lb">库龄</span>
        <span class="content">{{info.age}}</span>
      </li>
    </ul>
    <el-row>
      <el-col span="3" offset="15">
        <cate @val_change="change_cate"></cate>
      </el-col>
      <el-col span="3">
        <celler :cate="data.cate" ref="celler_ref"></celler>
      </el-col>
      <el-col span="3">
        <el-button @click="print_car_out" icon="el-icon-printer" type="success" plain>出库单</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import utils from "@/components/utils";
import carhistory from "@/components/common/getCarHistory";
import cate from "@/components/common/sel_carsold_cate";
import celler from "@/components/common/sel_celler name";
import axios from "axios";

export default {
  components: { carhistory, celler, cate },
  data() {
    return {
      info: {},
      data: { cate: "" },
      token: sessionStorage.getItem("token")
    };
  },
  filters: {
    linkorder_fmt(val) {
      if (val.linkinfo.length == 0) {
        return "-";
      } else {
        return val.linkorderinfo;
      }
    },
    key_posi_fmt(val) {
      if (val == "") {
        return "-";
      } else {
        return val;
      }
    },
    dol_fmt(val) {
      if (val == "") {
        return "-";
      } else {
        return utils.longdate(val);
      }
    }
  },
  methods: {
    change_cate(d) {
      this.data.cate = d;
    },
    print_car_out() {
      if (this.$refs.celler_ref.celler == "") {
        this.$message("请选择销售顾问");
        return;
      }

      this.$confirm("打印出库单会同时变更钥匙位置，要继续吗？").then(() => {
        axios({
          url: require("@/config").axios.keymove_name,
          method: "post",
          data: {
            token: this.token,
            info: {
              celler: this.$refs.celler_ref.celler,
              vin: this.info.vin,
              remenber: "打印出库单"
            },
            vin: this.info.vin
          }
        }).then(re => {
          let storage = {
            info: {
              vin: this.info.vin,
              price: this.info.typeinfo[0].price,
              stype: this.info.typeinfo[0].stype,
              clr: this.info.carinfo[0].clr,
              staff: this.$refs.celler_ref.celler
            }
          };

          localStorage.setItem("car_out", JSON.stringify(storage));
          window.open("/print/car_out.html", "", "", "");
        });
      });
    }
  }
};
</script>

<style scoped>
div {
  text-align: left;
}
ul li {
  display: inline-block;
  border: 10px solid rgba(0, 0, 0, 0);
  width: 50%;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.lb {
  border-right: 1px solid #aaa;
  display: inline-block;
  width: 60px;
  color: #888;
  text-align: right;
  padding-right: 10px;
}
.content {
  padding-left: 10px;
  display: inline-block;
  text-align: left;
  color: #444;
}
.carhistory {
  display: inline-block;
}
</style>