<template>
  <div>
    <el-tabs v-model="active" @tab-click="tbClick">
      <el-tab-pane name="list" label="列表">
        <el-row>
          <order_fast_list @tocontrol_c="tocontrol"></order_fast_list>
        </el-row>
      </el-tab-pane>
      <el-tab-pane name="add" label="订单管理">
        <el-row>
          <el-col span="12" offset="6">
            <order_fast_au @tolist="tolist" :car_order_id="now_order_id"></order_fast_au>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane name="count" label="统计">
        <el-row>
          <el-col span="12" offset="6">
            <order_fast_count></order_fast_count>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import order_fast_list from "./order_fast_list";
import order_fast_au from "./order_fast_au";
import order_fast_count from "./order_fast_count";
export default {
  components: {
    order_fast_list,
    order_fast_au,
    order_fast_count
  },
  data() {
    return {
      active: "list",
      now_order_id: ""
    };
  },
  methods: {
    tolist(d) {
      this.now_order_id="";
      this.active = "list";
    },
    tocontrol(d) {
      this.now_order_id = d;
      this.active = "add";
    }
  }
};
</script>

