<template>
  <div>
    <el-form :model="form" label-width="80px">
      <el-form-item label="标签名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="具体描述">
        <el-input type="textarea" v-model="form.content"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="doupdate" icon="el-icon-edit">更新数据</el-button>
        <el-button type="success" @click="backlist" icon="el-icon-caret-left">返回列表</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["tag_id"],
  data() {
    return {
      form: {
        name: "",
        content: ""
      },
      token: sessionStorage.getItem("token")
    };
  },
  computed: {
    now_tag_id() {
      return this.tag_id;
    }
  },
  watch: {
    now_tag_id() {
      this.getinfo();
    }
  },
  methods: {
    input_ok() {
      let err = [];
      this.now_tag_id == "" ? err.push("请从列表选择标签") : "";
      this.form.name.trim().length == 0 ? err.push("需要标签名称") : "";
      this.form.content.trim().length == 0 ? err.push("需要具体描述") : "";

      return err;
    },
    getinfo() {
      axios({
        url: require("@/config").axios.tag_getinfo,
        method: "post",
        data: { token: this.token, info: { tag_id: this.now_tag_id } }
      }).then(re => {
        this.form.name = re.data.data.name;
        this.form.content = re.data.data.content;
      });
    },
    backlist(){
      this.$emit("back_list");
    },
    doupdate() {
      const err = this.input_ok();
      if (err.length > 0) {
        this.$message(err.join("，"));
        return;
      }

      this.$confirm("确认操作?", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        const info = this.form;
        info.tag_id = this.now_tag_id;
        axios({
          url: require("@/config").axios.tag_update,
          method: "post",
          data: {
            token: this.token,
            info: info
          }
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("更新完成");
            this.form.name = "";
            this.form.content = "";
            this.tag_id="";
          } else {
            this.$alert(re.data.msg);
          }
        });
      });
    }
  }
};
</script>

<style scoped>
div {
  text-align: right;
}
</style>