<template>
  <div>
    <ul>
      <li>
        <span class="lb">主车型</span>
        <span class="content">{{info.typeinfo[0].stype}}</span>
      </li>
      <li>
        <span class="lb">价格</span>
        <span class="content">￥{{info.typeinfo[0].price}}</span>
      </li>
      <li>
        <span class="lb">配置</span>
        <span class="content">{{info.typeinfo[0].ltype}}</span>
      </li>
      <li>
        <span class="lb">颜色</span>
        <span class="content">{{info.clr}}</span>
      </li>
      <li>
        <span class="lb">车架号</span>
        <span class="content">{{info.vin}}</span>
      </li>
      <li>
        <span class="lb">生产</span>
        <span class="content">{{info.pdt}}</span>
      </li>
      <li>
        <span class="lb">入库</span>
        <span class="content">{{info.arrdt}}</span>
      </li>
      <li>
        <span class="lb">点款</span>
        <span class="content">{{info.payall |dol_fmt}}</span>
      </li>
      <li>
        <span class="lb">上报</span>
        <span class="content">{{info.dolreport | dol_fmt}}</span>
      </li>
      <li>
        <span class="lb">发动机号</span>
        <span class="content">{{info.fdjcode}}</span>
      </li>
      <li>
        <span class="lb">来源</span>
        <span class="content">{{info.source}} 合格证序号：{{info.hgz}}</span>
      </li>
      <li>
        <span class="lb">厂家型号</span>
        <span class="content">{{info.typeinfo[0].typecode_pub}}</span>
      </li>
      <li>
        <span class="lb">内部型号</span>
        <span class="content">{{info.typeinfo[0].typecode_pri}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import utils from "@/components/utils";
import carhistory from "@/components/common/getCarHistory";
import axios from "axios";

export default {
  props: ["data"],
  watch: {
    data(nv, ov) {
      this.info = nv;
    }
  },
  components: { carhistory },
  data() {
    return {
      info: {},
      token: sessionStorage.getItem("token")
    };
  },
  filters: {
    dol_fmt(val) {
      if (val == "") {
        return "-";
      } else {
        return utils.longdate(val);
      }
    }
  },
  mounted() {
    this.clearInfo();
  },
  methods: {
    clearInfo() {
      this.info = {
        typeinfo: [
          {
            stype: "",
            price: "",
            ltype: "",
            typecode_pub: "",
            typecode_pri: ""
          }
        ],
        clr: "",
        arrdt: "",
        pdt: "",
        payall: "",
        dolreport: "",
        fdjcode: "",
        source: "",
        hgz: ""
      };
    }
  }
};
</script>

<style scoped>
div {
  text-align: left;
}
ul li {
  display: inline-block;
  border: 10px solid rgba(0, 0, 0, 0);
  width: 50%;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.lb {
  border-right: 1px solid #aaa;
  display: inline-block;
  width: 60px;
  color: #888;
  text-align: right;
  padding-right: 10px;
}
.content {
  padding-left: 10px;
  display: inline-block;
  text-align: left;
  color: #444;
}
.carhistory {
  display: inline-block;
}
</style>