<template>
  <div>
    <orderlist ref="orderfit_ref" @choose="chooseorder"></orderlist>
    <el-divider></el-divider>
    <orderinfo ref="orderinfo_ref" @no_order="no_order"></orderinfo>
    <el-divider></el-divider>
    <el-row>
      <el-col span="4" offset="20">
        <el-button type="primary" icon="el-icon-edit" @click="dosold">添加销售</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <deco_au ref="deco_ref"></deco_au>
    <el-row>
      <el-col span="4" offset="20">
        <el-button type="success" plain icon="el-icon-printer" @click="doprint">打印单据</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import orderlist from "@/components/nowstore/carcontrol/orderfit";
import orderinfo from "@/components/nowstore/carcontrol/orderinfo";
import deco_au from "@/components/common/deco_au";
import axios from "axios";

export default {
  components: { orderlist, orderinfo, deco_au },
  data() {
    return {
      info: {
        vin: "",
        clr: "",
        type_id: "",
        car_order_id: "",
        typecode: ""
      },
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    no_order() {
      this.$refs.deco_ref.decos = [];
    },
    dosold() {
      if (this.$refs.orderinfo_ref.vin == "") {
        this.$message("请先选择车辆");
        return;
      }

      if (this.$refs.orderinfo_ref.input_ok().length > 0) {
        this.$message(this.$refs.orderinfo_ref.input_ok().join("，"));
        return;
      }
      this.$confirm("确认操作？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.dosold,
          method: "post",
          data: {
            token: this.token,
            info: this.$refs.orderinfo_ref.createinfo(this.$refs.deco_ref.decos)
          }
        }).then(re => {
          this.$message("操作完成");
          this.$refs.orderinfo_ref.clear_info(); //清空输入框
          this.$refs.orderfit_ref.updatelist(); //刷新订单列表
          this.$refs.deco_ref.decos = []; //清空装饰项目
        });
      });
    },
    doprint() {
      //打印数据
      const decos = this.$refs.deco_ref.sel_decos;
      if (decos.length == 0) {
        this.$message("尚未选择装饰项目");
        return;
      }
      let storage = {
        info: {
          name: this.$refs.orderinfo_ref.info.name,
          clr: this.$refs.orderfit_ref.clr,
          vin: this.info.vin,
          tel: this.$refs.orderinfo_ref.info.tel,
          typecode: this.info.typecode,
          celler: this.$refs.orderinfo_ref.info.cellerinfo[0].name
        },
        decos: decos
      };

      localStorage.setItem("carsold_deco", JSON.stringify(storage));
      window.open("/print/carsold_deco.html", "", "", "");
    },
    getorderfit(car) {
      this.info.vin = car.vin; //获取选定的车辆
      this.$refs.orderinfo_ref.vin = car.vin;
      this.info.clr = car.carinfo[0].clr;
      this.info.type_id = car.carinfo[0].type_id;
      this.info.typecode = car.typeinfo[0].typecode_pub;

      this.$refs.orderfit_ref.getOrders({
        vin: this.info.vin,
        clr: this.info.clr,
        type_id: this.info.type_id
      });
    },
    chooseorder(d) {
      this.car_order_id = d.car_order_id; //获取选定的订单
      this.$refs.orderinfo_ref.updateInfo(d);
      this.$refs.deco_ref.updateData(d.car_order_id);
    }
  }
};
</script>

