<template>
  <div>
    <el-tabs v-model="active">
      <el-tab-pane name="list" label="列表">
        <el-row>
          <el-col>
            <tag_list @edit="toedit"></tag_list>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane name="au" label="创建">
        <el-row>
          <el-col span="8" offset="8">
            <tag_add></tag_add>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane name="update" label="更新">
        <el-row>
          <el-col span="8" offset="8">
            <tag_update :tag_id="now_tag_id" @back_list="back_list"></tag_update>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import tag_add from "@/components/tag/tag_add";
import tag_update from "@/components/tag/tag_update";
import tag_list from "@/components/tag/tag_list";

export default {
  components: { tag_add, tag_list, tag_update },
  data() {
    return {
      active: "list",
      now_tag_id: ""
    };
  },
  methods: {
    back_list() {
      this.active = "list";
    },
    toedit(d) {
      this.now_tag_id = d;
      this.active = "update";
    }
  }
};
</script>

