<template>
  <div>
    <el-select v-model="val" placeholder="颜色" clearable @change="val_change_c">
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["stype", "data"],
  data() {
    return { vals: [], val: "" };
  },
  watch: {
    data(nv, ov) {
      this.val = nv;
    },
    stype(nv, ov) {
      nv == "" ? (this.vals = []) : this.getData();
    }
  },
  methods: {
    val_change_c() {
      this.$emit("val_change", this.val);
    },
    getData() {
      axios({
        url: require("@/config").axios.carsoldclrlist + "/" + this.stype,
        method: "post",
        data: { token: sessionStorage.getItem("token") }
      }).then(re => {
        this.vals = re.data.data.map(d => {
          return { val: d };
        });
      });
    }
  }
};
</script>