<template>
  <div>
    <el-tabs v-model="active">
      <el-tab-pane name="list" label="列表">
        <el-row>
          <list @to_control="to_control"></list>
        </el-row>
      </el-tab-pane>
      <el-tab-pane name="update" label="数据管理">
        <el-row>
          <el-col span="8" offset="8">
            <car_update :data="sel_vin"></car_update>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import list from "./list";
import car_update from "./update";

export default {
  components: { list, car_update },
  data() {
    return {
      active: "list",
      sel_vin: ""
    };
  },
  methods: {
    to_control(d) {
      this.sel_vin = d.vin;
      this.active = "update";
    }
  }
};
</script>

