<template>
  <div>
    <ul>
      <li>
        <div class="inputflex">
          <label>目标日期</label>
          <dtpicker ref="dtpicker_ref"></dtpicker>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>销售人员</label>
          <sel_celler ref="celler_ref" cate="展厅"></sel_celler>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>单车产值</label>
          <el-input v-model.number="data.chanzhi"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>单车按揭款</label>
          <el-input v-model.number="data.anjie"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>交车目标</label>
          <el-input v-model.number="data.soldtag"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>按揭数量</label>
          <el-input v-model.number="data.anjie_car"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>特殊车型目标</label>
          <el-input v-model.number="data.special_car"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label>任务完成率%</label>
          <el-input v-model.number="data.tagrate"></el-input>
        </div>
      </li>
      <li>
        <el-button @click="savedata" type="primary" v-if="state">保存数据</el-button>
        <el-button @click="updatedata" type="primary" v-if="!state">更新数据</el-button>
      </li>
    </ul>
  </div>
</template>

<script>
import sel_celler from "@/components/common/sel_celler";
import dtpicker from "@/components/common/monthpicker";
import axios from "axios";

export default {
  components: { sel_celler, dtpicker },
  data() {
    return {
      data: {
        chanzhi: 3000,
        anjie: 4000,
        soldtag: 0,
        anjie_car: 0,
        special_car: 0,
        tagrate: 50,
        cellerid: 0
      },
      state: 1,
      err: []
    };
  },
  methods: {
    input_rate(ev) {
      alert("");
    },
    input_err() {
      this.err = [];
      let isErr = false;
      if (!/\d+/gi.test(this.data.chanzhi)) {
        this.err.push("产值金额不正确");
        isErr = true;
      }

      if (!/\d+/gi.test(this.data.anjie)) {
        this.err.push("按揭款金额不正确");
        isErr = true;
      }

      if (!/\d+/gi.test(this.data.soldtag)) {
        this.err.push("交车目标不正确");
        isErr = true;
      }

      if (!/\d+/gi.test(this.data.anjie_car)) {
        this.err.push("按揭数量不正确");
        isErr = true;
      }

      if (this.$refs.celler_ref.celler.length == 0) {
        this.err.push("销售顾问名称为空");
        isErr = true;
      }

      if (!/\d+/gi.test(this.data.special_car)) {
        this.err.push("特殊车型数量不正确");
        isErr = true;
      }

      if (!/\d+/gi.test(this.data.tagrate)) {
        this.err.push("任务完成率不正确");
        isErr = true;
      }

      return isErr;
    },
    savedata() {
      if (!this.input_err()) {
        this.$confirm("要添加任务目标吗？", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.addtag,
            method: "post",
            data: {
              year: this.$refs.dtpicker_ref.year,
              month: this.$refs.dtpicker_ref.month,
              celler_id: this.$refs.celler_ref.cellerid,
              chanzhi: this.data.chanzhi,
              anjie: this.data.anjie,
              soldtag: this.data.soldtag,
              anjietag: this.data.anjie_car,
              specialtag: this.data.special_car,
              tagrate: this.data.tagrate,
              token:sessionStorage.getItem("token")
            }
          }).then(re => {
            if (re.data.code == 200) {
              this.$message("任务目标已保存");
              this.data = {
                chanzhi: 3000,
                anjie: 4000,
                soldtag: 0,
                anjie_car: 0,
                special_car: 0,
                tagrate: 50,
                cellerid: 0
              };
            } else {
              this.$message("任务目标重复，保存失败");
            }
          });
        });
      } else {
        this.$message(this.err.join("、"));
      }
    },
    updatedata() {}
  }
};
</script>

<style scoped>
ul li {
  padding: 8px 0;
}

div button {
  width: 100%;
}
</style>