<template>
  <div>
    <el-row v-show="is_payfull">
      <el-col span="24">
        <el-button @click="cancelpay" type="danger" icon="el-icon-delete">取消点款</el-button>
      </el-col>
    </el-row>
    <el-row v-show="!is_payfull">
      <el-col span="6">
        <dtpicker ref="paydt"></dtpicker>
      </el-col>
      <el-col span="4">
        <el-button @click="dopayfull" type="primary" icon="el-icon-edit">确认点款</el-button>
      </el-col>
      <el-col span="16"></el-col>
    </el-row>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datetimepicker";
import axios from "axios";

export default {
  components: { dtpicker },
  data() {
    return {
      vin: "",
      is_payfull: true,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    cancelpay() {
      if (this.vin == "") {
        this.$message("请先选择车辆");
        return;
      }

      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.cancelpay,
          method: "post",
          data: {
            vin: this.vin,
            token: this.token
          }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    },
    dopayfull() {
      if (this.vin == "") {
        this.$message("请先选择车辆");
        return;
      }
      this.$confirm("确认操作？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.dopayfull,
          method: "post",
          data: {
            vin: this.vin,
            token: this.token,
            dt: new Date(this.$refs.paydt.value1)
          }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    }
  }
};
</script>

