<template>
  <div>
    <el-divider>更新在途数据</el-divider>
    <el-row>
      <el-col span="4" offset="8">
        <down_inway></down_inway>
      </el-col>
      <el-col span="4">
        <csv_upload @on_uploaded="uploaded"></csv_upload>
      </el-col>
    </el-row>
    <el-divider>厂家系统未入库车辆</el-divider>
    <el-row>
      <el-col span="12" offset="6">
        <car_is_in></car_is_in>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import csv_upload from "@/components/common/uploader_csv";
import car_is_in from "@/components/common/sel_car_is_in";
import down_inway from "@/components/common/template/down_inway";

import axios from "axios";

export default {
  data() {
    return { token: sessionStorage.getItem("token") };
  },
  components: { csv_upload, car_is_in, down_inway },
  methods: {
    uploaded(d) {
      this.$confirm("上传完成。开始更新数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.carinway_import,
          method: "post",
          data: { token: this.token, info: { filename: d } }
        }).then(re => {
          this.$message("更新完成");
        });
      });
    }
  }
};
</script>

