<template>
  <div>
    <el-row>
      <el-col span="6">
        <el-button type="primary" @click="doupdate">刷新</el-button>
      </el-col>
      <el-col span="12">
        <el-select v-model="val" placeholder="已入库车辆" clearable>
          <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
        </el-select>
      </el-col>
      <el-col span="6">
        <el-button type="warning" @click="dodel">删除</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return { vals: [], val: "" };
  },
  methods: {
    doupdate() {
      this.getData();
      this.$message("Done");
    },
    dodel() {
      if (this.val == "") {
        this.$message("请先选择车辆");
        return;
      }
      this.$confirm("确定删除?", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.carinway_isin_del,
          method: "post",
          data: {
            token: sessionStorage.getItem("token"),
            info: { vin: this.val }
          }
        }).then(re => {
          this.$message("Done");
          this.getData();
        });
      });
    },
    getData() {
      axios({
        url: require("@/config").axios.carinway_isinlist,
        method: "post",
        data: { token: sessionStorage.getItem("token") }
      }).then(re => {
        this.vals = re.data.data.map(d => {
          return { val: d };
        });
      });
    }
  }
};
</script>