<template>
  <div>
    <el-row v-show="car_order_id!=''">
      <el-col>
        <el-tag type="primary" @click="noorder">{{car_order_info}} 点击取消</el-tag>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>客户姓名</label>
          <el-input v-model="info.name"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>身份证号</label>
          <el-input v-model="info.idcode"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>联系电话</label>
          <el-input v-model="info.tel"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>地址</label>
          <el-input v-model="info.address"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>定车日期</label>
          <dtpicker ref="odt_ref"></dtpicker>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>销售日期</label>
          <dtpicker ref="solddt_ref"></dtpicker>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="6">
        <div class="inputflex">
          <label>车款</label>
          <el-input v-model.number="info.carpay"></el-input>
        </div>
      </el-col>
      <el-col span="6">
        <div class="inputflex">
          <label>装饰款</label>
          <el-input v-model.number="info.zspay"></el-input>
        </div>
      </el-col>
      <el-col span="6">
        <div class="inputflex">
          <label>按揭款</label>
          <el-input v-model.number="info.ajpay"></el-input>
        </div>
      </el-col>
      <el-col span="6">
        <div class="inputflex">
          <label>优惠额度</label>
          <span class="spanstyle">{{youhui}}</span>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>支付方式</label>
          <sel_carsold_payway1 @val_change="change_payway1" ref="payway1_ref"></sel_carsold_payway1>
          <sel_carsold_payway2 :payway1="now_payway1" ref="payway2_ref"></sel_carsold_payway2>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>销售划分</label>
          <sel_carsold_cate @val_change="change_cate" ref="cate_ref"></sel_carsold_cate>
          <sel_celler :cate="now_cate" ref="celler_ref" @val_change="change_celler"></sel_celler>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-input v-model="info.tips" placeholder="备注信息"></el-input>
    </el-row>
  </div>
</template>

<script>
import sel_carsold_payway1 from "@/components/common/sel_carsold_payway1";
import sel_carsold_payway2 from "@/components/common/sel_carsold_payway2";
import sel_carsold_cate from "@/components/common/sel_carsold_cate";
import sel_celler from "@/components/common/sel_celler";
import axios from "axios";
import dtpicker from "@/components/common/datetimepicker";
import utils from "@/components/utils";

export default {
  components: {
    sel_carsold_payway1,
    sel_carsold_payway2,
    sel_carsold_cate,
    sel_celler,
    dtpicker
  },
  watch: {
    vin(nv, ov) {
      this.get_carinfo(nv);
    },
    carpay(nv, ov) {
      this.carinfo.typeinfo != undefined
        ? (this.youhui = this.carinfo.typeinfo[0].price - this.info.carpay)
        : "";
    }
  },
  computed: {
    carpay() {
      return this.info.carpay;
    }
  },
  data() {
    return {
      youhui: 0,
      carinfo: {},
      now_celler_id: 0,
      token: sessionStorage.getItem("token"),
      now_payway1: "",
      now_cate: "",
      vin: "", //选中车辆
      car_order_id: "", //选中订单ID
      car_order_info: "", //选中订单描述
      info: {}
    };
  },
  mounted() {
    this.clear_info();
  },
  methods: {
    get_carinfo(vin) {
      if (vin != "") {
        axios({
          url: require("@/config").axios.sincarinfo,
          method: "post",
          data: { token: this.token, info: { vin: this.vin } }
        }).then(re => {
          this.carinfo = re.data.data[0];
          this.youhui = this.carinfo.typeinfo[0].price - this.info.carpay;
        });
      }
    },
    change_celler(d) {
      this.now_celler_id = d;
      axios({
        url: require("@/config").axios.cellerid2name,
        method: "post",
        data: { token: this.token, info: { cellerid: d } }
      }).then(re => {
        this.info.cellerinfo[0].name = re.data.name;
        this.info.cellerinfo[0].cate = re.data.cate;
        this.info.cellerinfo[0].celler_id = re.data.celler_id;
      });
    },
    noorder() {
      this.clear_info();
      this.$emit("no_order", ""); //清除订单装饰数据
    },
    clear_info() {
      this.car_order_id = "";
      this.info = {
        name: "",
        idcode: "",
        tel: "",
        adress: "",
        odt: "",
        carpay: 0,
        zspay: 0,
        ajpay: 0,
        payway1: "",
        payway2: "",
        tips: "",
        car_order_id: "",
        deco_order_id: "",
        cellerinfo: [{ cate: "", celler_id: "", name: "" }]
      };

      this.$refs.cate_ref.val = "";
      this.$refs.celler_ref.celler = "";
      this.$refs.payway1_ref.val = "";
      this.$refs.payway2_ref.val = "";
    },
    change_payway1(d) {
      this.now_payway1 = d;
    },
    change_cate(d) {
      this.now_cate = d;
    },
    updateInfo(d) {
      this.car_order_id = d.car_order_id;
      this.car_order_info = `已选中：${d.odt} ${d.name} ${d.cellerinfo[0].name}`;
      axios({
        url: require("@/config").axios.orderinfo,
        method: "post",
        data: { token: this.token, car_order_id: d.car_order_id }
      }).then(re => {
        //返回有销售记录
        this.info = re.data.data;
        this.$refs.odt_ref.value1 = utils.shortdate(this.info.odt);
        this.$refs.payway1_ref.val = this.info.payway1;
        this.$refs.payway2_ref.val = this.info.payway2;
        this.$refs.cate_ref.val = this.info.cellerinfo[0].cate;
        this.$refs.celler_ref.celler = this.info.cellerinfo[0].name; //保存销售顾问姓名
        this.now_celler_id = this.info.cellerinfo[0].celler_id; //保存销售顾问id
        this.now_cate = this.info.cellerinfo[0].cate;
        this.$refs.celler_ref.cellerid = this.info.cellerinfo[0].celler_id;
      });
    },
    createinfo(decos) {
      return {
        carsold: {
          //销售数据
          vin: this.vin,
          solddt: this.$refs.solddt_ref.value1,
          orderdt: this.$refs.odt_ref.value1,
          cate: this.$refs.cate_ref.val,
          celler_id: this.now_celler_id,
          customer: this.info.name,
          tel: this.info.tel,
          idcode: this.info.idcode,
          address: this.info.address,
          price_car: this.info.carpay,
          price_deco: this.info.zjpay,
          price_anjie: this.info.ajpay,
          tips: this.info.tips,
          payway_1: this.$refs.payway1_ref.val,
          payway_2: this.$refs.payway2_ref.val
        },
        decos: decos,
        deco_order_id: this.info.deco_order_id,
        car_order_id: this.info.car_order_id
      };
    },
    input_ok() {
      let err = [];
      this.info.name == "" ? err.push("需要客户姓名") : "";
      this.$refs.cate_ref.val == "" ? err.push("需要销售划分") : "";
      this.$refs.celler_ref.celler == "" ? err.push("需要销售顾问") : "";
      this.$refs.payway1_ref.val == "" ? err.push("需要支付方式") : "";
      this.$refs.payway2_ref.val == "" ? err.push("需要方式关联") : "";

      return err;
    }
  }
};
</script>

<style scoped>
.spanstyle {
  line-height: 40px;
  color: #666;
}
</style>