<template>
  <div>
    <el-row gutter="10">
      <el-col span="2">
        <posi1 @val_change="change_posi1"></posi1>
      </el-col>
      <el-col span="2">
        <posi2 :posi1="info.posi1" @val_change="change_posi2"></posi2>
      </el-col>
      <el-col span="2">
        <stype @val_change="change_stype"></stype>
      </el-col>
      <el-col span="2">
        <price :stype="info.stype" @val_change="change_price" :data="info.price"></price>
      </el-col>
      <el-col span="6">
        <ltype
          :stype="info.stype"
          :price="info.price"
          @val_change="change_ltype"
          :data="info.ltype"
        ></ltype>
      </el-col>
      <el-col span="2">
        <clr :stype="info.stype" @val_change="change_clr" :data="info.clr"></clr>
      </el-col>
      <el-col span="2">
        <el-input placeholder="VIN" v-model="info.vin" @keyup.enter.native="doquery"></el-input>
      </el-col>
      <el-col span="2">
        <el-button type="primary" icon="el-icon-search" @click="doquery" :loading="queryloading">查询</el-button>
      </el-col>
    </el-row>
    <el-row v-show="all_tags.length">
      <el-col>
        <el-checkbox v-for="d in all_tags" :key="d.id" v-model="d.check">{{d.val}}</el-checkbox>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <div id="catesdiv">
        <el-badge :value="item.sum" class="item" v-for="item in cates" :key="item" type="primary">
          <button
            :class="item.css"
            @click="sel_cate"
            :stype="item.stype"
            :price="item.price"
            :clr="item.clr"
          >{{ item.stype }}-{{ item.price }}-{{ item.clr }}</button>
        </el-badge>
      </div>
    </el-row>
    <el-divider>{{report}}</el-divider>
    <el-table :data="details_in_page" border height="550px" style="width: 100%">
      <el-table-column prop="carinfo[0].pdt" label="生产" width="100" class="ddd"></el-table-column>
      <el-table-column prop="carinfo[0].arrdt" label="入库" width="100"></el-table-column>
      <el-table-column prop="movedt" label="移动" width="100"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="配置" width="400"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="价格" width="80"></el-table-column>
      <el-table-column prop="carinfo[0].clr" label="颜色" width="80"></el-table-column>
      <el-table-column prop="vin" label="车架号" width="200">
        <template slot-scope="scope">
          <el-link
            v-if="scope.row.carinfo[0].pdt!='-'"
            @click="tocontrol(scope.row)"
            :class="haslink(scope.row.linkinfo.length)"
            icon="el-icon-caret-right"
          >{{scope.row.vin}}</el-link>
          <span v-if="scope.row.carinfo[0].pdt=='-'">{{scope.row.vin}}</span>
        </template>
      </el-table-column>
      <el-table-column label="位置" width="200">
        <template slot-scope="scope">
          <div class="mystyle">{{scope.row.posi1}}-{{scope.row.posi2}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="age" label="库龄" width="50"></el-table-column>
      <el-table-column label="钥匙" width="80">
        <template slot-scope="scope">
          <div class="mystyle">{{scope.row.key_posi}}</div>
        </template>
      </el-table-column>
      <el-table-column label="点款" width="30">
        <template slot-scope="scope">
          <div class="mystyle">{{scope.row.carinfo[0].payall | dol_fmt}}</div>
        </template>
      </el-table-column>
      <el-table-column label="虚报" width="30">
        <template slot-scope="scope">
          <div class="mystyle">{{scope.row.carinfo[0].dolreport | dol_fmt}}</div>
        </template>
      </el-table-column>
      <el-table-column label="促销" width="30">
        <template slot-scope="scope">
          <div class="mystyle">{{scope.row.soldmeinfo | soldme_fmt}}</div>
        </template>
      </el-table-column>
      <el-table-column label="标签" width="60">
        <template slot-scope="scope">
          <el-button
            type="warning"
            size="mini"
            @click="showtags(scope.row)"
            v-show="scope.row.tags.length"
          >{{scope.row.tags.length}}</el-button>
        </template>
      </el-table-column>
      <el-table-column label="定单">
        <template slot-scope="scope">
          <div :class="order_style(scope.row.linkorderinfo)">{{scope.row.linkorderinfo}}</div>
        </template>
      </el-table-column>
      <el-table-column label="加装" width="80">
        <template slot-scope="scope">
          <el-button
            size="mini"
            plain
            v-if="scope.row.decoinfo.length!=0"
            @click="showdecos(scope.row)"
          >{{scope.row.decoinfo.length}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="outputstyle">
      <el-link icon="el-icon-download" class="linkstyle" @click="output_total">导出总库存</el-link>
      <el-link icon="el-icon-download" class="linkstyle" @click="output">导出选中库存</el-link>
    </div>
  </div>
</template>

<script>
import posi1 from "@/components/common/sel_nowstore_posi1";
import posi2 from "@/components/common/sel_nowstore_posi2";
import stype from "@/components/common/sel_nowstore_stype";
import price from "@/components/common/sel_nowstore_price";
import clr from "@/components/common/sel_nowstore_clr";
import ltype from "@/components/common/sel_all_ltype";
import axios from "axios";
import { shortdate, days, clr_map, outputData } from "@/components/utils";

export default {
  components: { posi2, posi1, stype, price, clr, ltype },
  data() {
    return {
      info: {},
      report: "",
      queryloading: false,
      nowpage: 1, //当前页数序号
      infoperpage: 10, //每页最大记录数量
      details: [], //总明细
      details_in_page: [], //筛选明细
      cates: [],
      vin: "",
      token: sessionStorage.getItem("token"),
      all_tags: []
    };
  },
  filters: {
    soldme_fmt(val) {
      if (val.length > 0) {
        return "●";
      } else {
        return "";
      }
    },
    dol_fmt(val) {
      if (val != "") {
        return "●";
      } else {
        return "";
      }
    }
  },
  mounted() {
    this.nowstore_tags();
    this.clearInfo();
  },
  methods: {
    clearInfo() {
      this.info = {
        posi1: "",
        posi2: "",
        stype: "",
        price: "",
        ltype: "",
        clr: "",
        vin: ""
      };
    },
    output_format(data) {
      let fmt = {};

      fmt.fields = [
        "生产",
        "入库",
        "移动",
        "VIN",
        "车型",
        "价格",
        "配置",
        "颜色",
        "位置", //7
        "库龄",
        "点款",
        "虚报",
        "促销",
        "标签",
        "订单",
        "加装"
      ];

      fmt.json = data.map(d => {
        let re = {};
        re[fmt.fields[0]] = d.carinfo[0].pdt;
        re[fmt.fields[1]] = d.carinfo[0].arrdt;
        re[fmt.fields[2]] = d.movedt;
        re[fmt.fields[3]] = d.vin;
        re[fmt.fields[4]] = d.typeinfo[0].stype;
        re[fmt.fields[5]] = d.typeinfo[0].price;
        re[fmt.fields[6]] = d.typeinfo[0].ltype;
        re[fmt.fields[7]] = d.carinfo[0].clr;
        re[fmt.fields[8]] = `${d.posi1}- ${d.posi2}`;
        re[fmt.fields[9]] = d.age;
        re[fmt.fields[10]] = d.carinfo[0].payall;
        re[fmt.fields[11]] = d.carinfo[0].dolreport;
        re[fmt.fields[12]] =
          d.soldmeinfo.length > 0
            ? `展厅：${d.soldmeinfo[0].payceller} 二网：${d.soldmeinfo[0].paynet}`
            : "";
        re[fmt.fields[13]] = ``;
        re[fmt.fields[14]] = d.linkorderinfo;
        re[fmt.fields[15]] = ``;
        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    },
    output_total() {
      if (this.details.length == 0) {
        this.$message("请先查询库存");
        return;
      }

      this.$confirm("确认导出数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        outputData(this.output_format(this.details));
      });
    },
    output() {
      if (this.details_in_page.length == 0) {
        this.$message("请先选中库存");
        return;
      }
      this.$confirm("确认导出数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        outputData(this.output_format(this.details_in_page));
      });
    },
    haslink(d) {
      if (d > 0) {
        return "haslink";
      } else {
        return "";
      }
    },
    nowstore_tags() {
      axios({
        url: require("@/config").axios.tag_nowstore,
        method: "post",
        data: { token: this.token }
      }).then(re => {
        this.all_tags = re.data.data.map(d => {
          d.check = false;
          return d;
        });
      });
    },
    order_style(val) {
      const content = JSON.stringify(val);
      if (content.indexOf("【无资源】") > -1) {
        return "mystyle nocar";
      } else if (content.indexOf("【绑定】") > -1) {
        return "mystyle linkcar";
      } else {
        return "mystyle";
      }
    },
    showtags(d) {
      const tagsArr = d.tags.map(d => {
        return `<tr><td width='30%'>${d.name}</td><td>${d.content}</td></tr>`;
      });

      const content = `<table width='100%'>
        <tr><th>名称</td><th>描述</td></tr>
        ${tagsArr.join("")}
        </table>`;
      this.$alert(content, "详情", {
        dangerouslyUseHTMLString: true
      });
    },
    showdecos(d) {
      const decoArr = d.decoinfo.map(d => {
        return `<tr><td>${d.name}</td><td>${d.count}</td></tr>`;
      });

      const content = `<table width='100%'>
        <tr><th>项目</td><th>数量</th></tr>
        ${decoArr.join("")}
        </table>`;
      this.$alert(content, "详情", {
        dangerouslyUseHTMLString: true
      });
    },
    tocontrol(data) {
      this.$emit("tocontrolpage_c", data);
    },
    sel_cate(ev) {
      const stype = ev.target.getAttribute("stype");
      const price = ev.target.getAttribute("price");
      const clr = ev.target.getAttribute("clr");

      this.details_in_page = this.details.filter(d => {
        return (
          d.typeinfo[0].stype == stype &&
          d.typeinfo[0].price == price &&
          d.carinfo[0].clr == clr
        );
      });
    },
    calc_cates() {
      let map = new Map();
      let order_map = new Map();

      let re = [];
      this.details.forEach(d => {
        if (d.vin != "无资源") {
          const key = `${d.typeinfo[0].stype}^${d.typeinfo[0].price}^${d.carinfo[0].clr}`;
          if (map.get(key) == undefined) {
            map.set(key, 1);
          } else {
            map.set(key, map.get(key) + 1);
          }
        }

        if (d.linkorderinfo != "") {
          const key = `${d.typeinfo[0].stype}^${d.typeinfo[0].price}^${d.carinfo[0].clr}`;
          if (order_map.get(key) == undefined) {
            order_map.set(key, 1);
          } else {
            order_map.set(key, order_map.get(key) + 1);
          }
        }
      });

      map.forEach((v, k) => {
        re.push({
          stype: k.split("^")[0],
          price: k.split("^")[1],
          clr: k.split("^")[2],
          sum:
            v + "/" + (order_map.get(k) == undefined ? "0" : order_map.get(k)),
          css: clr_map(k.split("^")[2])
        });
      });
      this.cates = re;
    },
    cur_page(val) {
      this.nowpage = val;
    },
    change_posi1(d) {
      this.info.posi1 = d;
      this.info.posi2 = "";
    },
    change_posi2(d) {
      this.info.posi2 = d;
    },
    change_stype(d) {
      this.info.stype = d;
      this.info.price = "";
      this.info.ltype = "";
      this.info.clr = "";
    },
    change_price(d) {
      this.info.price = d;
      this.info.ltype = "";
      this.info.clr = "";
    },
    change_ltype(d) {
      this.info.ltype = d;
      this.info.clr = "";
    },
    change_clr(d) {
      this.info.clr = d;
    },
    queryObj() {
      return Object.assign(
        this.info,
        { token: this.token },
        {
          tags: this.all_tags.filter(d => {
            return d.check == true;
          })
        }
      );
    },
    createReport(data) {
      this.report = "";
      let map = new Map();
      data.forEach(d => {
        map.get(d.typeinfo[0].stype) == undefined && d.vin.length == 17
          ? map.set(d.typeinfo[0].stype, 1)
          : d.vin.length == 17
          ? map.set(d.typeinfo[0].stype, map.get(d.typeinfo[0].stype) + 1)
          : "";
      });

      let report = [],
        sum = 0;
      map.forEach((v, k) => {
        sum = sum + v;
        report.push(`${k}：${v}`);
      });

      report.push(`合计：${sum}`);

      this.report = report.join("，");
    },
    doquery() {
      if (this.queryloading) {
        return;
      }
      this.queryloading = true;
      axios({
        url: require("@/config").axios.nowstorelist,
        method: "post",
        data: this.queryObj()
      }).then(re => {
        if (re.data.code == 401) {
          this.details = [];
        } else {
          this.details = re.data.data;
          this.details = this.details.map(d => {
            d.carinfo[0].pdt = shortdate(d.carinfo[0].pdt); //日期样式转换
            d.carinfo[0].arrdt = shortdate(d.carinfo[0].arrdt); //日期样式转换
            if (d.orderinfo.length > 0) {
              d.orderinfo[0].odt = shortdate(d.orderinfo[0].odt); //定单日期
            }
            if (d.carinfo[0].pdt == "-") {
              d.age = 0; //如果pdt（生产日期）为空，则这是一个超出库存的订单，没有库龄参数
            } else {
              d.age = days(d.carinfo[0].pdt, shortdate(new Date())); //计算库龄
            }
            d.movedt = shortdate(d.movedt);
            return d;
          });
          //生产日期排序
          this.details.sort((d1, d2) => {
            return new Date(d1.carinfo[0].pdt) - new Date(d2.carinfo[0].pdt);
          });
          this.nowpage = 1;
        }
        this.calc_cates();
        this.createReport(this.details);
        this.details_in_page = [];
        this.queryloading = false;
      });
    }
  }
};
</script>

<style scoped>
div {
  text-align: left;
}

.link {
  line-height: 35px;
}

.item {
  margin-top: 10px;
  margin-right: 40px;
}

#catesdiv {
  text-align: center;
  max-height: 150px;
  overflow: auto;
}

#catesdiv button {
  width: 200px;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  background: #fff;
  padding: 5px 15px;
  box-shadow: 1px 1px 3px #aaa;
}

.mystyle {
  font-size: 0.8rem;
}

.haslink:after {
  content: url("/icon/lock.svg");
  position: absolute;
  left: 140px;
  width: 20px;
  height: 20px;
}

.nocar {
  color: rgba(255, 0, 0, 0.8);
}

.linkcar {
  color: rgba(0, 0, 255, 0.8);
}
</style>