<template>
  <div>
    <el-row>
      <el-col span="5">
        <mthpicker ref="mtpk1_ref"></mthpicker>
      </el-col>
      <el-col span="2">
        <myceller ref="celler" cate="展厅"></myceller>
      </el-col>
      <el-col span="2">
        <el-button type="primary" @click="doquery" :loading="queryloading" icon="el-icon-search">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="details_in_page" border height="620px" style="width: 100%">
      <el-table-column prop="index" label="序号" width="50"></el-table-column>
      <el-table-column prop="cellerinfo[0].name" label="销售顾问" width="80"></el-table-column>
      <el-table-column prop="solddt" label="购车日期" width="150"></el-table-column>
      <el-table-column prop="customer" label="客户姓名" width="150"></el-table-column>
      <el-table-column prop="payway_1" label="支付方式" width="100"></el-table-column>
      <el-table-column prop="payway_2" label="支付方式详" width="100"></el-table-column>
      <el-table-column prop="vin" label="车架号" width="180"></el-table-column>
      <el-table-column prop="typeinfo[0].stype" label="主要车型" width="80"></el-table-column>
      <el-table-column prop="typeinfo[0].ltype" label="详细车型" width="150"></el-table-column>
      <el-table-column prop="typeinfo[0].price" label="市场报价" width="80"></el-table-column>
      <el-table-column prop="price_car" label="成交价" width="80"></el-table-column>
      <el-table-column prop="price_deco" label="装饰款" width="80"></el-table-column>
      <el-table-column prop="price_anjie" label="按揭款" width="80"></el-table-column>
      <el-table-column label="装饰明细" width="120" class="decostype">
        <template slot-scope="scope">
          <el-button
            @click="handleEdit(scope.$index,scope.row.decos)"
            size="mini"
            v-show="scope.row.decos.length>0"
          >{{scope.row.decos.length}}</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="js_sum" label="装饰结算" width="80"></el-table-column>
      <el-table-column prop="cb_sum" label="装饰成本" width="80"></el-table-column>
      <el-table-column prop="policy.percentage_limit" label="结算限价" width="80"></el-table-column>
      <el-table-column prop="policy.percentage" label="单车提成" width="80"></el-table-column>
      <el-table-column prop="zhongchanzhi" label="总产值" width="80"></el-table-column>
      <el-table-column prop="jingchanzhi" label="净产值" width="80"></el-table-column>
      <el-table-column prop="price_anjie_left" label="剩余按揭款" width="100"></el-table-column>
      <el-table-column prop="soldme[0].pay" label="特殊奖励" width="80"></el-table-column>
    </el-table>
    <div class="output">
      <el-button
        type="primary"
        plain
        @click="output"
        :loading="outputloading"
        icon="el-icon-download"
      >导出明细</el-button>
    </div>
    <el-divider>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="details.length"
        :hide-on-single-page="true"
        @current-change="cur_page"
      ></el-pagination>
    </el-divider>

    <el-drawer title="装饰明细" :visible.sync="drawer" :direction="direction">
      <el-table :data="car_decos" border style="width: 100%">
        <el-table-column prop="name" label="名称" width="200"></el-table-column>
        <el-table-column prop="js" label="结算价" width="80"></el-table-column>
        <el-table-column prop="cb" label="成本价" width="80"></el-table-column>
        <el-table-column prop="count" label="数量" width="80"></el-table-column>
      </el-table>
    </el-drawer>
  </div>
</template>

<script>
import mthpicker from "@/components/common/monthpicker";
import myceller from "@/components/common/sel_celler";
import axios from "axios";

export default {
  components: { mthpicker, myceller },
  data() {
    return {
      queryloading: false,
      outputloading: false,
      direction: "rtl",
      drawer: false,
      nowpage: 1,
      infoperpage: 10,
      year: 0,
      month: 0,
      details: [],
      car_decos: []
    };
  },
  computed: {
    details_in_page() {
      if (this.details.length > 0) {
        return this.details.slice(
          (this.nowpage - 1) * this.infoperpage,
          this.infoperpage * this.nowpage
        );
      } else {
        return [];
      }
    },
    output_format() {
      let fmt = {};

      fmt.fields = [
        "销售顾问",
        "购车日期",
        "客户姓名",
        "支付方式",
        "支付方式详",
        "车架号",
        "主要车型",
        "详细车型",
        "市场报价",
        "成交价",
        "装饰款",
        "按揭款",
        "装饰明细",
        "装饰结算",
        "装饰成本",
        "结算限价",
        "单车提成",
        "总产值",
        "净产值",
        "剩余按揭款",
        "特殊奖励"
      ];

      fmt.json = this.details.map(d => {
        let decos = [];
        if (d.decos.length > 0) {
          decos = d.decos.map(d => {
            return `${d.name}:${d.js}:${d.cb}:${d.count}`;
          });
        }

        let re = {};
        re[fmt.fields[0]] = d.cellerinfo[0].name;
        re[fmt.fields[1]] = d.solddt;
        re[fmt.fields[2]] = d.customer;
        re[fmt.fields[3]] = d.payway_1;
        re[fmt.fields[4]] = d.payway_2;
        re[fmt.fields[5]] = d.vin;
        re[fmt.fields[6]] = d.typeinfo[0].stype;
        re[fmt.fields[7]] = d.typeinfo[0].ltype;
        re[fmt.fields[8]] = d.typeinfo[0].price;
        re[fmt.fields[9]] = d.price_car;
        re[fmt.fields[10]] = d.price_deco;
        re[fmt.fields[11]] = d.price_anjie;
        re[fmt.fields[12]] = decos.join("，"); ////
        re[fmt.fields[13]] = d.js_sum;
        re[fmt.fields[14]] = d.cb_sum;
        re[fmt.fields[15]] = d.policy.percentage_limit;
        re[fmt.fields[16]] = d.policy.percentage;
        re[fmt.fields[17]] = d.zhongchanzhi;
        re[fmt.fields[18]] = d.jingchanzhi;
        re[fmt.fields[19]] = d.price_anjie_left;
        re[fmt.fields[20]] = d.soldme[0].pay;
        return re;
      });

      fmt.token = sessionStorage.getItem("token");
      return fmt;
    }
  },
  methods: {
    output() {
      if (this.details.length == 0) {
        return;
      }

      this.$confirm("确认导出明细？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        this.outputloading = true;
        axios({
          url: require("@/config").axios.outputsalary,
          method: "post",
          data: this.output_format
        }).then(re => {
          window.open(require("@/config").domain + re.data.filename);
          this.outputloading = false;
        });
      });
    },
    handleEdit(i, data) {
      if (data.length > 0) {
        this.car_decos = data;
        this.drawer = true;
      }
    },
    cur_page(val) {
      this.nowpage = val;
    },
    doquery() {
      this.queryloading = true;
      axios({
        url: require("@/config").axios.salarylist,
        method: "post",
        data: {
          year: this.$refs.mtpk1_ref.year,
          month: this.$refs.mtpk1_ref.month,
          celler_id: this.$refs.celler.celler,
          token: sessionStorage.getItem("token")
        }
      }).then(re => {
        //找不到对应月份政策数据
        if (re.data.code == 401) {
          this.details = [];
        } else {
          let i = 1;
          this.details = re.data.map(d => {
            d.index = i;
            i++;
            return d;
          });
        }
        this.queryloading = false;
      });
    }
  }
};
</script>

<style  scoped>
.decostype {
  font-size: 0.8rem !important;
}

div .output {
  text-align: right;
}

.el-icon-zoom-in {
  font-size: 25px !important;
}
</style>