<template>
  <div>
    <el-form :model="form" label-width="80px">
      <el-form-item label="标签名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="具体描述">
        <el-input type="textarea" v-model="form.content"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="doadd" icon="el-icon-edit">创建标签</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      form: {
        name: "",
        content: ""
      },
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    input_ok() {
      let err = [];
      this.form.name.trim().length == 0 ? err.push("需要标签名称") : "";
      this.form.content.trim().length == 0 ? err.push("需要具体描述") : "";

      return err;
    },
    doadd() {
      const err = this.input_ok();
      if (err.length > 0) {
        this.$message(err.join("，"));
        return;
      }

      this.$confirm("确认操作?", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.tag_add,
          method: "post",
          data: { token: this.token, info: this.form }
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("添加完成");
            this.form.name = "";
            this.form.content = "";
          } else {
            this.$alert(re.data.msg);
          }
        });
      });
    }   
  }
};
</script>

<style scoped>
div {
  text-align: right;
}
</style>