<template>
  <div>
    <el-row gutter="10">
      <el-col span="8">
        <posi1 @val_change="change_posi1" ref="posi1_ref"></posi1>
      </el-col>
      <el-col span="8">
        <posi2 :posi1="now_posi1" ref="posi2_ref"></posi2>
      </el-col>
      <el-col span="8">
        <el-button icon="el-icon-edit" type="primary" @click="dosoldback">确认操作</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import posi1 from "@/components/common/sel_nowstore_posi1";
import posi2 from "@/components/common/sel_nowstore_posi2";
import axios from "axios";

export default {
  components: { posi1, posi2 },
  data() {
    return {
      now_posi1: "",
      vin: "",
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    change_posi1(d) {
      this.now_posi1 = d;
    },
    input_ok() {
      let err = [];
      this.vin == "" ? err.push("请先选择车辆") : "";
      this.$refs.posi1_ref.val == "" ? err.push("请选择分区") : "";
      this.$refs.posi2_ref.val == "" ? err.push("请选择分区") : "";
      return err;
    },
    dosoldback() {
      if (this.input_ok().length > 0) {
        this.$message(this.input_ok().join("，"));
        return;
      }
      this.$confirm("确认操作？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        axios({
          url: require("@/config").axios.dosoldback,
          method: "post",
          data: {
            token: this.token,
            info: {
              vin: this.vin,
              posi1: this.$refs.posi1_ref.val,
              posi2: this.$refs.posi2_ref.val
            }
          }
        }).then(re => {
          this.$message("退回完成");
          this.vin = "";
          this.$refs.posi1_ref.val = "";
          this.$refs.posi2_ref.val = "";
        });
      });
    }
  }
};
</script>

