<template>
  <div>
    <el-row v-show="key_is_out">
      <el-col span="24">
        <el-button icon="el-icon-edit" type="danger" @click="doback">钥匙归还</el-button>
      </el-col>
    </el-row>
    <div v-show="!key_is_out">
      <el-row gutter="10">
        <el-col span="12">
          <div class="inputflex">
            <label>变动日期</label>
            <dtpicker ref="movedt"></dtpicker>
          </div>
        </el-col>
        <el-col span="12">
          <celler cate="展厅" ref="celler_ref"></celler>
        </el-col>
      </el-row>
      <el-row gutter="10">
        <el-col span="18">
          <el-input v-model="remenber" placeholder="请输入内容"></el-input>
        </el-col>
        <el-col span="6">
          <el-button icon="el-icon-edit" type="primary" @click="domove">确认移动</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import dtpicker from "@/components/common/datetimepicker";
import celler from "@/components/common/sel_celler";
import axios from "axios";

export default {
  components: { dtpicker, celler },
  data() {
    return {
      key_is_out: true,
      vin: "",
      remenber: "",
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    input_ok() {
      let err = [];
      this.vin == "" ? err.push("请先选择车辆") : "";
      this.$refs.celler_ref.celler == "" ? err.push("请选择人员") : "";
      return err;
    },
    doback() {
      if (this.vin != "") {
        this.$confirm("确认归还钥匙？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.keyback,
            method: "post",
            data: { vin: this.vin, token: this.token }
          }).then(re => {
            this.$message("操作完成");
          });
        });
      }
    },
    domove() {
      if (this.input_ok().length == 0) {
        this.$confirm("确认操作？", {
          confirmButtonText: "确认",
          cancelButtonText: "取消"
        }).then(() => {
          axios({
            url: require("@/config").axios.keymove,
            method: "post",
            data: {
              vin: this.vin,
              celler_id: this.$refs.celler_ref.celler,
              remenber: this.remenber,
              movedt: this.$refs.movedt.value1,
              token: this.token
            }
          }).then(re => {
            this.$message("操作完成");
            this.remenber = "";
          });
        });
      } else {
        this.$message(this.input_ok().join("，"));
      }
    }
  }
};
</script>

