<template>
  <div>
    <el-select v-model="val" placeholder="配置" clearable @change="val_change">
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["stype", "data"],
  data() {
    return { vals: [], val: "" };
  },
  watch: {
    stype(nv,ov) {
      nv==""?this.vals=[]:this.getdata();
    },
    data(nv, ov) {
      this.val = nv;
    }
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.val);
    },
    getdata() {
      axios({
        url: require("@/config").axios.car_ltypelist,
        method: "post",
        data: { stype: this.stype, token: sessionStorage.getItem("token") }
      }).then(re => {
        this.vals = re.data.data.map(d => {
          return { val: d };
        });
      });
    }
  }
};
</script>