<template>
  <div>
    <el-row>
      <el-col span="12">
        <el-button type="primary" :loading="loading" @click="dologin">开始登录</el-button>
      </el-col>
      <el-col span="12">{{msg}}</el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["data"],
  watch: {
    data(nv, ov) {
      this.chaptcha = nv;
    }
  },
  data() {
    return {
      chaptcha: "",
      msg: "",
      loading: false,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    dologin() {
      this.msg = "正在请求....";
      this.loading = true;
      axios({
        url: require("@/config").axios.ppt_login,
        method: "post",
        data: { token: this.token, info: { chaptcha: this.chaptcha } }
      }).then(re => {
        this.loading = false;
        if (re.data.code == 200) {
          this.msg = "登录成功";
          this.$emit("login_ok");
        } else {
          this.msg = "登录失败";
        }
      });
    }
  }
};
</script>

