<template>
  <div>
    <el-row v-show="car_order_id!=''">{{car_order_info}}</el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>客户姓名</label>
          <el-input v-model="info.customer"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>身份证号</label>
          <el-input v-model="info.idcode"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>联系电话</label>
          <el-input v-model="info.tel"></el-input>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>地址</label>
          <el-input v-model="info.address"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>定车日期</label>
          <dtpicker @val_change="change_odt" :data="info.orderdt"></dtpicker>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>销售日期</label>
          <dtpicker @val_change="change_solddt" :data="info.solddt"></dtpicker>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="8">
        <div class="inputflex">
          <label>车款</label>
          <el-input v-model.number="info.price_car"></el-input>
        </div>
      </el-col>
      <el-col span="8">
        <div class="inputflex">
          <label>装饰款</label>
          <el-input v-model.number="info.price_deco"></el-input>
        </div>
      </el-col>
      <el-col span="8">
        <div class="inputflex">
          <label>按揭款</label>
          <el-input v-model.number="info.price_anjie"></el-input>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col span="12">
        <div class="inputflex">
          <label>支付方式</label>
          <sel_carsold_payway1 @val_change="change_payway1" :data="info.payway_1"></sel_carsold_payway1>
          <sel_carsold_payway2
            @val_change="change_payway2"
            :payway1="info.payway_1"
            :data="info.payway_2"
          ></sel_carsold_payway2>
        </div>
      </el-col>
      <el-col span="12">
        <div class="inputflex">
          <label>销售划分</label>
          <sel_carsold_cate @val_change="change_cate" :data="info.cate"></sel_carsold_cate>
          <sel_celler :cate="info.cate" :data="info.celler_id" @val_change="change_celler"></sel_celler>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-input v-model="info.tips" placeholder="备注信息"></el-input>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <el-col span="4" offset="20">
        <el-button type="primary" icon="el-icon-edit" @click="doupdate">销售更新</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <deco_au_car :vin="info.vin" :update_tri="update_tri"></deco_au_car>
  </div>
</template>

<script>
import sel_carsold_payway1 from "@/components/common/sel_carsold_payway1";
import sel_carsold_payway2 from "@/components/common/sel_carsold_payway2";
import sel_carsold_cate from "@/components/common/sel_carsold_cate";
import sel_celler from "@/components/common/sel_celler";
import deco_au_car from "@/components/common/deco_au_car";
import axios from "axios";
import dtpicker from "@/components/common/datetimepicker";
import utils from "@/components/utils";

export default {
  components: {
    sel_carsold_payway1,
    sel_carsold_payway2,
    sel_carsold_cate,
    sel_celler,
    deco_au_car,
    dtpicker
  },
  data() {
    return {
      token: sessionStorage.getItem("token"),
      car_order_id: "", //选中订单ID
      car_order_info: "当前选中订单", //选中订单描述
      info: {},
      update_tri: ""
    };
  },
  mounted() {
    this.clearInfo();
  },
  methods: {
    change_celler(d) {
      this.info.celler_id = d;
    },
    change_solddt(d) {
      this.info.solddt = d;
    },
    change_odt(d) {
      this.info.orderdt = d;
    },
    clearInfo() {
      this.info = {
        solddt: new Date(),
        orderdt: new Date(),
        vin: "",
        cate: "",
        celler_id: "",
        customer: "",
        tel: "",
        idcode: "",
        address: "",
        price_car: 0,
        price_deco: 0,
        price_anjie: 0,
        order_id: "",
        payway_1: "",
        payway_2: "",
        tips: "",
        cellerinfo: [
          {
            cate: "",
            name: ""
          }
        ]
      };
    },
    doupdate() {
      if (this.vin == "") {
        this.$message("请先选择车辆");
        return;
      }
      this.$confirm("确认操作？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(re => {
        axios({
          url: require("@/config").axios.carsoldupdate,
          method: "post",
          data: { token: this.token, info: this.info }
        }).then(re => {
          this.$message("操作完成");
        });
      });
    },
    change_payway1(d) {
      this.info.payway_1 = d;
      this.info.payway_2 = "";
    },
    change_payway2(d) {
      this.info.payway_2 = d;
    },
    change_cate(d) {
      this.info.cate = d;
      this.info.celler_id = "";
    },
    updateInfo(vin) {
      //刷新客户信息
      this.vin = vin;
      axios({
        url: require("@/config").axios.carsoldinfo,
        method: "post",
        data: { token: this.token, vin: this.vin }
      }).then(re => {
        if (re.data.code == 200) {
          //返回有销售记录
          this.info = re.data.data[0];
          this.update_tri = Date.now();
        }
      });
    }
  }
};
</script>

