<template>
  <div>
    <el-row>
      <el-col span="12">
        <el-button type="primary" :loading="loading" @click="doclose">关闭爬虫</el-button>
      </el-col>
      <el-col span="12">{{msg}}</el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import { no_au_then } from "@/components/utils";

export default {
  props: ["data"],
  watch: {
    data(nv, ov) {
      this.chaptcha = nv;
    }
  },
  data() {
    return {
      chaptcha: "",
      msg: "",
      loading: false,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    doclose() {
      this.loading = true;
      axios({
        url: require("@/config").axios.ppt_close,
        method: "post",
        data: { token: this.token }
      }).then(re => {
      this.loading = false;
        no_au_then(re, re => {
          this.msg("爬虫已关闭");
        });
      });
    }
  }
};
</script>

