<template>
  <div>
    <el-date-picker
      v-model="value1"
      type="datetime"
      placeholder="选择日期"
      :editable="false"
      :clearable="false"
      @change="val_change"
    ></el-date-picker>
  </div>
</template>

<script>
export default {
  props: ["data"],
  watch: {
    data(nv, ov) {
      this.value1 = nv;
    }
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      value1: Date.now()
    };
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.value1);
    }
  }
};
</script>