<template>
  <div>
    <el-table :data="orders" border style="width: 100%" max-height="300px">
      <el-table-column prop="cellerinfo[0].name" label="销售顾问"></el-table-column>
      <el-table-column prop="name" label="客户"></el-table-column>
      <el-table-column prop="odt" label="定车日期"></el-table-column>
      <el-table-column prop="linkinfo[0].vin" label="指定车辆"></el-table-column>
      <el-table-column label="选中">
        <template slot-scope="scope">
          <el-button @click="choose(scope.row)" icon="el-icon-circle-check" size="mini" circle></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      islink: false,
      orders: [],
      vin: "",
      clr: "",
      type_id: "",
      typecode: "", //这个参数只是因为打印装饰单需要用到，所以保存在这里
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    choose(d) {
      //触发选中
      this.$emit("choose", d);
    },
    updatelist() {
      //外部条件变更之后，自我刷新列表，目的就是及时更新数据显示
      this.getlist({ type_id: this.type_id, clr: this.clr, token: this.token });
    },
    getlist(obj) {
      axios({
        url: require("@/config").axios.findOrderByTypeidAndClr,
        method: "post",
        data: {
          type_id: this.type_id,
          clr: this.clr,
          token: this.token
        }
      }).then(re => {
        if (re.data.code == 200) {
          this.orders = re.data.data.map(d => {
            d.odt = require("@/components/utils").longdate(d.odt);
            return d;
          });
        }
      });
    },
    getOrders(car) {
      if (car.vin == "") {
        return;
      }

      this.vin = car.vin;
      this.clr = car.clr;
      this.type_id = car.type_id;

      this.getlist({ type_id: this.type_id, clr: this.clr, token: this.token });
    }
  }
};
</script>

