<template>
  <div>
    <el-row v-if="isAdd">
      <el-col span="4">
        <el-button type="success" icon="el-icon-edit" @click="doAdd">新建项目</el-button>
      </el-col>
    </el-row>
    <div v-if="!isAdd">
      <el-row gutter="10">
        <el-col span="16">
          <el-select
            v-model="select_deco"
            filterable
            remote
            reserve-keyword
            placeholder="请输入关键词"
            :remote-method="remoteMethod"
            :loading="queryloading"
            @change="select_item"
          >
            <el-option v-for="item in decolist" :key="item._id" :label="item.name" :value="item"></el-option>
          </el-select>
        </el-col>
        <el-col span="8">
          <el-input-number
            v-model="deco_num"
            @change="handleChange"
            :min="1"
            :max="10"
            label="描述文字"
          ></el-input-number>
        </el-col>
      </el-row>
      <el-table :data="decos" @selection-change="handleSelectionChange" ref="multipleTable">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="name" label="名称" width="180"></el-table-column>
        <el-table-column prop="count" label="数量" width="50"></el-table-column>
        <el-table-column fixed="right" label="操作" width="50">
          <template slot-scope="scope">
            <el-button @click="remove_deco(scope.row.name)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-divider></el-divider>
      <el-row>
        <el-col span="8" offset="16">
          <el-button-group>
            <el-button type="primary" @click="doupdate" icon="el-icon-edit">更新</el-button>
            <el-button type="info" icon="el-icon-refresh" @click="toggle">选中</el-button>
            <el-button type="success" icon="el-icon-printer" @click="doprint">打印</el-button>
          </el-button-group>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["vin", "update_tri"],
  watch: {
    update_tri() {
      this.updateData(this.vin);
    },
    vin(nv, ov) {
      this.updateData(nv);
    }
  },
  data() {
    return {
      deco_num: 1,
      select_deco: {},
      queryloading: false,
      isAdd: true,
      deco_id: 0,
      decos: [],
      deco_keys: "",
      decolist: [],
      deco_order_id: 0,
      vin: "",
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    doprint() {
      //打印数据
      const decos = this.sel_decos;
      if (decos.length == 0) {
        this.$message("尚未选择装饰项目");
        return;
      }
      let storage = {
        info: {
          name: "",
          clr: "",
          vin: this.vin,
          tel: "",
          typecode: "",
          celler: ""
        },
        decos: decos
      };

      localStorage.setItem("carsold_deco", JSON.stringify(storage));
      window.open("/print/carsold_deco.html", "", "", "");
    },
    toggle() {
      this.decos.forEach(d => {
        this.$refs.multipleTable.toggleRowSelection(d);
      });
    },
    handleSelectionChange(d) {
      this.sel_decos = d;
    },
    updateData(vin) {
      if (vin == "") {
        return;
      }
      //通过车架号刷新客户装饰项目
      this.vin = vin;
      axios({
        url: require("@/config").axios.decoinfoincar,
        method: "post",
        data: { vin: this.vin, token: this.token }
      }).then(re => {
        this.deco_order_id =
          re.data.deco_order_id == undefined ? 0 : re.data.deco_order_id;
        if (re.data.code == 500) {
          //切换显示样式
          this.isAdd = true;
          this.decos = [];
        } else {
          this.isAdd = false;
          if (re.data.data != "") {
            this.decos = re.data.data;
          } else {
            this.decos = [];
          }
        }
      });
    },
    doupdate() {
      this.$confirm("确认更新装饰项目？", {
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      }).then(() => {
        let data = { data: this.decos };
        data.deco_order_id = this.deco_order_id; //如果是选择已有的订单，那么ID是已存在的ID
        data.vin = this.vin;
        data.token = this.token;
        axios({
          url: require("@/config").axios.carupdatedeco,
          method: "post",
          data: data
        }).then(re => {
          this.$message("更新完成");
        });
      });
    },
    remove_deco(d) {
      this.decos.forEach((item, i) => {
        if (item.name == d) {
          this.decos.splice(i, 1);
        }
      });
    },
    select_item() {
      let dupData = false;
      this.decos.forEach((item, i) => {
        if (item.name == this.select_deco.name) {
          //找到重复的数据
          dupData = true;
        }
      });

      if (dupData) {
        this.$message("重复项目");
      } else {
        this.select_deco.count = this.deco_num;
        this.decos.push(this.select_deco);
      }
    },
    remoteMethod(keys) {
      if (keys.length != "") {
        // 带有查询条件
        this.queryloading = true;
        axios({
          url: require("@/config").axios.decolist,
          method: "post",
          data: {
            querystr: keys,
            onlyactive: true,
            token: this.token
          }
        }).then(re => {
          this.decolist = re.data.data;
          this.queryloading = false;
        });
      } else {
        this.decolist = [];
      }
    },
    doAdd() {
      this.deco_order_id = Date.now(); //生成项目ID
      this.isAdd = false; //切换显示状态
    }
  }
};
</script>

