<template>
  <div>
    <el-collapse v-for="(d,i) in data" v-model="activeNames" @change="handleChange" :key="i" accordion>
      <el-collapse-item :title="d.cate" :name="d.id">
        <ul>
          <li v-for="(item,item_i) in d.items" :key="item_i">
            <span class="rights_text">{{item.text}}</span>
            <button
              class="start"
              :rid="item.id"
              @click="addau"
              v-if="!item.state"
            >启用</button>
            <button
              class="stop"
              :rid="item.id"
              @click="delau"
              v-if="item.state"
            >禁用</button>
          </li>
        </ul>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["data"],
  data() {
    return {
      uid: 0,
      activeNames: ["1"],
      token:sessionStorage.getItem("token")
    };
  },
  methods: {
    uidOk() {
      if (this.uid != 0) {
        return true;
      } else {
        return false;
      }
    },
    doaxios(data) {
      data.token =this.token;
      axios({
        url: require("@/config").axios.au,
        method: "post",
        data: data
      }).then(re => {
        this.$emit("au_is_update");
      });
    },
    addau(ev) {
      if (this.uidOk()) {
        this.doaxios({
          t: "add",
          uid: this.uid,
          auid: ev.target.getAttribute("rid")
        });
      } else {
        this.$message("请先选中一个用户");
      }
    },
    delau(ev) {
      if (this.uidOk()) {
        this.doaxios({
          t: "del",
          uid: this.uid,
          auid: ev.target.getAttribute("rid")
        });
      } else {
        this.$message("请先选中一个用户");
      }
    },
    handleChange(val) {}
  }
};
</script>

<style scoped>
.rights_text {
  display: inline-block;
  width: 50%;
}

ul button {
  border-radius: 3px;
  background: none;
  border: none;
  padding: 3px 15px;
  color: #fff;
}

ul .start {
  background: #409eff;
}

ul .stop {
  background: #f56c6c;
}

ul li {
  padding: 5px;
}
</style>