<template>
  <div>
    <el-select v-model="val" placeholder="车型" clearable @change="val_change">
      <el-option v-for="item in vals" :key="item.val" :label="item.val" :value="item.val"></el-option>
    </el-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["data"],
  watch: {
    data(nv, ov) {
      this.val = nv;
    }
  },
  data() {
    return { vals: [], val: "" };
  },
  mounted() {
    axios({
      url: require("@/config").axios.carsoldmtypelist,
      method: "post",
      data: { token: sessionStorage.getItem("token") }
    }).then(re => {
      this.vals = re.data.data.map(d => {
        return { val: d };
      });
    });
  },
  methods: {
    val_change() {
      this.$emit("val_change", this.val);
    }
  }
};
</script>