<template>
  <div>
    <el-row gutter="10">
      <el-col span="12">
        <el-input v-model="q_name"></el-input>
      </el-col>
      <el-col span="12">
        <el-button type="primary" icon="el-icon-search" @click="doquery">查询</el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="detail" border ma-height="450px" style="width:100%">
      <el-table-column prop="createdt" label="创建日期" width="150px"></el-table-column>
      <el-table-column prop="name" label="标签名称" width="150px"></el-table-column>
      <el-table-column label="使用数量" width="100">
        <template slot-scope="scope">
          <el-button v-show="scope.row.vins.length">{{scope.row.vins.length}}</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="content" label="具体描述" width="500px"></el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button
            icon="el-icon-edit"
            @click="to_control(scope.row)"
            circle
            type="primary"
            size="mini"
            plain
          ></el-button>
          <el-button
            icon="el-icon-close"
            @click="deltag(scope.row.tag_id)"
            circle
            type="danger"
            size="mini"
            plain
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
import { longdate } from "@/components/utils";

export default {
  data() {
    return { detail: [], q_name: "", token: sessionStorage.getItem("token") };
  },
  methods: {
    getList() {
      axios({
        url: require("@/config").axios.tag_list,
        method: "post",
        data: { token: this.token, info: { name: this.q_name } }
      }).then(re => {
        this.detail = re.data.data.map(d => {
          d.createdt = longdate(d.createdt);
          return d;
        });
      });
    },
    doquery() {
      this.getList();
    },
    to_control(d) {
      this.$emit("edit",d.tag_id);
    },
    deltag(d) {
      this.$confirm("删除标签会同时影响到所有使用该标签的车辆，要继续吗？", {
        confirmButtonText: "确定",
        cancelButtonText: "删除"
      }).then(() => {
        axios({
          url: require("@/config").axios.del_tag,
          method: "post",
          data: { token: this.token, info: { tag_id: d } }
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("操作完成");
            this.getList();
          } else {
            this.$message(re.data.msg);
          }
        });
      });
    }
  }
};
</script>

<style scoped>
div {
  text-align: left;
}
</style>